const { Fragment } = React || {}
class PaginationView extends React.Component {
    _onChangeCurrent(page) {
        const { current, total, show } = this.props || {}
        const totalPage = Math.ceil(total / show)
        if (current != page && page >= 1 && page <= totalPage)
            this.props.onChangeCurrent(page)
    }
    _renderPage() {
        const { current, total, show } = this.props || {}
        const data = []
        if (total > 0) {
            const totalPage = Math.ceil(total / show)
            let start = Math.max(0, current - 4)
            let end = Math.min(start + 4, totalPage - 1)
            if (current - 1 > 3 && totalPage - current > 3) { //current page middle
                start = Math.max(0, current - 2)
                end = Math.max(0, start + 2)
            }
            if (current + 3 > totalPage - 1) { //current page ended
                end = Math.max(0, totalPage - 1)
                start = Math.max(0, end - 4)
            }
            if (current - 4 > 0) { //render page 1
                let pageFirst = 1
                data.push(
                    <li 
                    className={`${current == pageFirst ? 'active' : ''}`} 
                    key={`page_first`}
                    onClick={(e) => {this._onChangeCurrent(pageFirst)}} >
                    <a>{pageFirst}</a>
                </li>
                )
                data.push(
                    <li className="more" key={`pagination_ellipsis_start`}><a>...</a></li>
                )
            }
            for (let i = start; i <= end; i++) {
                data.push(
                    <li 
                    className={`${current == i+1 ? 'active' : ''}`} 
                    key={`page_${i}`}
                    onClick={(e) => {this._onChangeCurrent(i+1)}} >
                    <a>{i+1}</a>
                </li>
                )
            }
            if (totalPage - 1 > start + 4) { //render last page
                data.push(
                    <li className="more" key={`pagination_ellipsis_end`}><a>...</a></li>
                )
                data.push(
                    <li 
                    className={`${current == totalPage ? 'active' : ''}`} 
                    key={`page_${totalPage}`}
                    onClick={(e) => {this._onChangeCurrent(totalPage)}} >
                    <a>{totalPage}</a>
                </li>
                )
            }
            return data
        }
    }
    render() {
        const { current, total, show } = this.props || {}
        const totalPage = Math.ceil(total / show)
        return (
            <div className={`${totalPage <= 0 ? 'hide' : ''}`}>
                <ul className={`pagination flex-end ${totalPage <= 0 ? 'hide' : ''}`}>
                        <li className={`${current == 1 ? 'disable': ''}`} onClick={event=>{
                                this._onChangeCurrent(current-1)
                            }}>
                            <a>
                                <i className="ti-angle-left"></i>
                            </a>
                        </li>
                        {this._renderPage()}
                        <li className={` ${current == totalPage ? 'disable': ''}`} onClick={event=>{
                                this._onChangeCurrent(current+1)
                            }}>
                            <a>
                                <i className="ti-angle-right"></i>
                            </a>
                        </li>
                    </ul>
            </div>
        )
    }
}
module.exports = PaginationView


