import {
	SHOW_NOTIFY,
	HIDE_NOTIFY
} from 'modules/notify/types'
const show = (type='success', mess='')=>{
	return (dispatch)=>{
		dispatch({
			type: SHOW_NOTIFY,
			payload: {
				type: type,
				message: mess
			}
		})
		setTimeout(()=>{
			dispatch({
				type: HIDE_NOTIFY,
				payload: null
			})
		}, 3000)
	}
}
module.exports = show


