import DescView from 'modules/home.admin.transfer.agent.history.report.desc/DescView'
import {
    hide,
    onValidate,
    onSubmit
} from 'modules/home.admin.transfer.agent.history.report/actions'
class ReportView extends React.Component {
    _onClose() {
        this.props.hide()
    }
    _onSubmit() {
        this.props.onValidate()
            .then(() => {
                this.props.onSubmit()
            })
    }
    render() {
        return (
            <div className="modal sm" id="modalReportTransferAgentID">
				<div className="modal__container">
					<div className="modal__dimmer"></div>
					<div className="modal__content">
						<div className="modal__header">
							<div className="title">Báo cáo giao dịch</div>
							<div className="btn close" onClick={event=>{
								this._onClose()
							}}><i className="ti-close"></i></div>
						</div>
						<div className="modal__body">
							<div className="form">
								<DescView />
							</div>
						</div>
						<div className="modal__footer">
							<a className="btn btn--cancel" onClick={event=>{
								this._onClose()
							}}>Cancel</a>
							<div className="btnWrapper" id="btnReportTransferAgentID">
							<input type="button" value="Ok" className="btn btn--submit" onClick={event=>{
								this._onSubmit()
							}}/>
							</div>
						</div>
					</div>
				</div>
			</div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        hide,
        onValidate,
        onSubmit
    }, dispatch)
}
export default ReactRedux.connect(null, mapDispatchToProps)(ReportView)


