import {
    onSubmit
} from 'modules/home.agent.giftcode.list.submit/actions'
class SubmitView extends React.Component {
    _onSubmit() {
        this.props.onSubmit()
    }
    render() {
        return (
            <div className="flex flex-space-between">
                <a className="btn btn--primary" onClick={event=>{
                    this._onSubmit()
                }}>
                    Tìm kiếm
                </a>
			</div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onSubmit
    }, dispatch)
}
export default ReactRedux.connect(null, mapDispatchToProps)(SubmitView)


