import {
    ON_GETDATA_ADMIN_GIFTCODE_LIST_AGENT
} from 'modules/home.admin.giftcode.agent.list/types'
import {
    onChange
} from 'modules/home.admin.giftcode.agent.list.pagination/actions'
import {
    CHANGE_CURRENT_PAGE_ADMIN_GIFTCODE_LIST_AGENT
} from 'modules/home.admin.giftcode.agent.list.pagination/types'
const onSubmit = (isFirst) => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: daily } = getState().adminGiftCodeDaiLy || {}
        const { data: fromDate } = getState().filterAdminAgentGiftcodeListFrom || {}
        const { data: toDate } = getState().filterAdminAgentGiftcodeListTo || {}
        const { show, current } = getState().adminAgentGiftcodeListPagination || {}
        axios.post(`${Config.API_URL}admin/agent/giftcodeList`, {
                Data: {
                    fromDate: fromDate ? moment(fromDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 00:00:00 Z') : null,
                    toDate: toDate ? moment(toDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 23:59:59 Z') : null,
                    daily: daily,
                    limit: show,
                    offset: (current - 1) * show
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info, totals } = data || {}
                if (success) {
                    dispatch({
                        type: ON_GETDATA_ADMIN_GIFTCODE_LIST_AGENT,
                        payload: {
                            data: info
                        }
                    })
                    dispatch({
                        type: CHANGE_CURRENT_PAGE_ADMIN_GIFTCODE_LIST_AGENT,
                        payload: {
                            show: show,
                            current: current,
                            totals: totals
                        }
                    })
                }
                resolve()
            }, (err) => {
                reject(err)
            })
    })
}
export default onSubmit


