import {
    onSubmit as getData
} from 'modules/home.admin.agents.transfer.history.submit/actions'
import PaginationView from 'modules/home.admin.agents.transfer.history.pagination/PaginationView'
import DaiLyView from 'modules/home.admin.agents.transfer.history.daily/DaiLyView'
import FromView from 'modules/home.admin.agents.transfer.history.from/FromView'
import ToView from 'modules/home.admin.agents.transfer.history.to/ToView'
import SubmitView from 'modules/home.admin.agents.transfer.history.submit/SubmitView'
class HistoryView extends React.Component {
    componentDidMount() {
        this.props.getData(true)
    }
    _onReport(item) {
        // if (item.report) {
        // this.props.showNotify('error', 'Giao dịch đã được báo cáo Admin')
        // } else {
        this.props.showModal(item._id)
        // }
    }
    _onFreeze(item) {
        if (item.freeze) {
            this.props.showNotify('error', 'Giao dịch đã được đóng băng')
        } else {
            this.props.showModalFreeze(item._id)
        }
    }
    render() {
        const { data } = this.props.agentsTransferHistory || {}
        const { show, current } = this.props.adminAgentsTranferHistoryPagination || {}
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Lịch sử giao dịch</h1>
					</div>
				</div>
				<div className="page__body">
					<div className="box margin-top-10">
                        <div className="tableFilter">
                            <div className="tableFilter__row">
                                <div className="tableFilter__column">
                                    <DaiLyView />
                                </div>
                                <div className="tableFilter__column">
                                    <FromView />
                                </div>
                                <div className="tableFilter__column">
                                    <ToView />
                                </div>
                                <div className="tableFilter__column">
                                    <SubmitView />
                                </div>
                            </div>
                        </div>
						<div className="table-container">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                                    <table className="table sticky margin-top-10">
                                        <thead>
                                            <tr>
                                                <th className="cell-no">STT</th>
                                                <th>Người chuyển</th>
                                                <th>Người nhận</th>
                                                <th>ZON chuyển</th>
                                                <th>ZON nhận</th>
                                                <th>Nội dung</th>
                                                <th>Mã giao dịch</th>
                                                <th>Ngày giao dịch</th>
                                                <th>Tình trạng</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                        	{data.map((item, index)=>{
                                        		return (
                                        			<tr key={`history_${item._id}`}>
        		                                        <td className="cell-no text-center">{ (current - 1) * show+index+1}</td>
        		                                        <td className="text-nowrap text-center">
        		                                        	{item.from}
        		                                        </td>
        		                                        <td className="text-center">{item.to}</td>
        		                                        <td className={`cell-datetime text-center ${item.red >=0 ? 'text-positive' : 'text-negative'}`}>{Helper._formatMoney(item.red)}</td>
        		                                        <td className={`cell-datetime text-center ${item.red_c >=0 ? 'text-positive' : 'text-negative'}`}>{Helper._formatMoney(item.red_c)}</td>
        		                                        <td className="text-center">{item.message}</td>
        		                                        <td className="text-center">{item._id}</td>
        		                                        <td className="cell-datetime text-center">{moment(item.time).format(`DD/MM/YYYY HH:mm:ss`)}</td>
                                                        <td className="text-center">
                                                            <div className="tooltip">
                                                                <div className="tooltip__header">
                                                                    <span className={`status ${item.report ? 'status--renote' : item.freeze ? 'status--freezed' : 'status--success'}`}>
                                                                      {item.report ? 'Báo cáo' : item.freeze ? 'Đóng băng': 'Thành công'}
                                                                    </span>
                                                                </div>
                                                                <div className={`tooltip__body right ${(!item.report && !item.freeze) ? 'hide' : ''}`}>
                                                                    <div className="tooltip__content">
                                                                        {item.report ? item.report : item.freeze ? item.freeze : ''}
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </td>
        		                                    </tr>
                                        			)
                                        	})}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            <div className="margin-top-10">
                                <PaginationView />
                        	</div>
                        </div>
					</div>
				</div>
			</div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData,
    }, dispatch)
}
const mapStateToProps = ({
    agentsTransferHistory,
    adminAgentsTranferHistoryPagination
}) => {
    return {
        agentsTransferHistory,
        adminAgentsTranferHistoryPagination
    }
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(HistoryView)



