import {
    ONVALIDTE_ADD_DAILY
} from 'modules/home.admin.agent.list.add/types'
import {
    onError as onErrorName,
    onFocus as onFocusName
} from 'modules/home.admin.agent.list.add.name/actions'
import {
    onError as onErrorNickname,
    onFocus as onFocusNickname
} from 'modules/home.admin.agent.list.add.nickname/actions'
import {
    onError as onErrorPhone,
    onFocus as onFocusPhone
} from 'modules/home.admin.agent.list.add.phone/actions'
import {
    onError as onErrorLocation,
    onFocus as onFocusLocation
} from 'modules/home.admin.agent.list.add.location/actions'
import {
    onError as onErrorFB,
    onFocus as onFocusFB
} from 'modules/home.admin.agent.list.add.fb/actions'
const onValidate = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: nameAddDaiLy } = getState().nameAddDaiLy || {}
        const { data: nicknameAddDaiLy } = getState().nicknameAddDaiLy || {}
        const { data: phoneAddDaiLy } = getState().phoneAddDaiLy || {}
        const { data: locationAddDaiLy } = getState().locationAddDaiLy || {}
        const { data: fbAddDaiLy } = getState().fbAddDaiLy || {}
        if (!nameAddDaiLy) {
            dispatch(onErrorName('Bắt buột'))
            dispatch(onFocusName())
            reject()
        }
        if (!nicknameAddDaiLy) {
            dispatch(onErrorNickname('Bắt buột'))
            dispatch(onFocusNickname())
            reject()
        }
        if (!phoneAddDaiLy) {
            dispatch(onErrorPhone('Bắt buột'))
            dispatch(onFocusPhone())
            reject()
        }
        if (!locationAddDaiLy) {
            dispatch(onErrorLocation('Bắt buột'))
            dispatch(onFocusLocation())
            reject()
        }
        if (!fbAddDaiLy) {
            dispatch(onErrorFB('Bắt buột'))
            dispatch(onFocusFB())
            reject()
        }
        if (nameAddDaiLy && nicknameAddDaiLy && phoneAddDaiLy && locationAddDaiLy && fbAddDaiLy) {
            resolve()
        }
    })
}
module.exports = onValidate


