import {
    ONSUBMIT_LOGIN
} from 'modules/auth/types'
import {
    onFocus as focusUsername,
    onError as onErrorUsername
} from 'modules/auth.username/actions'
import {
    onFocus as focusPassword,
    onError as onErrorPassword
} from 'modules/auth.password/actions'
import {
    onLoading,
    onLoaded
} from 'modules/auth.btn/actions'
const { push } = ReactRouterRedux.routerActions || {}
const onLogin = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: username } = getState().authUsername || {}
        const { data: password } = getState().authPassword || {}
        dispatch(onLoading())
        axios.post(`${Config.API_URL}login`, {
                Data: {
                    username: username,
                    password: password
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { token, success, data: userInfo } = data || {}
                dispatch(onLoaded())
                if (success) {
                    Helper._setCookie("token", token)
                    Helper._setCookie("userInfo", JSON.stringify(userInfo))
                    dispatch(push(Routes.home.view))
                    resolve(response)
                } else {
                    if (userInfo.username) {
                    	dispatch(onErrorUsername(userInfo.username))
                        dispatch(focusUsername())
                    } else if (userInfo.password) {
                    	dispatch(onErrorPassword(userInfo.password))
                        dispatch(focusPassword())
                    }
                }
            }, (err) => {
                dispatch(onLoaded())
                reject(err)
            })
    })
}
module.exports = onLogin


