import {
    ONBLUR_CONFIRM_CHANGE_PASSWORD
} from 'modules/home.changePassword.confirmpass/types'
const onBlur = () => {
    return (dispatch) => {
        $(`#confirmPassID`).blur()
        dispatch({
            type: ONBLUR_CONFIRM_CHANGE_PASSWORD,
            payload: null
        })
    }
}
module.exports = onBlur


