import {
    ONCHANGE_TYPE_HISTORY_USER_FILTER
} from 'modules/home.user.history.type/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_TYPE_HISTORY_USER_FILTER:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


