import {
    ONCHANGE_NICKNAME_XOSO_HISTORY_ADMIN
} from 'modules/home.games.history.xoso.nickname/types'
const onChange = (val) => {
    return (dispatch) => {
        dispatch({
            type: ONCHANGE_NICKNAME_XOSO_HISTORY_ADMIN,
            payload: {
                data: val ? val.toLowerCase() : val
            }
        })
    }
}
module.exports = onChange


