import {
    ONCHANGE_DAILY_TRANSFER_HISTORY_AGENTS
} from 'modules/home.admin.agents.transfer.history.daily/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateMessAgentsTransferHistoryDaiLy`).removeClass('has-error')
        dispatch({
            type: ONCHANGE_DAILY_TRANSFER_HISTORY_AGENTS,
            payload: {
                data: val
            }
        })
    }
}
module.exports = onChange


