import {
    show,
    hide
} from 'modules/notify/actions'
class NotifyView extends React.Component {
    _onClose() {
        this.props.hide()
    }
    render() {
        const { status, type, message } = this.props.notify || {}
        return (
            <div className={`alert ${status ? 'active' : ''} ${type}`}>
				<div className="alert__content">
					<a className="btn close" onClick={event=>{
						this._onClose()
					}}>
						<i className="ti-close"></i>
					</a>
					<p>{message}</p>
					<div className="text-right">
						<a className="btn" onClick={event=>{
							this._onClose()
						}}>OK</a>
					</div>
				</div>
			</div>
        )
    }
}
const mapStateToProps = ({
    notify
}) => {
    return {
        notify
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        show,
        hide
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(NotifyView)


