import {
    ONERROR_DESC_FREEZE_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent.history.freeze.desc/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateDescFreezeTransferDaiLy`).addClass('has-error')
        $(`#validateMessDescFreezeTransferDaiLy`).removeClass('hide')
        $(`#validateMessDescFreezeTransferDaiLy`).text(mess)
        dispatch({
            type: ONERROR_DESC_FREEZE_TRANSFER_AGENT,
            payload: null
        })
    }
}
module.exports = onError


