import FromView from 'modules/home.user.transfer.statement.from/FromView'
import ToView from 'modules/home.user.transfer.statement.to/ToView'
import SubmitView from 'modules/home.user.transfer.statement.submit/SubmitView'
import {
    onSubmit as getData
} from 'modules/home.user.transfer.statement.submit/actions'
class StatementView extends React.Component {
    componentDidMount() {
        this.props.getData()
    }
    render() {
        const { data } = this.props.statementTransferUser || {}
        return (
            <div className="page__content">
                <div className="page__header">
                    <div className="title">
                        <h1 className="pageTitle">Thống kê Chuyển khoản User</h1>
                    </div>
                </div>
                <div className="page__body">
                    <div className="box margin-top-10">
                        <div className="tableFilter">
                            <div className="tableFilter__row">
                                <div className="tableFilter__column">
                                </div>
                                <div className="tableFilter__column">
                                </div>
                                <div className="tableFilter__column">
                                    <FromView />
                                </div>
                                <div className="tableFilter__column">
                                    <ToView />
                                </div>
                                <div className="tableFilter__row">
                                <div className="tableFilter__column" />
                                <div className="tableFilter__column" />
                                <div className="tableFilter__column">
                                    <SubmitView />
                                </div>
                            </div>
                            </div>
                        </div>
                        <div className="table-container margin-top-10">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                                    <table className="table sticky">
                                        <thead>
                                            <tr>
                                                <th>Tổng Chuyển</th>
                                                <th>Tổng Nhận</th>
                                                <th>Tổng Phế</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td className="text-nowrap text-center">{Helper._formatMoney(data ? data.send : 0)}</td>
                                                <td className="text-nowrap text-center">{Helper._formatMoney(data ? data.receive : 0)}</td>
                                                <td className="text-nowrap text-center">{Helper._formatMoney(data ? data.fee : 0)}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = ({
    statementTransferUser
}) => {
    return {
        statementTransferUser
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(StatementView)


