import {
    ONERROR_CODE_GIFTCODE_MARKETING_CHECK
} from 'modules/home.admin.giftcode.marketing.check.code/types'
const onError = () => {
    return (dispatch) => {
        $(`#validateMessCodeGiftCodeMarketingCheck`).addClass('has-error')
        dispatch({
            type: ONERROR_CODE_GIFTCODE_MARKETING_CHECK,
            payload: null
        })
    }
}
module.exports = onError


