import {
    ONCHANGE_CONFIRM_CHANGE_PASSWORD
} from 'modules/home.changePassword.confirmpass/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_CONFIRM_CHANGE_PASSWORD:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


