import {
    onChange
} from 'modules/home.admin.giftcode.marketing.list.trangthai/actions'
class TrangThaiView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    render() {
        const { data } = this.props.trangthaiGiftCodeMarketingListFilter || {}
        return (
            <div className="form-group-box" id="validateTrangthaiGiftCodeMarketingListFilter">
            		<label className="control-label">Trang thái</label>
					<div className="select">
						<select 
							className="form-control"  
							value={data}
							selected={data}
							onChange={event=>{
								this._onChange(event)
							}}>
							<option value="">Tất cả</option>
							<option value="1">Đã sử dụng</option> 
							<option value="2">Chưa sử dụng</option> 
						</select>
					</div>
				</div>
        )
    }
}
const mapStateToProps = ({ trangthaiGiftCodeMarketingListFilter }) => {
    return {
        trangthaiGiftCodeMarketingListFilter
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(TrangThaiView)


