import {
    ONCHECK_NICKNAME_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent.nickname/types'
import {
    onError as onErrorNickname
} from 'modules/home.admin.transfer.agent.nickname/actions'
const onCheck = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: nickname } = getState().nicknameTransferDaiLy || {}
        axios.get(`${Config.API_URL}agent/checkNicknameAgent`, {
                params: {
                    nickname: nickname.toLowerCase()
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info } = data || {}
                if (!success) {
                    dispatch(onErrorNickname(info.nickname))
                }
                else {
                	$(`#validateNicknameTransferDaiLy`).addClass('has-success')
                	$(`#validateNicknameTransferDaiLy`).removeClass('has-error')
        			$(`#validateMessNicknameTransferDaiLy`).addClass('hide')
                }
            }, (err) => {
                reject(err)
            })
    })
}
module.exports = onCheck


