import {
    HIDE_MODAL_CHANGEPASS
} from 'modules/home.changePassword/types'
const hide = () => {
    return (dispatch) => {
        $(`#modalChangePassID`).removeClass('active')
        dispatch({
            type: HIDE_MODAL_CHANGEPASS,
            payload: null
        })
    }
}
module.exports = hide


