import {
    onChange
}
from 'modules/home.admin.agent.list.add.phone/actions'
import {
	onFocus as focusLocation
} from 'modules/home.admin.agent.list.add.location/actions'
class PhoneView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    _onKeyDown(event) {
        const { keyCode } = event || {}
        switch (keyCode) {
            case 13:
                this.props.focusLocation()
                break
            default:
                break
        }
    }
    render() {
        const { data } = this.props.phoneAddDaiLy || {}
        return (
            <div className="form-group-box" id="validatePhoneAddDaiLyID">
				<label className="control-label">Số điện thoại</label>
				<div className="input">
					<input 
						onChange={event=>{
							this._onChange(event)
						}}
						onKeyDown={event=>{
							this._onKeyDown(event)
						}}
						value={data}
						type="text" 
						id="phoneAddDaiLyID" 
						className="form-control" 
						placeholder="Số điện thoại" />
				</div>
                <p className="help-block hide" id="validatePhoneMessAddDaiLyID"></p>
			</div>
        )
    }
}
const mapStateToProps = ({
    phoneAddDaiLy
}) => {
    return {
        phoneAddDaiLy
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        focusLocation
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(PhoneView)


