import {
    ONCHANGE_TRANGTHAI_GIFTCODE_DAILY_FILTER
} from 'modules/home.agent.giftcode.list.trangthai/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_TRANGTHAI_GIFTCODE_DAILY_FILTER:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


