import NicknameView from 'modules/home.admin.transfer.agent.nickname/NicknameView'
import ValueView from 'modules/home.admin.transfer.agent.value/ValueView'
import OTPView from 'modules/home.admin.transfer.agent.otp/OTPView'
import DescView from 'modules/home.admin.transfer.agent.desc/DescView'
import {
    onSubmit,
    onSubmitOTP,
    onValidate
} from 'modules/home.admin.transfer.agent/actions'
class TransferView extends React.Component {
    _onSubmit() {
        this.props.onValidate()
            .then(() => {
                this.props.onSubmit()
            }, (err) => {

            })
    }
    _onSubmitOTP() {
     console.log(this.props);
        this.props.onSubmitOTP();
    }
    render() {
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Chuyển ZON</h1>
					</div>
				</div>
				<div className="page__body">
					<form className="form form--transfer" id="formTransferDaiLy">
						<div className="form__body">
						   <NicknameView />
						   <ValueView />
						   <OTPView />
						   <DescView />
						</div>
						<div className="form__footer">
							<div className="btnWrapper" style={{padding : 10}} id="btnTransferDaiLyID">
								<a
									className="btn btn--block btn--primary"
									onClick={event=>{
										this._onSubmit()
									}} >
									Chuyển khoản
									</a>
							</div>
       <div className="btnWrapper" style={{padding : 10}} id="btnGetOTP">
        <a
         className="btn btn--block btn--primary"
         onClick={event=>{
          this._onSubmitOTP()
         }} >
         Lấy OTP
         </a>
       </div>
						</div>
					</form>
				</div>
			</div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onSubmit,
        onSubmitOTP,
        onValidate
    }, dispatch)
}
export default ReactRedux.connect(null, mapDispatchToProps)(TransferView)



