import {
    onChange
} from 'modules/home.user.history.type/actions'
import {
    getData
} from 'modules/home.user.history/actions'
class TypeView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
        .then(()=>{
        	this.props.getData()
        })
    }
    render() {
        const { data } = this.props.typeHistoryUserFilter || {}
        return (
            <div className="form-group-box width-300 margin-right-10" id="validateTypeHistoryUserFilter">
            		<label className="control-label">Giao Dịch</label>
					<div className="select">
						<select 
							className="form-control"  
							value={data}
							selected={data}
							onChange={event=>{
								this._onChange(event)
							}}>
							<option value="">Tất Cả</option>
							<option value="1">Chuyển Khoản</option> 
							<option value="2">Thẻ Cào</option> 
							<option value="3">Giftcode</option> 
							<option value="4">Ngân Hàng</option> 
							<option value="5">Tài Xỉu</option> 
							<option value="6">Bầu Cua</option>
							<option value="7">Xóc Đĩa</option>
							<option value="8">MiniPoker</option>
							<option value="9">Mini 3 Cây</option>
							<option value="10">Bigbabol</option>
							<option value="11">AngryBirds</option>
							<option value="12">Vương Quốc Red</option>
							<option value="13">Candy</option>
							<option value="14">Long Lân</option>
							<option value="15">Xổ Số</option>
						</select>
					</div>
				</div>
        )
    }
}
const mapStateToProps = ({ typeHistoryUserFilter }) => {
    return {
        typeHistoryUserFilter
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        getData
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(TypeView)


