import {
    ONVALIDATE_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent/types'
import {
    onError as onErrorNickname
} from 'modules/home.admin.transfer.agent.nickname/actions'
import {
    onError as onErrorValue
} from 'modules/home.admin.transfer.agent.value/actions'
import {
    onError as onErrorOtp
} from 'modules/home.admin.transfer.agent.otp/actions'
import {
    onError as onErrorDesc
} from 'modules/home.admin.transfer.agent.desc/actions'
const onValidate = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: nickname } = getState().nicknameTransferDaiLy || {}
        const { data: desc } = getState().descTransferDaiLy || {}
        const { data: valueRed } = getState().valueTransferDaiLy || {}
        const { data: otp } = getState().otpTransferDaiLy || {}
        dispatch({
            type: ONVALIDATE_TRANSFER_AGENT,
            payload: null
        })
        if (!desc || desc == '') {
            dispatch(onErrorDesc('Lí do bắt buộc'))
        }
        if (!valueRed || valueRed == '') {
            dispatch(onErrorValue('Giá trị bắt buộc'))
        }
        if (!otp || otp == '') {
            dispatch(onErrorOtp('Otp bắt buộc'))
        }
        if (!nickname || nickname == '') {
            dispatch(onErrorNickname('Nickname bắt buộc'))
        }
        if (nickname &&
            desc &&
            valueRed &&
            otp) {
            resolve()
        } else {
            reject()
        }

    })
}
module.exports = onValidate


