import {
    HIDE_CONFIRM_REMOVE_DAILY2
} from 'modules/home.admin.agent2.list.remove.confirm/types'
const hide = () => {
    return (dispatch) => {
        $(`#confirmRemoveAgent2ID`).removeClass('active')
        dispatch({
            type: HIDE_CONFIRM_REMOVE_DAILY2,
            payload: null
        })
    }
}
module.exports = hide


