import {
    ONBLUR_LOCATION_ADD_DAILY
} from 'modules/home.admin.agent.list.add.location/types'
const onBlur = () => {
    return (dispatch) => {
        $(`#locationAddDaiLyID`).focus()
        dispatch({
            type: ONBLUR_LOCATION_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onBlur


