import {
    ONCHANGE_PHONE_ADD_DAILY
} from 'modules/home.admin.agent.list.add.phone/types'
const onChange = (val) => {
    return (dispatch, getState) => {
        const { data } = getState().phoneAddDaiLy || {}
        $(`#validatePhoneAddDaiLyID`).removeClass('has-error')
        $(`#validatePhoneMessAddDaiLyID`).addClass('hide')
        if (data != val) {
            dispatch({
                type: ONCHANGE_PHONE_ADD_DAILY,
                payload: {
                    data: val
                }
            })
        }
    }
}
module.exports = onChange


