import {
    ONCHANGE_SOLUONG_GIFTCODE_MARKETING
} from 'modules/home.admin.giftcode.marketing.add.soluong/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateMessSoLuongGiftCodeMarketingAdd`).removeClass('has-error')
        dispatch ({
            type: ONCHANGE_SOLUONG_GIFTCODE_MARKETING,
            payload: {
            	data: val
            }
        })
    }
}
module.exports = onChange



