import {
    ONFOCUS_PASSWORD_LOGIN
} from 'modules/auth.password/types'
const onFocus = () => {
    return (dispatch) => {
        $(`#passID`).focus()
        dispatch({
            type: ONFOCUS_PASSWORD_LOGIN,
            payload: null
        })
    }
}
module.exports = onFocus


