import NicknameView from 'modules/home.user.deposit.check.nickname/NicknameView'
import SubmitView from 'modules/home.user.deposit.check.submit/SubmitView'
class CheckView extends React.Component {
    render() {
        const { data, total } = this.props.userDepositCheck || {}
        return (
            <div className="page__content">
                <div className="page__header">
                    <div className="title">
                        <h1 className="pageTitle">Check Lịch Sử Nạp Tiền User</h1>
                    </div>
                </div>
                <div className="page__body">
                    <div className="box margin-top-10">
                        <div className="tableFilter">
                            <div className="tableFilter__row">
                                <div className="tableFilter__column">
                                    <NicknameView />
                                </div>
                                <div className="tableFilter__column">
                                    <SubmitView />
                                </div>
                                <div className="tableFilter__column">
                                </div>
                                <div className="tableFilter__column">
                                    
                                </div>
                                <div className="tableFilter__row">
                                <div className="tableFilter__column" />
                                <div className="tableFilter__column" />
                                <div className="tableFilter__column">
                                </div>
                            </div>
                            </div>
                        </div>
                        <div className="table-container margin-top-10">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                                    <table className="table sticky">
                                        <thead>
                                            <tr>
                                                <th className="cell-no">STT</th>
                                                <th>Loại</th>
                                                <th>Nguồn</th>
                                                <th>Giá Trị</th>
                                                <th>Ngày Nạp</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                             <tr className={data.length>0 ? 'hide' : ''}>
                                                <td className="text-nowrap text-center" colSpan="5">Không tìm thấy</td>
                                            </tr>
                                            {data.map((item, index)=>{
                                                return (
                                                    <tr key={`check_deposit_${index}`}>
                                                        <td className="cell-no text-center">{index + 1}</td>
                                                        <td className="text-nowrap text-center">{item.type}</td>
                                                        <td className="text-nowrap">{item.source}</td>
                                                        <td className="text-nowrap text-center">{Helper._formatMoney(item.value)}</td>
                                                        <td className="text-nowrap text-center">{moment(item.time).format("DD/MM/YYYY HH:mm:ss")}</td>
                                                    </tr>
                                                    )
                                            })}
                                        </tbody>
                                        <tfoot className={data.length == 0 ? 'hide' : ''}>
                                            <tr>
                                                <th colSpan="3" className="cell-number">Tổng Cộng</th>
                                                <th className="text-nowrap text-center">{Helper._formatMoney(total)}</th>
                                                <th className="text-nowrap text-center"></th>
                                            </tr>
                                        </tfoot>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = ({
    userDepositCheck

}) => {
    return {
        userDepositCheck
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, null)(CheckView)


