import {
    GET_SUMMARY_DATA
} from 'modules/home.init/types'
const INITIAL_STATE = {
    data: {}
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case GET_SUMMARY_DATA:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


