import {
    ONCHANGE_TODATE_GAME_MONEYLINE
} from 'modules/home.admin.moneyline.to/types'
const onChange = (val) => {
    return (dispatch) => {
        dispatch({
            type: ONCHANGE_TODATE_GAME_MONEYLINE,
            payload: {
                data: val
            }
        })
    }
}

module.exports = onChange


