import {
    ONOPEN_MENU
} from 'modules/home.menu/types'
const openMenu = (menuID) => {
    return (dispatch, getState) => {
        const { opened } = getState().menu || {}
        dispatch({
            type: ONOPEN_MENU,
            payload: {
                opened: opened == menuID ? -1 : menuID
            }
        })
    }
}
module.exports = openMenu


