import {
    ONBLUR_FB_ADD_DAILY
} from 'modules/home.admin.agent.list.add.fb/types'
const onBlur = () => {
    return (dispatch) => {
        $(`#fbAddDaiLyID`).focus()
        dispatch({
            type: ONBLUR_FB_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onBlur


