import {
    onChange
}
from 'modules/home.admin.agent.list.add.name/actions'
import {
	onFocus as focusNickname
} from 'modules/home.admin.agent.list.add.nickname/actions'
class NameView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    _onKeyDown(event) {
        const { keyCode } = event || {}
        switch (keyCode) {
            case 13:
                this.props.focusNickname()
                break
            default:
                break
        }
    }
    render() {
        const { data } = this.props.nameAddDaiLy || {}
        return (
            <div className="form-group-box" id="validateNameAddDaiLyID">
				<label className="control-label">Tên Đại lý</label>
				<div className="input">
					<input 
						onChange={event=>{
							this._onChange(event)
						}}
						onKeyDown={event=>{
							this._onKeyDown(event)
						}}
						value={data}
						type="text" 
						id="nameAddDaiLyID" 
						className="form-control" 
						placeholder="Tên Đại lý" />
				</div>
                <p className="help-block hide" id="validateNameMessAddDaiLyID"></p>
			</div>
        )
    }
}
const mapStateToProps = ({
    nameAddDaiLy
}) => {
    return {
        nameAddDaiLy
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        focusNickname
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(NameView)


