import {
    SHOW_CONFIRM_REMOVE_DAILY
} from 'modules/home.admin.agent.list.remove.confirm/types'
const show = () => {
    return (dispatch) => {
        $(`#confirmRemoveAgentID`).addClass('active')
        dispatch({
            type: SHOW_CONFIRM_REMOVE_DAILY,
            payload: null
        })
    }
}
module.exports = show


