import App from 'app/App'
import Backend from 'app/Backend'
import AuthHoc from 'common/hoc/Auth'
import LoginView from 'modules/auth/LoginView'
import AgentListView from 'modules/home.admin.agent.list/ListView'
import HomeMainView from 'modules/home/HomeMainView'
import AddView from 'modules/home.admin.giftcode.agent.add/AddView'
import TransferView from 'modules/home.admin.transfer.agent/TransferView'
import TransferHistoryView from 'modules/home.admin.transfer.agent.history/HistoryView'
import Agent2ListView from 'modules/home.admin.agent2.list/ListView'
import Agent2ProfitView from 'modules/home.admin.agent.agent2.profit/ProfitView'
import AgentMyProfitView from 'modules/home.admin.agent.profit/ProfitView'
import AgentGiftcodeListView from 'modules/home.agent.giftcode.list/ListView'
import AgentGiftcodeStatementView from 'modules/home.agent.giftcode.statement/StatementView'
import AdminAgentGiftcodeListView from 'modules/home.admin.giftcode.agent.list/ListView'
import AddGiftcodeMarketingView from 'modules/home.admin.giftcode.marketing.add/AddView'
import ListGiftcodeMarketingView from 'modules/home.admin.giftcode.marketing.list/ListView'
import CheckView from 'modules/home.admin.giftcode.marketing.check/CheckView'
import AdminProfitAgentView from 'modules/home.admin.agents.profit/ProfitView'
import AdminTransferHistoryView from 'modules/home.admin.agents.transfer.history/HistoryView'
import AdminTransferView from 'modules/home.admin.transfer/TransferView'
import AdminTransferUserHistoryView from 'modules/home.admin.transfer.history/HistoryView'
import MoneyLineView from 'modules/home.admin.moneyline/MoneyLineView'
import SummaryView from 'modules/home.init/SummaryView'
import HistoryTaiXiuView from 'modules/home.games.history.taixiu/HistoryView'
import HistoryBauCuaView from 'modules/home.games.history.baucua/HistoryView'
import HistoryXocDiaView from 'modules/home.games.history.xocdia/HistoryView'
import HistoryMinipokerView from 'modules/home.games.history.minipoker/HistoryView'
import HistoryMini3cayView from 'modules/home.games.history.mini3cay/HistoryView'
import HistoryBigbabolView from 'modules/home.games.history.bigbabol/HistoryView'
import HistoryAngrybirdsView from 'modules/home.games.history.angrybirds/HistoryView'
import HistoryVuongquocredView from 'modules/home.games.history.vuongquocred/HistoryView'
import HistoryTamhungView from 'modules/home.games.history.tamhung/HistoryView'
import HistoryCandyView from 'modules/home.games.history.candy/HistoryView'
import HistoryLonglanView from 'modules/home.games.history.longlan/HistoryView'
import HistoryZeusView from 'modules/home.games.history.zeus/HistoryView'
import HistoryXosoView from 'modules/home.games.history.xoso/HistoryView'
import UserListView from 'modules/home.userlist/ListView'
import UserStatementTransferView from 'modules/home.user.transfer.statement/StatementView'
import UserStatementCardView from 'modules/home.user.card.statement/StatementView'
import UserDepositCheckView from 'modules/home.user.deposit.check/CheckView'
const { Route, IndexRoute } = ReactRouter
const routes = (
    <Route component={App}>
		<Route component={AuthHoc(Backend)}>
			<Route component={HomeMainView} path={Routes.home.view}>
				<Route component={AgentListView} path={Routes.agentList.view} />
				<Route component={AddView} path={Routes.agentGiftcodeAdd.view} />
				<Route component={TransferView} path={Routes.agentTransferCreate.view} />
				<Route component={TransferHistoryView} path={Routes.agentTransferHistory.view} />
				<Route component={Agent2ListView} path={Routes.agentAgent2List.view} />
				<Route component={Agent2ProfitView} path={Routes.agent2Profit.view} />
				<Route component={AgentMyProfitView} path={Routes.agentMyProfit.view} />
				<Route component={AgentGiftcodeListView} path={Routes.agentGiftcodeList.view} />
				<Route component={AgentGiftcodeStatementView} path={Routes.agentGiftcodeStatement.view} />
				<Route  component={AdminAgentGiftcodeListView} path={Routes.adminAgentGiftcodeList.view} />
				<Route component={AddGiftcodeMarketingView} path={Routes.adminGiftcodeMarketingActive.view} />
				<Route component={ListGiftcodeMarketingView} path={Routes.adminGiftcodemarketingList.view} />
				<Route component={CheckView} path={Routes.adminGiftcodeMarketingCheck.view} />
				<Route component={AdminProfitAgentView} path={Routes.adminProfitAgent.view} />
				<Route component={AdminTransferHistoryView} path={Routes.adminTransferAgentHistory.view} />
				<Route component={AdminTransferView} path={Routes.adminTransferRed.view} />
				<Route component={AdminTransferUserHistoryView} path={Routes.adminTransferHistory.view} />
				<Route component={MoneyLineView} path={Routes.adminMoneyLine.view} />
				<IndexRoute component={SummaryView} />
				<Route component={HistoryTaiXiuView} path={Routes.taixiuHistory.view} />
				<Route component={HistoryBauCuaView} path={Routes.baucuaHistory.view} />
				<Route component={HistoryXocDiaView} path={Routes.xocdiaHistory.view} />
				<Route component={HistoryMinipokerView} path={Routes.minipokerHistory.view} />
				<Route component={HistoryMini3cayView} path={Routes.mini3cayHistory.view} />
				<Route component={HistoryBigbabolView} path={Routes.bigbabolHistory.view} />
				<Route component={HistoryAngrybirdsView} path={Routes.angrybirdsHistory.view} />
				<Route component={HistoryVuongquocredView} path={Routes.vuongquocredHistory.view} />
    <Route component={HistoryTamhungView} path={Routes.tamhungHistory.view} />
				<Route component={HistoryCandyView} path={Routes.candyHistory.view} />
				<Route component={HistoryLonglanView} path={Routes.longlanHistory.view} />
    <Route component={HistoryZeusView} path={Routes.zeusHistory.view} />
				<Route component={HistoryXosoView} path={Routes.xosoHistory.view} />
				<Route component={UserListView} path={Routes.userlist.view} />
				<Route component={UserStatementTransferView} path={Routes.userStatementTransfer.view} />
				<Route component={UserStatementCardView} path={Routes.userStatementCard.view} />
				<Route component={UserDepositCheckView} path={Routes.userDepositCheck.view} />
				{/*<Route component={UserListView} path={Routes.home.view} />}
				{/*<Route component={ResultView} path={Routes.result.view} />
				<Route component={OutsView} path={Routes.outstanding.view} />
				<Route component={WinLossView} path={Routes.winloss.view} />
				<Route component={StatementView} path={Routes.statement.view} />*/}
			</Route>
			{/*<Route component={LockScreenView} path={Routes.lockscreen.view} />
			{/*<Route path="*" component={UserListView}/>*/}
		</Route>
		<Route component={LoginView} path={Routes.login.view}>
		</Route>
	</Route>
)

module.exports = routes



