import {
    ONCHANGE_NICKNAME_TRANSFER_HISTORY_ADMIN
} from 'modules/home.admin.transfer.history.nickname/types'
const onChange = (val) => {
    return (dispatch) => {
        dispatch ({
            type: ONCHANGE_NICKNAME_TRANSFER_HISTORY_ADMIN,
            payload: {
            	data: val
            }
        })
    }
}
module.exports = onChange


