import {
    INIT_MENU
} from 'modules/home.menu/types'
const init = () => {
    return (dispatch, getState) => {
        const { data: userInfo } = getState().userInfo || {}
        const { rights } = userInfo || {}
        var menus = Helper._mapRightsToMenu(rights)
        dispatch({
            type: INIT_MENU,
            payload: {
                data: menus
            }
        })
    }
}
module.exports = init


