import {
    ONSUBMIT_ADMIN_ADD_GIFTCODE_AGENT
} from 'modules/home.admin.giftcode.agent.add/types'
import { show as showAlert } from 'modules/notify/actions'
const onSubmit = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: daily } = getState().dailyGiftCodeDaiLyAdd || {}
        const { data: menhgia } = getState().menhgiaGiftCodeDaiLyAdd || {}
        const { data: soluong } = getState().soluongGiftCodeDaiLyAdd || {}
        const { data: ngaythang } = getState().ngaythangGiftCodeDaiLyAdd || {}
        console.log(getState().ngaythangGiftCodeDaiLyAdd);
        $(`#btnAddGiftCodeDaiLyID`).addClass('isLoading')
        axios.post(`${Config.API_URL}admin/giftcodeDailyAdd`, {

                Data: {
                    daily: daily,
                    menhgia: menhgia,
                    soluong: soluong,
                    ngaythang: ngaythang
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { data: info, success } = data || {}
                if (success) {
                    dispatch(showAlert('success', info.message))
                } else {
                    dispatch(showAlert('error', info.message))
                }
                $(`#btnAddGiftCodeDaiLyID`).removeClass('isLoading')
            }, (err) => {
                reject(err)
                $(`#btnAddGiftCodeDaiLyID`).removeClass('isLoading')
            })
    })
}
module.exports = onSubmit



