import {
    onChange,
    onError
} from 'modules/home.admin.giftcode.marketing.add.soluong/actions'
class SoLuongView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    render() {
        const { data } = this.props.soluongGiftCodeMarketingAdd || {}
        return (
            <div className="form-group-box" id="validateMessSoLuongGiftCodeMarketingAdd">
            <label className="control-label">Số lượng</label>
				<div className="input">
					<input
						type="number"
						min="1"
						className="form-control"
						placeholder="Số lượng"
						value={data}
						onChange={event=>{
							this._onChange(event)
						}}
						 />
				</div>
			</div>
        )
    }
}
const mapStateToProps = ({
    soluongGiftCodeMarketingAdd
}) => {
    return {
        soluongGiftCodeMarketingAdd
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        onError
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(SoLuongView)



