import {
    ONCHANGE_USERNAME_LOGIN
} from 'modules/auth.username/types'
const onChange = (val) => {
    return (dispatch, getState) => {
        const { data } = getState().authUsername || {}
        if (data != val) {
            dispatch({
                type: ONCHANGE_USERNAME_LOGIN,
                payload: {
                    data: val
                }
            })
        }
    }
}
module.exports = onChange


