import {
	ON_LOGOUT
} from 'modules/auth/types'
const { push } = ReactRouterRedux.routerActions || {}
const onLogout  = ()=>{
	return (dispatch)=>new Promise((resolve, reject)=>{
		Helper._removeCookie('token')
		Helper._removeCookie('userInfo')
		dispatch(push(Routes.login.view))
		resolve()
	})
}
module.exports = onLogout


