import {
	ONLOADING_BTN_LOGIN
} from 'modules/auth.btn/types'
const onLoading = ()=>{
	return (dispatch)=>{
		$(`#btnLoginID`).addClass("btn--loading")
		dispatch({
			type: ONLOADING_BTN_LOGIN,
			payload: null
		})
	}
}
module.exports = onLoading


