import {
    HIDE_MODAL_ADD_DAILY
} from 'modules/home.admin.agent.list.add/types'
const hide = () => {
    return (dispatch) => {
        $(`#modalAddAgentID`).removeClass('active')
        dispatch({
            type: HIDE_MODAL_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = hide


