import {
    ONFOCUS_OLD_CHANGE_PASSWORD
} from 'modules/home.changePassword.oldpass/types'
const onFocus = () => {
    return (dispatch) => {
        $(`#oldPassID`).focus()
        dispatch({
            type: ONFOCUS_OLD_CHANGE_PASSWORD,
            payload: null
        })
    }
}
module.exports = onFocus


