const _mapUrlToTitle = (url) => {
  return {
    'statement': 'SAO KÊ',
    'login': 'Đăng nhập',
    '': 'Trang chủ',
    'outstanding': 'VÉ ĐANG CHẠY',
    'winloss': 'VÉ ĐÃ TÍNH',
    'result': 'KẾT QUẢ',
    'userlist': 'TÀI KHOẢN',
  } [url]
}
module.exports = _mapUrlToTitle


