import {
    ONCHANGE_MENHGIA_GIFTCODE_DAILY
} from 'modules/home.admin.giftcode.agent.add.menhgia/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_MENHGIA_GIFTCODE_DAILY:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


