import {
    ONSUBMIT_TRANSFER_ADMIN
} from 'modules/home.admin.transfer/types'
import { show as showAlert } from 'modules/notify/actions'
import {
    onError as onErrorNickname
} from 'modules/home.admin.transfer.nickname/actions'
const onSubmit = (isAdd) => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: nickname } = getState().nicknameTransferAdmin || {}
        const { data: desc } = getState().descTransferAdmin || {}
        const { data: valueRed } = getState().valueTransferAdmin || {}
        $(`#btnTransferAdmin${isAdd ? 'Add' : 'Minus'}ID`).addClass('isLoading')
        dispatch({
            type: ONSUBMIT_TRANSFER_ADMIN,
            payload: null
        })
        axios.post(`${Config.API_URL}admin/transfer`, {
                Data: {
                    nickname: nickname,
                    desc: desc,
                    valueRed: parseInt(valueRed.toString().split(',').join('')) * (isAdd ? 1 : -1),
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { data: info, success } = data || {}
                if (success) {
                    dispatch(showAlert('success', info.message))
                } else {
                    if (info.nickname) {
                        dispatch(onErrorNickname(info.nickname))
                    } else {
                        dispatch(showAlert('error', info.message))
                    }
                }
                $(`#btnTransferAdmin${isAdd ? 'Add' : 'Minus'}ID`).removeClass('isLoading')
            }, (err) => {
                reject(err)
                $(`#btnTransferAdmin${isAdd ? 'Add' : 'Minus'}ID`).removeClass('isLoading')
            })
    })
}
module.exports = onSubmit


