import {
    getData
} from 'modules/home.games.history.xoso/actions'
import PaginationView from 'modules/home.games.history.xoso.pagination/PaginationView'
import NicknameView from 'modules/home.games.history.xoso.nickname/NicknameView'
import FromView from 'modules/home.games.history.xoso.from/FromView'
import ToView from 'modules/home.games.history.xoso.to/ToView'
import SubmitView from 'modules/home.games.history.xoso.submit/SubmitView'
class HistoryView extends React.Component {
    componentDidMount() {
        this.props.getData()
    }
    render() {
        const { data } = this.props.xosoHistory || {}
        const { show, current } = this.props.xosoHistoryPagination || {}
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Lịch sử Xổ Số</h1>
					</div>
				</div>
				<div className="page__body">
					<div className="box margin-top-10">
                        <div className="tableFilter">
                            <div className="tableFilter__row">
                                    <div className="tableFilter__column">
                                    </div>
                                    <div className="tableFilter__column">
                                        <NicknameView />
                                    </div>
                                    <div className="tableFilter__column">
                                        <FromView />
                                    </div>
                                    <div className="tableFilter__column">
                                        <ToView />
                                    </div>
                                    <div className="tableFilter__row">
                                    <div className="tableFilter__column" />
                                    <div className="tableFilter__column" />
                                    <div className="tableFilter__column">
                                        <SubmitView />
                                    </div>
                                </div>
                            </div>
                        </div>
						<div className="table-container margin-top-10">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                            <table className="table sticky">
                                <thead>
                                    <tr>
                                        <th className="cell-no">STT</th>
                                        <th>Name</th>
                                        <th>Loại</th>
                                        <th>Số Chọn</th>
                                        <th>Điểm</th>
                                        <th>Cược</th>
                                        <th>ZON Nhận</th>
                                        <th>Trạng Thái</th>
                                        <th>Thời Gian</th>
                                    </tr>
                                </thead>
                                <tbody>
                                	{data.map((item, index)=>{
                                		return (
                                			<tr key={`history_${item._id}`}>
		                                        <td className="cell-no text-center">{(current - 1) * show+index+1}</td>
		                                        <td className="text-nowrap text-center">
		                                        	{item.name}
		                                        </td>
		                                        <td className="text-center">{Helper._mapTypeXSMB(item.type)}</td>
		                                        <td className={`cell-datetime text-center`}>{item.so.join()}</td>
                                                <td className="text-center">{item.diem}</td>
		                                        <td className="text-center">{Helper._formatMoney(item.cuoc)}</td>
		                                        <td className={`text-center ${item.win >= item.cuoc ? 'text-positive' : 'text-negative'}`}>{Helper._formatMoney(item.win)}</td>
                                                <td className="text-center">
                                                    <div className="tooltip">
                                                        <div className="tooltip__header">
                                                            <span className={`status status--${item.thanhtoan ? item.win >= item.cuoc ? 'success' : 'renote' : 'freezed'}`}>
                                                             {item.thanhtoan ? item.win >= item.cuoc ? 'Thắng' : 'Thua' : 'Chưa Có Kết Quả'}
                                                            </span>
                                                        </div>
                                                        <div className="tooltip__body right hide">
                                                            <div className="tooltip__content">
                                                            </div>
                                                         </div>
                                                    </div>
                                                </td>
		                                        <td className="cell-datetime text-center">{moment(item.time).format(`DD/MM/YYYY HH:mm:ss`)}</td>
		                                    </tr>
                                			)
                                	})}
                                </tbody>
                            </table>
                        </div>
                    </div>
                            <div className="margin-top-10">
                                <PaginationView />
                        	</div>
                        </div>
					</div>
				</div>
			</div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData,
    }, dispatch)
}
const mapStateToProps = ({
    xosoHistory,
    xosoHistoryPagination
}) => {
    return {
        xosoHistory,
        xosoHistoryPagination
    }
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(HistoryView)



