import NameView from 'modules/home.admin.agent.list.add.name/NameView'
import NicknameView from 'modules/home.admin.agent.list.add.nickname/NicknameView'
import PhoneView from 'modules/home.admin.agent.list.add.phone/PhoneView'
import LocationView from 'modules/home.admin.agent.list.add.location/LocationView'
import FBView from 'modules/home.admin.agent.list.add.fb/FBView'
import {
    hide,
    onValidate,
    onSubmit
} from 'modules/home.admin.agent.list.add/actions'
class AddView extends React.Component {
    _onClose() {
        this.props.hide()
    }
    _onSubmit() {
        this.props.onValidate()
            .then(() => {
            	this.props.onSubmit()
            })
    }
    render() {
        return (
            <div className="modal sm" id="modalAddAgentID">
				<div className="modal__container">
					<div className="modal__dimmer"></div>
					<div className="modal__content">
						<div className="modal__header">
							<div className="title">Thêm Đại lý</div>
							<div className="btn close" onClick={event=>{
								this._onClose()
							}}><i className="ti-close"></i></div>
						</div>
						<div className="modal__body">
							<div className="form">
								<NameView />	
								<NicknameView />
								<PhoneView />
								<LocationView />
								<FBView />
							</div>
						</div>
						<div className="modal__footer">
							<a className="btn btn--cancel" onClick={event=>{
								this._onClose()
							}}>Cancel</a>
							<div className="btnWrapper" id="btnCreateAgentID">
							<input type="button" value="Ok" className="btn btn--submit" onClick={event=>{
								this._onSubmit()
							}}/>
							</div>
						</div>
					</div>
				</div>
			</div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        hide,
        onValidate,
        onSubmit
    }, dispatch)
}
export default ReactRedux.connect(null, mapDispatchToProps)(AddView)


