import {
    ONFOCUS_NICKNAME_ADD_DAILY2
} from 'modules/home.admin.agent2.list.add.nickname/types'
const onFocus = () => {
    return (dispatch) => {
        $(`#nicknameAddDaiLy2ID`).focus()
        dispatch({
            type: ONFOCUS_NICKNAME_ADD_DAILY2,
            payload: null
        })
    }
}
module.exports = onFocus


