import {
    GET_MONEYLINE
} from 'modules/home.admin.moneyline/types'
const INITIAL_STATE = {
    data: []
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case GET_MONEYLINE:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


