import {
    ONCHANGE_NICKNAME_ADD_DAILY2
} from 'modules/home.admin.agent2.list.add.nickname/types'
const onChange = (val) => {
    return (dispatch, getState) => {
        const { data } = getState().nicknameAddDaiLy2 || {}
        $(`#validateNicknameAddDaiLy2ID`).removeClass('has-error')
        $(`#validateNicknameMessAddDaiLy2ID`).addClass('hide')
        if (data != val) {
            dispatch({
                type: ONCHANGE_NICKNAME_ADD_DAILY2,
                payload: {
                    data: val
                }
            })
        }
    }
}
module.exports = onChange


