// import { checkMenu } from 'modules/header.menu/actions'
import { onLogout } from 'modules/auth/actions'
const Auth = ComposedComponent => {
    class Authentication extends React.Component {
        componentDidMount() {
            this._checkBeforeGoRoute()
        }
        componentDidUpdate() {
            this._checkBeforeGoRoute()
            // this._scalePage()
        }
        _scalePage() {
            const siteWidth = Config.siteWidth ? Config.siteWidth : 1024
            const scale = (screen ? screen.width : 1024) / siteWidth
            document.querySelector('meta[name="viewport"]').setAttribute(`content`, `width=${siteWidth}, initial-scale=${scale}`)
        }
        _checkBeforeGoRoute() {
            // this.props.checkMenu()
            if (Helper._getCookie('token')) {
                const { location } = window || {}
                const { pathname } = location || {}
            } else {
                // window.location.href = window.location.origin
                this.props.onLogout()
                this.props.push(Routes.login.view)
            }
        }
        render() {
            return <ComposedComponent {...this.props} />
        }
    }

    const mapStateToProps = ({ auth }) => {
        return {
            auth
        }
    }

    const mapDispatchToProps = (dispatch) => {
        return Redux.bindActionCreators({
            ...ReactRouterRedux.routerActions,
            onLogout
            // checkMenu
        }, dispatch)
    }

    return ReactRedux.connect(null, mapDispatchToProps)(Authentication)
}

export default Auth


