import {
    HIDE_CONFIRM_REMOVE_DAILY
} from 'modules/home.admin.agent.list.remove.confirm/types'
const hide = () => {
    return (dispatch) => {
        $(`#confirmRemoveAgentID`).removeClass('active')
        dispatch({
            type: HIDE_CONFIRM_REMOVE_DAILY,
            payload: null
        })
    }
}
module.exports = hide


