import {
    SHOW_NOTIFY,
    HIDE_NOTIFY
} from 'modules/notify/types'
const INITIAL_STATE = {
    status: false,
    type: 'success',
    message: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case SHOW_NOTIFY:
            return {
                ...state,
                status: true,
                type: action.payload.type,
                message: action.payload.message
            }
        case HIDE_NOTIFY:
            return {
                ...state,
                status: false
            }
        default:
            return state
    }
    return state
}
export default Reducer


