import {
    ONCHANGE_LOCATION_ADD_DAILY
} from 'modules/home.admin.agent.list.add.location/types'
const onChange = (val) => {
    return (dispatch, getState) => {
        const { data } = getState().phoneAddDaiLy || {}
        $(`#validateLocationAddDaiLyID`).removeClass('has-error')
        $(`#validateLocationMessAddDaiLyID`).addClass('hide')
        if (data != val) {
            dispatch({
                type: ONCHANGE_LOCATION_ADD_DAILY,
                payload: {
                    data: val
                }
            })
        }
    }
}
module.exports = onChange


