import {
    ONERROR_PHONE_ADD_DAILY
} from 'modules/home.admin.agent.list.add.phone/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validatePhoneAddDaiLyID`).addClass('has-error')
        $(`#validatePhoneMessAddDaiLyID`).removeClass('hide')
        $(`#validatePhoneMessAddDaiLyID`).text(mess)
        dispatch({
            type: ONERROR_PHONE_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onError


