import {
    ONVALIDTE_ADD_DAILY2
} from 'modules/home.admin.agent2.list.add/types'
import {
    onError as onErrorNickname,
    onFocus as onFocusNickname
} from 'modules/home.admin.agent2.list.add.nickname/actions'
const onValidate = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: nicknameAddDaiLy2 } = getState().nicknameAddDaiLy2 || {}

        if (!nicknameAddDaiLy2) {
            dispatch(onErrorNickname('Bắt buột'))
            dispatch(onFocusNickname())
            reject()
        } else {
            resolve()
        }
    })
}
module.exports = onValidate


