import {
    ONFOCUS_NAME_ADD_DAILY
} from 'modules/home.admin.agent.list.add.name/types'
const onFocus = () => {
    return (dispatch) => {
        $(`#nameAddDaiLyID`).focus()
        dispatch({
            type: ONFOCUS_NAME_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onFocus


