import {
    ONCHANGE_TOMDATE_AGENT2_PROFIT
} from 'modules/home.admin.agent.agent2.profit.to/types'
const onChange = (val) => {
    return (dispatch) => {
        dispatch({
            type: ONCHANGE_TOMDATE_AGENT2_PROFIT,
            payload: {
                data: val
            }
        })
    }
}

module.exports = onChange


