import {
    ONERROR_OLD_CHANGE_PASSWORD
} from 'modules/home.changePassword.oldpass/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateOldPassID`).addClass('has-error')
        $(`#validateOldPassMessID`).removeClass('hide')
        $(`#validateOldPassMessID`).text(mess)
        dispatch({
            type: ONERROR_OLD_CHANGE_PASSWORD,
            payload: null
        })
    }
}
module.exports = onError


