import {
    ONERROR_NGAYTHANG_GIFTCODE_DAILY
} from 'modules/home.admin.giftcode.agent.add.ngaythang/types'
const onError = () => {
    return (dispatch) => {
        $(`#validateMessNgayThangGiftCodeDaiLyAdd`).addClass('has-error')
        dispatch({
            type: ONERROR_NGAYTHANG_GIFTCODE_DAILY,
            payload: null
        })
    }
}
module.exports = onError



