import {
    SHOW_MODAL_CHANGEPASS
} from 'modules/home.changePassword/types'
import {
    onFocus
} from 'modules/home.changePassword.oldpass/actions'
const show = () => {
    return (dispatch) => {
        $(`#modalChangePassID`).addClass('active')
        dispatch(onFocus())
        dispatch({
            type: SHOW_MODAL_CHANGEPASS,
            payload: null
        })
    }
}
module.exports = show


