import {
    CHANGE_CURRENT_PAGE_TAIXIU_HISTORY_ADMIN
} from 'modules/home.games.history.taixiu.pagination/types'
const onChange = ({ totals, current, show }) => {
    return (dispatch) => new Promise((resolve, reject) => {
        dispatch({
            type: CHANGE_CURRENT_PAGE_TAIXIU_HISTORY_ADMIN,
            payload: {
                totals: totals,
                current: current,
                show: show
            }
        })
        resolve()
    })
}
module.exports = onChange


