import {
    ONCHANGE_FB_ADD_DAILY
} from 'modules/home.admin.agent.list.add.fb/types'
const onChange = (val) => {
    return (dispatch, getState) => {
        const { data } = getState().fbAddDaiLy || {}
        $(`#validateFBAddDaiLyID`).removeClass('has-error')
        $(`#validateFBMessAddDaiLyID`).addClass('hide')
        if (data != val) {
            dispatch({
                type: ONCHANGE_FB_ADD_DAILY,
                payload: {
                    data: val
                }
            })
        }
    }
}
module.exports = onChange


