import {
    ONCHANGE_CODE_GIFTCODE_MARKETING_CHECK
} from 'modules/home.admin.giftcode.marketing.check.code/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_CODE_GIFTCODE_MARKETING_CHECK:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


