import {
    onChange
}
from 'modules/home.admin.agent.list.add.location/actions'
import {
	onFocus as focusFB
} from 'modules/home.admin.agent.list.add.fb/actions'
class LocationView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    _onKeyDown(event) {
        const { keyCode } = event || {}
        switch (keyCode) {
            case 13:
                this.props.focusFB()
                break
            default:
                break
        }
    }
    render() {
        const { data } = this.props.locationAddDaiLy || {}
        return (
            <div className="form-group-box" id="validateLocationAddDaiLyID">
				<label className="control-label">Khu vực</label>
				<div className="input">
					<input 
						onChange={event=>{
							this._onChange(event)
						}}
						onKeyDown={event=>{
							this._onKeyDown(event)
						}}
						value={data}
						type="text" 
						id="locationAddDaiLyID" 
						className="form-control" 
						placeholder="Khu vực" />
				</div>
                <p className="help-block hide" id="validateLocationMessAddDaiLyID"></p>
			</div>
        )
    }
}
const mapStateToProps = ({
    locationAddDaiLy
}) => {
    return {
        locationAddDaiLy
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        focusFB
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(LocationView)


