import {
    ONCHANGE_NICKNAME_USERLIST
} from 'modules/home.userlist.nickname/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_NICKNAME_USERLIST:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


