import {
    ONCHANGE_SOLUONG_GIFTCODE_MARKETING_LIST
} from 'modules/home.admin.giftcode.marketing.list.soluong/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateMessSoLuongGiftCodeMarketingList`).removeClass('has-error')
        dispatch ({
            type: ONCHANGE_SOLUONG_GIFTCODE_MARKETING_LIST,
            payload: {
            	data: val
            }
        })
    }
}
module.exports = onChange


