import {
    ONSUBMIT_ADMIN_ADD_GIFTCODE_MARKETING
} from 'modules/home.admin.giftcode.marketing.add/types'
import { show as showAlert } from 'modules/notify/actions'
const onSubmit = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: menhgia } = getState().menhgiaGiftCodeMarketingAdd || {}
        const { data: soluong } = getState().soluongGiftCodeMarketingAdd || {}
        const { data: ngaythang } = getState().ngaythangGiftCodeMarketingAdd || {}
        $(`#btnAddGiftCodeMarketingID`).addClass('isLoading')
        axios.post(`${Config.API_URL}admin/giftcodeDailyAdd`, {
                Data: {
                    menhgia: menhgia,
                    soluong: soluong,
                    ngaythang: ngaythang
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { data: info, success } = data || {}
                if (success) {
                    dispatch(showAlert('success', info.message))
                } else {
                    dispatch(showAlert('error', info.message))
                }
                $(`#btnAddGiftCodeMarketingID`).removeClass('isLoading')
            }, (err) => {
                reject(err)
                $(`#btnAddGiftCodeMarketingID`).removeClass('isLoading')
            })
    })
}
module.exports = onSubmit



