import {
    SHOW_MODAL_USER_HISTORY
} from 'modules/home.user.history/types'
const show = (user) => {
    return (dispatch) => new Promise((resolve, reject) => {
        $('#modalUserHistoryID').addClass('active')
        dispatch({
            type: SHOW_MODAL_USER_HISTORY,
            payload: {
                user: user
            }
        })
        resolve(user)
    })
}
module.exports = show


