import {
    CHANGE_CURRENT_PAGE_XOSO_HISTORY_ADMIN
}
from 'modules/home.games.history.xoso.pagination/types'
const INITAL_STATE = {
    totals: 0,
    current: 1,
    show: 50
}
const Reducer = (state = INITAL_STATE, action) => {
    switch (action.type) {
        case CHANGE_CURRENT_PAGE_XOSO_HISTORY_ADMIN:
            return {
                ...state,
                totals: action.payload.totals,
                current: action.payload.current,
                show: action.payload.show
            }
        default:
            return state
    }
    return state
}
export default Reducer


