import {
    ONCONFIRM_REMOVE_DAILY2
} from 'modules/home.admin.agent2.list/types'
import {
    show as showConfirm
} from 'modules/home.admin.agent2.list.remove.confirm/actions'
const onConfirmRemove = (idRemove) => {
    return (dispatch) => {
        dispatch({
            type: ONCONFIRM_REMOVE_DAILY2,
            payload: {
                idRemove: idRemove
            }
        })
        dispatch(showConfirm(idRemove))
    }
}
module.exports = onConfirmRemove


