import {
    ONCHANGE_NICKNAME_TRANSFER_ADMIN
} from 'modules/home.admin.transfer.nickname/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateNicknameTransferAdmin`).removeClass('has-error')
        $(`#validateNicknameTransferAdmin`).removeClass('has-success')
        $(`#validateMessNicknameTransferAdmin`).addClass('hide')
        dispatch ({
            type: ONCHANGE_NICKNAME_TRANSFER_ADMIN,
            payload: {
            	data: val
            }
        })
    }
}
module.exports = onChange


