import {
    ONERROR_PASSWORD_LOGIN
} from 'modules/auth.password/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateComonIDText`).text(mess)
        $(`#validateComonIDText`).removeClass('hide')
        setTimeout(()=>{
        	$(`#validateComonIDText`).addClass('hide')
        }, 3000)
        dispatch({
            type: ONERROR_PASSWORD_LOGIN,
            payload: null
        })
    }
}
module.exports = onError


