import {
    ONFOCUS_FB_ADD_DAILY
} from 'modules/home.admin.agent.list.add.fb/types'
const onFocus = () => {
    return (dispatch) => {
        $(`#fbAddDaiLyID`).focus()
        dispatch({
            type: ONFOCUS_FB_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onFocus


