import { ONVALIDATE_ADMIN_ADD_GIFTCODE_AGENT } from 'modules/home.admin.giftcode.agent.add/types'
import {
    onError as onErrorDaiLy
} from 'modules/home.admin.giftcode.agent.add.daily/actions'
import {
    onError as onErrorMenhGia
} from 'modules/home.admin.giftcode.agent.add.menhgia/actions'
import {
    onError as onErrorSoLuong
} from 'modules/home.admin.giftcode.agent.add.soluong/actions'
import {
    onError as onErrorNgayThang
} from 'modules/home.admin.giftcode.agent.add.ngaythang/actions'
const onValidate = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: daily } = getState().dailyGiftCodeDaiLyAdd || {}
        const { data: menhgia } = getState().menhgiaGiftCodeDaiLyAdd || {}
        const { data: soluong } = getState().soluongGiftCodeDaiLyAdd || {}
        const { data: ngaythang } = getState().ngaythangGiftCodeDaiLyAdd || {}
        if (!daily) {
            dispatch(onErrorDaiLy())
            reject()
        }
        if (!menhgia) {
            dispatch(onErrorMenhGia())
            reject()
        }
        if (!soluong) {
            dispatch(onErrorSoLuong())
            reject()
        }
        if (!ngaythang) {
            dispatch(onErrorNgayThang())
            reject()
        }
        if(menhgia && soluong && daily && ngaythang)
        	resolve()
    })
}
module.exports = onValidate



