import {
	ONERROR_CONFIRM_CHANGE_PASSWORD
} from 'modules/home.changePassword.confirmpass/types'
const onError = (mess)=>{
	return (dispatch)=>{
		$(`#validateConfirmPassID`).addClass('has-error')
        $(`#validateConfirmPassMessID`).removeClass('hide')
        $(`#validateConfirmPassMessID`).text(mess)
        dispatch({
            type: ONERROR_CONFIRM_CHANGE_PASSWORD,
            payload: null
        })
	}
}
module.exports = onError


