import {
    ONCHANGE_FB_ADD_DAILY
} from 'modules/home.admin.agent.list.add.fb/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_FB_ADD_DAILY:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


