import {
    ONCHANGE_CONFIRM_CHANGE_PASSWORD
} from 'modules/home.changePassword.confirmpass/types'
const onChange = (val) => {
    return (dispatch, getState) => {
        const { data } = getState().confirmPass || {}
        $(`#validateConfirmPassID`).removeClass('has-error')
        $(`#validateConfirmPassMessID`).addClass('hide')
        if (data != val) {
            dispatch({
                type: ONCHANGE_CONFIRM_CHANGE_PASSWORD,
                payload: {
                    data: val
                }
            })
        }
    }
}
module.exports = onChange


