import {
    init
} from 'modules/home.header/actions'
import {
    onLogout
} from 'modules/auth/actions'
import {
    show as showChangePass
} from 'modules/home.changePassword/actions'
import BalanceView from 'modules/home.balance/BalanceView'
class HeaderView extends React.Component {
    componentDidMount() {
        this.props.init()
        this._initMenu()
        this._initToggleMenu()
    }
    _initToggleMenu() {
        $(".toggle").click(function() {
            $(".page").toggleClass("close-aside")
        })
    }
    _initMenu() {
        const $menu = $('#menuHeaderID');
        $(document).mouseup(e => {
            if (!$menu.is(e.target) // if the target of the click isn't the container...
                &&
                $menu.has(e.target).length === 0) // ... nor a descendant of the container
            {
                $menu.removeClass('active')
            }
        })

        $('#toggleID').on('click', () => {
            $menu.toggleClass('active')
        })
    }
    _logout(event) {
        this.props.onLogout()
    }
    _openChangePass() {
        this.props.showChangePass()
    }
    render() {
        const { data } = this.props.userInfo || {}
        return (
            <header className="header">
                    <div className="container">
                        <div className="header__content">
                            <div className="brand">
                                <a href="/"><img src="/images/logo.png"/></a>
                            </div>
                            <div className="title">
                                <a className="toggle btn btn-only-icon">
                                    <i className="ti-menu"></i>
                                </a>
                            </div>
                            <div className="actions">
                                <div className="noti">
                                    <div className="noti__display">
                                        <a>
                                            <i className="ti-bell"></i>
                                            {/*<i className="number"></i>*/}
                                        </a>
                                    </div>
                                    {/*<div className="noti__container">
                                        <div className="noti__content">
                                            <ul>
                                                <li className="count"><a>You have 10 notifications</a></li>
                                                <li className="active">
                                                    <a>
                                                        <i className="ti-file">
                                                    </i> <span>5 new members joined today</span></a>
                                                </li>
                                                <li>
                                                    <a>
                                                        <i className="ti-file"></i> <span> Very long description here that may not fit into the page and may cause design problems</span>
                                                    </a>
                                                </li>
                                                <li>
                                                    <a><i className="ti-file"></i> <span>5 new members joined today</span></a>
                                                </li>
                                                <li>
                                                    <a><i className="ti-file"></i> <span>5 new members joined today</span></a>
                                                </li>
                                                <li className="viewall">
                                                    <a>View all</a>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>*/}
                                </div>
                               {data.rights!=9 ? (<BalanceView />) : (<span />)}
                                <div className="account" id="menuHeaderID">
                                    <div className="account__display" id="toggleID">
                                        <div className="avatar">
                                            <img src="/images/user.png"/>
                                            <span className="name">&nbsp;{data ? data.nickname : ''}</span>
                                        </div>
                                    </div>
                                    <div className="account__container">
                                        <div className="account__content">
                                            <ul>
                                                <li onClick={event=>{
                                                    this._openChangePass()
                                                }}><a>Đổi mật khẩu</a></li>
                                                <li onClick={event=>{
                                                    this._logout(event
                                                        )}}>
                                                <a>Thoát</a></li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </header>
        )
    }
}
const mapStateToProps = ({
    userInfo
}) => {
    return {
        userInfo
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        init,
        onLogout,
        showChangePass
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(HeaderView)



