import {
    ONCHANGE_CODE_GIFTCODE_MARKETING_CHECK
} from 'modules/home.admin.giftcode.marketing.check.code/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateMessCodeGiftCodeMarketingCheck`).removeClass('has-error')
        dispatch ({
            type: ONCHANGE_CODE_GIFTCODE_MARKETING_CHECK,
            payload: {
            	data: val ? val.toUpperCase() : val
            }
        })
    }
}
module.exports = onChange


