import {
	ONERROR_NEW_CHANGE_PASSWORD
} from 'modules/home.changePassword.newpass/types'
const onError = (mess)=>{
	return (dispatch)=> {
		$(`#validateNewPassID`).addClass('has-error')
        $(`#validateNewPassMessID`).removeClass('hide')
        $(`#validateNewPassMessID`).text(mess)
        dispatch({
            type: ONERROR_NEW_CHANGE_PASSWORD,
            payload: null
        })
	}
}
module.exports = onError


