import {
    ONCHANGE_VALUE_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent.value/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateValueTransferDaiLy`).removeClass('has-error')
        $(`#validateMessValueTransferDaiLy`).addClass('hide')
        dispatch ({
            type: ONCHANGE_VALUE_TRANSFER_AGENT,
            payload: {
            	data: val
            }
        })
    }
}
module.exports = onChange


