import {
    ONFOCUS_NEW_CHANGE_PASSWORD
} from 'modules/home.changePassword.newpass/types'
const onFocus = () => {
    return (dispatch) => {
        $(`#newPassID`).focus()
        dispatch({
            type: ONFOCUS_NEW_CHANGE_PASSWORD,
            payload: null
        })
    }
}
module.exports = onFocus


