import {
    HIDE_MODAL_USER_HISTORY
} from 'modules/home.user.history/types'
const hide = () => {
    return (dispatch) => {
        $('#modalUserHistoryID').removeClass('active')
        dispatch({
            type: HIDE_MODAL_USER_HISTORY,
            payload: null
        })
    }
}
module.exports = hide


