import {
    SHOW_MODAL_ADD_DAILY2
} from 'modules/home.admin.agent2.list.add/types'
import {
	onFocus as forcusNickname
} from 'modules/home.admin.agent2.list.add.nickname/actions'
const show = () => {
    return (dispatch) => {
        $(`#modalAddAgent2ID`).addClass('active')
        dispatch({
            type: SHOW_MODAL_ADD_DAILY2,
            payload: null
        })
        dispatch(forcusNickname())
    }
}
module.exports = show


