import {
    ONBLUR_PASSWORD_LOGIN
} from 'modules/auth.password/types'
const onBlur = () => {
    return (dispatch) => {
        $(`#passID`).blur()
        dispatch({
            type: ONBLUR_PASSWORD_LOGIN,
            payload: null
        })
    }
}
module.exports = onBlur


