import {
    onChange
}
from 'modules/home.admin.agent.list.add.fb/actions'
import {
    onFocus as focusFB
} from 'modules/home.admin.agent.list.add.fb/actions'
import {
    hide,
    onValidate,
    onSubmit
} from 'modules/home.admin.agent.list.add/actions'
class FBView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    _onKeyDown(event) {
        const { keyCode } = event || {}
        switch (keyCode) {
            case 13:
                this.props.onValidate()
                    .then(() => {
                        this.props.onSubmit()
                    })
                break
            default:
                break
        }
    }
    render() {
        const { data } = this.props.fbAddDaiLy || {}
        return (
            <div className="form-group-box" id="validateFBAddDaiLyID">
                <label className="control-label">ID Facebook</label>
                <div className="input">
                    <input 
                        onChange={event=>{
                            this._onChange(event)
                        }}
                        onKeyDown={event=>{
                            this._onKeyDown(event)
                        }}
                        value={data}
                        type="text" 
                        id="fbAddDaiLyID" 
                        className="form-control" 
                        placeholder="ID Facebook" />
                </div>
                <p className="help-block hide" id="validateFBMessAddDaiLyID"></p>
            </div>
        )
    }
}
const mapStateToProps = ({
    fbAddDaiLy
}) => {
    return {
        fbAddDaiLy
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        focusFB,
        onValidate,
        onSubmit
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(FBView)


