import {
    GET_DATA_USER_HISTORY
} from 'modules/home.user.history/types'
const getData = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data, user } = getState().userHistory || {}
        const { data: filterType } = getState().typeHistoryUserFilter || {}
        const { id } = user || {}
        $('.loading').addClass('active')
        axios.get(`${Config.API_URL}admin/userHistory`, {
                params: {
                    id: id,
                    type: filterType
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info } = data || {}
                const { logs, userInfo } = info || {}
                if (success) {
                    dispatch({
                        type: GET_DATA_USER_HISTORY,
                        payload: {
                            data: {
                                logs: logs,
                                userInfo: userInfo
                            }
                        }
                    })
                }
                $('.loading').removeClass('active')
                resolve(response)
            }, (err) => {
                $('.loading').removeClass('active')
                reject(err)
            })
    })
}
module.exports = getData


