import {
    INIT_MENU,
    ONOPEN_MENU,
    ONACTIVE_SUB_MENU
} from 'modules/home.menu/types'
const INITIAL_STATE = {
    data: {},
    active: -1,
    opened: -1
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case INIT_MENU:
            return {
                ...state,
                data: action.payload.data
            }
        case ONACTIVE_SUB_MENU:
            return {
                ...state,
                active: action.payload.active
            }
        case ONOPEN_MENU:
            return {
                ...state,
                opened: action.payload.opened
            }
        default:
            return state
    }
    return state
}
export default Reducer


