import {
    ONCHANGE_DESC_TRANSFER_ADMIN
} from 'modules/home.admin.transfer.desc/types'
const onChange = (val) => {
    return (dispatch) => {
        $(`#validateDescTransferAdmin`).removeClass('has-error')
        $(`#validateMessDescTransferAdmin`).addClass('hide')
        dispatch({
            type: ONCHANGE_DESC_TRANSFER_ADMIN,
            payload: {
                data: val
            }
        })
    }
}
module.exports = onChange


