import {
    GET_MONEYLINE
} from 'modules/home.admin.moneyline/types'
import {
    CHANGE_CURRENT_PAGE_MONEYLINE
} from 'modules/home.admin.moneyline.pagination/types'
const getData = (isFirst) => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { show, current } = getState().gameMoneylinePagination || {}
        const { data: fromDate } = getState().filterGameMoneylineFrom || {}
        const { data: toDate } = getState().filterGameMoneylineTo || {}
        $('.loading').addClass('active')
        axios.post(`${Config.API_URL}admin/moneylineHistory`, {
                Data: {
                    limit: show,
                    offset: (current - 1) * show,
                    fromDate: fromDate ? moment(fromDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 00:00:00 Z') : fromDate,
                    toDate: toDate ? moment(toDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 23:59:59 Z') : toDate,
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info, totals } = data || {}
                if (success) {
                    dispatch({
                        type: GET_MONEYLINE,
                        payload: {
                            data: info
                        }
                    })
                    dispatch({
                        type: CHANGE_CURRENT_PAGE_MONEYLINE,
                        payload: {
                            totals: totals,
                            show: show,
                            current: current
                        }
                    })
                }
                $('.loading').removeClass('active')
            }, (err) => {
                $('.loading').removeClass('active')
                reject(err)
            })
    })
}
module.exports = getData


