import FromView from 'modules/home.admin.agents.profit.from/FromView'
import ToView from 'modules/home.admin.agents.profit.to/ToView'
import SubmitView from 'modules/home.admin.agents.profit.submit/SubmitView'
import DaiLyView from 'modules/home.admin.agents.profit.daily/DaiLyView'
import {
    onSubmit
} from 'modules/home.admin.agents.profit.submit/actions'
class ProfitView extends React.Component {
    componentDidMount() {
        this.props.onSubmit()
    }
    render() {
        const { data } = this.props.profitAgents || {}
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Doanh thu Đại lý</h1>
					</div>
				</div>
				<div className="page__body">
					<div className="box margin-top-10">
					<div className="box margin-top-10">
                        <div className="tableFilter">
                            <div className="tableFilter__row">
                                <div className="tableFilter__column">
                                    <DaiLyView />
                                </div>
                                <div className="tableFilter__column">
                                    <FromView />
                                </div>
                                <div className="tableFilter__column">
                                    <ToView />
                                </div>
                                <div className="tableFilter__column">
                                    <SubmitView />
                                </div>
                            </div>
                        </div>
					</div>
						<div className="table-container margin-top-10">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                                    <table className="table sticky">
                                        <thead>
                                            <tr>
                                                <th className="cell-no">STT</th>
                                                <th>Đại lý</th>
                                                <th>Doanh số Bán</th>
                                                <th>Doanh số Mua</th>
                                                <th>Doanh số Liên minh</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {data.map((item, index)=>{
                                                return (
                                                    <tr key={`profit_agent_${index}`}>
                                                        <td className="cell-no text-center">
                                                            {index + 1}
                                                        </td>
                                                        <td className="text-center">
                                                            {item.nickname}
                                                        </td>
                                                        <td className={`cell-datetime text-center ${item.totalFrom >=0 ? 'text-positive' : 'text-negative'}`}>{Helper._formatMoney(item ? item.totalFrom : 0)}</td>
                                                        <td className={`cell-datetime text-center ${item.totalTo >=0 ? 'text-positive' : 'text-negative'}`}>{Helper._formatMoney(item? item.totalTo : 0)}</td>
                                                        <td className={`cell-datetime text-center ${(item.totalFrom + item.totalTo) >=0 ? 'text-positive' : 'text-negative'}`}>{Helper._formatMoney((item ? item.totalFrom : 0) + (item ? item.totalTo : 0))}</td>
                                                    </tr>
                                                    )
                                            })}
                                        	
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
					</div>
				</div>
			</div>
        )
    }
}
const mapStateToProps = ({
    profitAgents
}) => {
    return {
        profitAgents
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onSubmit
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(ProfitView)


