import {
    ON_CHECK_GIFTCODE_MARKETING
} from 'modules/home.admin.giftcode.marketing.check/types'
const onSubmit = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: code } = getState().codeGiftCodeMarketingCheck || {}
        $('.loading').addClass('active')
        axios.post(`${Config.API_URL}admin/giftcodeCheck`, {
                Data: {
                    code: code
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info } = data || {}
                if (success) {
                    dispatch({
                        type: ON_CHECK_GIFTCODE_MARKETING,
                        payload: {
                            data: info
                        }
                    })
                }
                $('.loading').removeClass('active')
                resolve()
            }, (err) => {
                $('.loading').removeClass('active')
                reject(err)
            })
    })
}
export default onSubmit


