import {
    hide
} from 'modules/home.changePassword/actions'
import OldPassView from 'modules/home.changePassword.oldpass/OldPassView'
import NewPassView from 'modules/home.changePassword.newpass/NewPassView'
import ConfirmPassView from 'modules/home.changePassword.confirmpass/ConfirmPassView'
class ChangePassView extends React.Component {
    _onClose() {
        this.props.hide()
    }
    render() {
        return (
            <div className="modal sm" id="modalChangePassID">
				<div className="modal__container">
					<div className="modal__dimmer"></div>
					<div className="modal__content">
						<div className="modal__header">
							<div className="title">Đổi mật khẩu</div>
							<div className="btn close" onClick={event=>{
								this._onClose()
							}}><i className="ti-close"></i></div>
						</div>
						<div className="modal__body">
							<div className="form">
								<OldPassView />
								<NewPassView />
								<ConfirmPassView />	
							</div>
							
						</div>
						<div className="modal__footer">
							<a className="btn btn--cancel" onClick={event=>{
								this._onClose()
							}}>Huỷ</a>
							<div className="btnWrapper" id="btnChangePassID">
								<input type="button" value="Xác nhận" className="btn btn--submit" />
							</div>
						</div>
					</div>
				</div>
			</div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        hide
    }, dispatch)
}
export default ReactRedux.connect(null, mapDispatchToProps)(ChangePassView)


