import {
    ONERROR_NAME_ADD_DAILY
} from 'modules/home.admin.agent.list.add.name/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateNameAddDaiLyID`).addClass('has-error')
        $(`#validateNameMessAddDaiLyID`).removeClass('hide')
        $(`#validateNameMessAddDaiLyID`).text(mess)
        dispatch({
            type: ONERROR_NAME_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onError


