import {
    ONCHANGE_SOLUONG_GIFTCODE_DAILY
} from 'modules/home.admin.giftcode.agent.add.soluong/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateMessSoLuongGiftCodeDaiLyAdd`).removeClass('has-error')
        dispatch ({
            type: ONCHANGE_SOLUONG_GIFTCODE_DAILY,
            payload: {
            	data: val
            }
        })
    }
}
module.exports = onChange


