import {
    ONCHANGE_NICKNAME_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent.nickname/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateNicknameTransferDaiLy`).addClass('has-error')
        $(`#validateMessNicknameTransferDaiLy`).removeClass('hide')
        $(`#validateMessNicknameTransferDaiLy`).text(mess)
        dispatch({
            type: ONCHANGE_NICKNAME_TRANSFER_AGENT,
            payload: null
        })
    }
}
module.exports = onError


