import LogoView from 'modules/auth.logo/LogoView'
import UsernameView from 'modules/auth.username/UsernameView'
import PasswordView from 'modules/auth.password/PasswordView'
import BtnView from 'modules/auth.btn/BtnView'
import {
	onFocus as focusUsername
} from 'modules/auth.username/actions'
class LoginView extends React.Component {
	componentDidMount(){
		this.props.focusUsername()
	}
	render(){
		return (
			<div className="login">
				<div className="login__content">
					<LogoView />
					<div className="login__body">
						<UsernameView />
						<PasswordView />
					</div>
					<BtnView />
				</div>
			</div>
			)
	}
}
const mapDispatchToProps = (dispatch)=>{
	return Redux.bindActionCreators({
		focusUsername
	}, dispatch)
}
export default ReactRedux.connect(null, mapDispatchToProps)(LoginView)


