import {
    getData
} from 'modules/home.admin.moneyline/actions'
import FromView from 'modules/home.admin.moneyline.from/FromView'
import ToView from 'modules/home.admin.moneyline.to/ToView'
import SubmitView from 'modules/home.admin.moneyline.submit/SubmitView'
class MoneyLineView extends React.Component {
    componentDidMount() {
        this.props.getData()
    }
    render() {
        const { data } = this.props.gameMoneyline || {}
        const { show, current } = this.props.gameMoneylinePagination || {}
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Dòng tiền trong Game</h1>
					</div>
				</div>
				<div className="page__body">
					<div className="box margin-top-10">
                        <div className="tableFilter">
                            <div className="tableFilter__row">
                                    <div className="tableFilter__column">
                                    </div>
                                    <div className="tableFilter__column">
                                    </div>
                                    <div className="tableFilter__column">
                                        <FromView />
                                    </div>
                                    <div className="tableFilter__column">
                                        <ToView />
                                    </div>
                                    <div className="tableFilter__row">
                                    <div className="tableFilter__column" />
                                    <div className="tableFilter__column" />
                                    <div className="tableFilter__column">
                                        <SubmitView />
                                    </div>
                                </div>
                            </div>
                        </div>
						<div className="table-container margin-top-10">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                                    <table className="table sticky">
                                        <thead>
                                            <tr>
                                                <th>Game</th>
                                                <th>Cược</th>
                                                <th>Trả Lại</th>
                                                <th>Thắng</th>
                                                <th>Phế</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                        	{data.map((item, index)=>{
                                        		return (
                                        			<tr key={`history_${index}`}>
        		                                        <td className="text-nowrap text-center">{item.name}</td>
        		                                        <td className="text-nowrap text-center">
                                                        <div className="tooltip">
                                                                <div className="tooltip__header">
                                                                    <span className="status status--success">{Helper._formatMoney(item.cuoc)}</span>
                                                                </div>
                                                                <div className="tooltip__body right hide">
                                                                    <div className="tooltip__content"></div>
                                                                </div>
                                                            </div>
        		                                        </td>
        		                                        <td className="text-center">
                                                            <div className="tooltip">
                                                                <div className="tooltip__header">
                                                                    <span className="status status--renote">{Helper._formatMoney(item.tralai)}</span>
                                                                </div>
                                                                <div className="tooltip__body right hide">
                                                                    <div className="tooltip__content"></div>
                                                                </div>
                                                            </div>
                                                        </td>
        		                                        <td className="cell-datetime text-center">
                                                            <div className="tooltip">
                                                                <div className="tooltip__header">
                                                                    <span className="status status--success">{Helper._formatMoney(item.thang)}</span>
                                                                </div>
                                                                <div className="tooltip__body right hide">
                                                                    <div className="tooltip__content"></div>
                                                                </div>
                                                            </div>
                                                        </td>
        		                                        <td className="cell-datetime text-center">
                                                        <div className="tooltip">
                                                                <div className="tooltip__header">
                                                                    <span className="status status--freezed">{Helper._formatMoney(item.phe)}</span>
                                                                </div>
                                                                <div className="tooltip__body right hide">
                                                                    <div className="tooltip__content"></div>
                                                                </div>
                                                            </div>
                                                        </td>
        		                                    </tr>
                                        			)
                                        	})}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            <div className="margin-top-10">
                        	</div>
                        </div>
					</div>
				</div>
			</div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData,
    }, dispatch)
}
const mapStateToProps = ({
    gameMoneyline,
    gameMoneylinePagination
}) => {
    return {
        gameMoneyline,
        gameMoneylinePagination
    }
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(MoneyLineView)


