import {
    ON_CHECK_GIFTCODE_MARKETING
} from 'modules/home.admin.giftcode.marketing.check/types'
const INITIAL_STATE = {
    data: null
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ON_CHECK_GIFTCODE_MARKETING:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


