import {
    ONSUBMIT_CHANGEPASS
} from 'modules/home.changePassword/types'
import {
    onError as onErrorOldPass,
    onFocus as onFocusOldPass
} from 'modules/home.changePassword.oldpass/actions'
import {
    onError as onErrorNewPass,
    onFocus as onFocusNewPass
} from 'modules/home.changePassword.newpass/actions'
import {
    onError as onErrorConfirmPass,
    onFocus as onFocusConfirmPass
} from 'modules/home.changePassword.confirmpass/actions'
import {
    show as showNotify
} from 'modules/notify/actions'
import {
    hide as hideChangePassword
} from 'modules/home.changePassword/actions'
const onSubmit = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: oldPass } = getState().oldPass || {}
        const { data: newPass } = getState().newPass || {}
        const { data: confirmPass } = getState().confirmPass || {}
        $(`#btnChangePassID`).addClass('isLoading')
        axios.post(`${Config.API_URL}changePassword`, {
                Data: {
                    oldPass: oldPass,
                    newPass: newPass,
                    confirmPass: confirmPass
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: infoChange } = data || {}
                if (success) {
                    //dispatch success
                    dispatch(showNotify('success', infoChange.message))
                    dispatch(hideChangePassword())
                } else {
                    if (infoChange.oldPass) {
                        dispatch(onErrorOldPass(infoChange.oldPass))
                        dispatch(onErrorOldPass())
                    } else if (infoChange.newPass) {
                        dispatch(onErrorNewPass(infoChange.newPass))
                        dispatch(onFocusNewPass())
                    } else if (infoChange.confirmPass) {
                        dispatch(onErrorConfirmPass(infoChange.confirmPass))
                        dispatch(onFocusConfirmPass())
                    }
                }
                $(`#btnChangePassID`).removeClass('isLoading')
            }, (err) => {
                $(`#btnChangePassID`).removeClass('isLoading')
            })
    })
}
module.exports = onSubmit


