import {
    onChange,
    onError,
    onCheck
} from 'modules/home.admin.transfer.history.nickname/actions'
class NicknameView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    render() {
        const { data } = this.props.nicknameTransferHistoryAdmin || {}
        return (
            <div className="form-group-box">
                <label className="control-label">Nickname</label>
				<div className="input">
					<input
						type="text"
						className="form-control"
						placeholder="Nickname nhận ZON"
						value={data}
						onChange={event=>{
							this._onChange(event)
						}}
						 />
				</div>
			</div>
        )
    }
}
const mapStateToProps = ({
    nicknameTransferHistoryAdmin
}) => {
    return {
        nicknameTransferHistoryAdmin
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(NicknameView)



