import {
    ONSUBMIT_ADD_DAILY
} from 'modules/home.admin.agent2.list.add/types'
import {
    hide as hideModal
} from 'modules/home.admin.agent2.list.add/actions'
import {
    show as showNotify
} from 'modules/notify/actions'
import {
    onError as onErrorNickname,
    onFocus as onFocusNickname
} from 'modules/home.admin.agent2.list.add.nickname/actions'
import { getDaily } from 'modules/home.admin.agent2.list/actions'
const onSubmit = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: nicknameAddDaiLy2 } = getState().nicknameAddDaiLy2 || {}
        $(`#btnCreateAgent2ID`).addClass('isLoading')
        axios.post(`${Config.API_URL}agent/createDaily2`, {
                Data: {
                    nickname: nicknameAddDaiLy2
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { data: info, success } = data || {}
                if (success) {
                    dispatch(hideModal())
                    dispatch(showNotify('success', 'Thêm Đại lý 2 thành công'))
                    dispatch(getDaily())
                } else {
                    if (info.nickname) {
                        dispatch(onErrorNickname(info.nickname))
                        dispatch(onFocusNickname())
                    } else {
                        dispatch(showNotify('error', info.message))
                    }
                }
                $(`#btnCreateAgent2ID`).removeClass('isLoading')
            }, (err) => {
                $(`#btnCreateAgent2ID`).removeClass('isLoading')
                reject(err)
            })
    })
}
module.exports = onSubmit


