import {
    ONBLUR_NAME_ADD_DAILY
} from 'modules/home.admin.agent.list.add.name/types'
const onBlur = () => {
    return (dispatch) => {
        $(`#nameAddDaiLyID`).focus()
        dispatch({
            type: ONBLUR_NAME_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onBlur


