import {
    ONCHANGE_NICKNAME_TRANSFER_ADMIN
} from 'modules/home.admin.transfer.nickname/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateNicknameTransferAdmin`).addClass('has-error')
        $(`#validateMessNicknameTransferAdmin`).removeClass('hide')
        $(`#validateMessNicknameTransferAdmin`).text(mess)
        dispatch({
            type: ONCHANGE_NICKNAME_TRANSFER_ADMIN,
            payload: null
        })
    }
}
module.exports = onError


