import {
    ONVALIDATE_LOGIN
} from 'modules/auth/types'
import { onFocus as focusUsername } from 'modules/auth.username/actions'
import { onFocus as focusPassword } from 'modules/auth.password/actions'
const onValidate = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: username } = getState().authUsername || {}
        const { data: password } = getState().authPassword || {}
        if (!username) {
            dispatch(focusUsername())
            reject('username')
        } else if (!password) {
            dispatch(focusPassword())
            reject('password')
        }
        resolve()
    })
}
module.exports = onValidate


