import NicknameView from 'modules/home.userlist.nickname/NicknameView'
import SubmitView from 'modules/home.userlist.submit/SubmitView'
import PaginationView from 'modules/home.userlist.pagination/PaginationView'
import HistoryView from 'modules/home.user.history/HistoryView'
import {
    onSubmit as getData,
    onChangeStatus as onChangeStatusLogin,
    onResetPhone as resetPhone
} from 'modules/home.userlist.submit/actions'
import {
	show as showModal
} from 'modules/home.user.history/actions'
import { getData as getDataUserHistory } from 'modules/home.user.history/actions'
class ListView extends React.Component {
    componentDidMount() {
        this.props.getData()
    }
    _onClick(user){
    	this.props.onChangeStatusLogin(!user.ban_login, user.userID)
    }
    _onClickResetPhone(user){
     this.props.resetPhone(user.userID);
    }
    _onClickHistory(user){
    	this.props.showModal(user)
    	.then(()=>{
    		this.props.getDataUserHistory()
    	})
    }
    render() {
        const { data } = this.props.userlist || {}
        const { show, current } = this.props.userlistPagination || {}
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Danh sách User</h1>
					</div>
				</div>
				<div className="page__body">
					<div className="box margin-top-10">
						<div className="tableFilter">
							<div className="tableFilter__row">
								<div className="tableFilter__column">
								</div>
								<div className="tableFilter__column">
								</div>
								<div className="tableFilter__column">
								</div>
								<div className="tableFilter__column">
									<NicknameView />
								</div>
								<div className="tableFilter__row">
								<div className="tableFilter__column" />
								<div className="tableFilter__column" />
								<div className="tableFilter__column">
									<SubmitView />
								</div>
							</div>
							</div>
						</div>
						<div className="table-container margin-top-10">
							<div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
		                            <table className="table sticky">
		                                <thead>
		                                    <tr>
		                                        <th className="cell-no text-center">UID</th>
		                                        <th>Nickname</th>
		                                        <th>Kích Hoạt OTP</th>
		                                        <th>ZON</th>
		                                        <th>Két Sắt</th>
		                                        <th>Tổng Cược</th>
		                                        <th>Tổng Thắng</th>
		                                        <th>Tổng Thua</th>
		                                        <th>Lãi/Lỗ</th>
		                                        <th>Thời Gian</th>
		                                        <th className="cell-actions">Hành Động</th>
		                                    </tr>
		                                </thead>
		                                <tbody>
		                                	{data.map((item, index)=>{
		                                		return (
		                                			<tr key={`giftcode_agent_${index}`}>
				                                        <td className="cell-no text-center">{item.UID}</td>
				                                        <td className="text-nowrap">{item.name}</td>
				                                        <td className="text-nowrap text-center">
				                                        	<div className="tooltip">
				                                        		<div className="tooltip__header">
				                                        			<span className={`status status--${item.otpFirst ? 'success' : ''}`}>{item.otpFirst ? 'Đã Kích Hoạt' : 'Chưa Kích Hoạt'}</span>
				                                        		</div>
					                                        	<div className="tooltip__body right hide">
					                                        		<div className="tooltip__content"></div>
					                                        	</div>
				                                        	</div>
				                                        </td>
				                                        <td className="text-center">{Helper._formatMoney(item.red)}</td>
				                                        <td className="text-center">{Helper._formatMoney(item.ketSat)}</td>
				                                        <td className="text-center">{Helper._formatMoney(item.redPlay)}</td>
				                                        <td className="text-center">
				                                        	<div className="tooltip">
				                                        		<div className="tooltip__header">
				                                        			<span className={`status status--success`}>
				                                        				{Helper._formatMoney(item.redWin)}
				                                        			</span>
				                                        		</div>
					                                        	<div className="tooltip__body right hide">
					                                        		<div className="tooltip__content"></div>
					                                        	</div>
				                                        	</div>
				                                        </td>
				                                        <td className="text-center">
				                                        	<div className="tooltip">
				                                        		<div className="tooltip__header">
				                                        			<span className={`status status--renote`}>
				                                        				{Helper._formatMoney(item.redLost)}
				                                        			</span>
				                                        		</div>
					                                        	<div className="tooltip__body right hide">
					                                        		<div className="tooltip__content"></div>
					                                        	</div>
				                                        	</div>
				                                        </td>
				                                        <td className="text-center">
				                                        	<div className="tooltip">
				                                        		<div className="tooltip__header">
				                                        			<span className={`status status--${(item.redWin - item.redLost) >= 0 ? 'success' : 'renote'}`}>
				                                        				{Helper._formatMoney(item.redWin - item.redLost)}
				                                        			</span>
				                                        		</div>
					                                        	<div className="tooltip__body right hide">
					                                        		<div className="tooltip__content"></div>
					                                        	</div>
				                                        	</div>
				                                        </td>
				                                        <td className="cell-datetime text-center">{moment(item.joinedOn).format("DD/MM/YYYY HH:mm:ss")}</td>
				                                        <td className="cell-actions text-center">
				                                        	<div className="dropdown">
			                                            		<div className="dropdown__header btnWrapper" id="btnBlockAccountID">
			                                            			<a
			                                            			onClick={event=>{
			                                            				this._onClick(item)
			                                            			}}
			                                            			className={`btn btn--sm btn--${item && item.ban_login ? 'warning' : 'info'}`}>
			                                                			<i className={`${item && item.ban_login ? 'ti-key' : 'ti-unlock'}`}></i> Khoá
			                                                		</a>
			                                                		<a className="btn btn--sm btn--primary margin-left-5" onClick={event=>{
			                                                			this._onClickHistory(item)
			                                                		}}>
			                                                			<i className="ti-reload" />
			                                                		 Lịch Sử
			                                                		</a>
                                                     <a className="btn btn--sm btn--primary margin-left-5" onClick={event=>{
			                                                			this._onClickResetPhone(item)
			                                                		}}>
			                                                			<i className="ti-reload" />
			                                                		 Reset Phone
			                                                		</a>
			                                            		</div>
			                                            	</div>
				                                        </td>
				                                    </tr>
		                                			)
		                                	})}
		                                </tbody>
		                            </table>
		                         </div>
		                    </div>
                            <PaginationView />
                        </div>
					</div>
				</div>
				<HistoryView />
			</div>
        )
    }
}
const mapStateToProps = ({
    userlist,
    userlistPagination
}) => {
    return {
        userlist,
        userlistPagination
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData,
        onChangeStatusLogin,
        resetPhone,
        showModal,
        getDataUserHistory
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(ListView)



