import {
    INIT_HEADER
} from 'modules/home.header/types'
const init = () => {
    return (dispatch) => {
        let data = Helper._getCookie('userInfo')
        try {
            data = JSON.parse(data)
        } catch (err) {
            data = {}
        }
        dispatch({
            type: INIT_HEADER,
            payload: {
                data: data
            }
        })
    }
}
module.exports = init


