import {
    ONCHANGE_NGAYTHANG_GIFTCODE_MARKETING
} from 'modules/home.admin.giftcode.marketing.add.ngaythang/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateMessNgayThangGiftCodeMarketingAdd`).removeClass('has-error')
        dispatch ({
            type: ONCHANGE_NGAYTHANG_GIFTCODE_MARKETING,
            payload: {
            	data: val
            }
        })
    }
}
module.exports = onChange



