import {
    onChange
} from 'modules/home.admin.agent.profit.to/actions'
class ToView extends React.Component {
    componentDidMount() {
        this.initDatePicker()
    }
    initDatePicker() {
        const { data } = this.props.filterAgentProfitTo || {}
        const datePicker = $(`.datepicker_agent_profit_to`).datepicker({
            dateFormat: 'dd/mm/yy',
            onSelect: function() {
                let date = $(`.datepicker_agent_profit_to`).datepicker('getDate')
                date = moment(date).format('YYYY-MM-DD 23:59:59 Z')
                this.props.onChange(date)
            }.bind(this)
        })
        $(datePicker).datepicker('setDate', moment(data, 'YYYY-MM-DD 23:59:59 Z').toDate())
    }
    render() {
        const { data } = this.props.filterAgentProfitTo || {}
        return (
            <div className="form-group-box">
                <label className="control-label">Đến ngày</label>
                <div className="input input--icon">
                    <input type="text" className="form-control datepicker_agent_profit_to" placeholder="DD/MM/YYYY" />
                    <span className="icon"><i className="ti-calendar"></i></span>
                </div>
            </div>
        )
    }
}
const mapStateToProps = ({
    filterAgentProfitTo
}) => {
    return {
        filterAgentProfitTo
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(ToView)


