import { ONVALIDATE_REPORT_TRANSACTION_AGENT } from 'modules/home.admin.transfer.agent.history.report/types'
import {
    onError as onErrorDesc
} from 'modules/home.admin.transfer.agent.history.report.desc/actions'
const onValidate = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: desc } = getState().descReportTransferDaiLy || {}
        if (!desc) {
            dispatch(onErrorDesc('Vui lòng nhập lí do'))
            reject()
        } else {
            resolve()
        }
    })
}
module.exports = onValidate


