import {
    ON_DELETE_ADMIN_GIFTCODE_MARKETING_LIST
} from 'modules/home.admin.giftcode.marketing.list/types'
import { show as showAlert } from 'modules/notify/actions'
const onDelete = (code) => {
    console.log(code);
    return (dispatch, getState) => new Promise((resolve, reject) => {
     axios.get(`${Config.API_URL}admin/marketing/giftcodeDelete`, {
      params: {
           code: code
      }
         })
         .then((response) => {
             const { data } = response || {}
             const { success, data: info } = data || {}
             if (success) {
                 dispatch(showAlert('success', 'Xoá Gift Code Thành Công'))
                 window.location.reload();
             } else {
                 dispatch(showAlert('error', 'Hệ thống bảo trì. \nVui lòng thử lại sau.'))
             }
             resolve(response)
         }, (err) => {
             reject(err)
         })
    })
}
module.exports = onDelete



