import {
    onChange,
    onError
} from 'modules/home.admin.transfer.agent.desc/actions'
class DescView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    render() {
        const { data } = this.props.descTransferDaiLy || {}
        return (
            <div className="form-group-box margin-top-10" id="validateDescTransferDaiLy">
            <label className="control-label">Lý do</label>
				<div className="input">
					<input 
						type="text"
						className="form-control" 
						placeholder="Lý do"  
						value={data}
						onChange={event=>{
							this._onChange(event)
						}}
						 />
				</div>
                <div className="help-block hide" id="validateMessDescTransferDaiLy"></div>
			</div>
        )
    }
}
const mapStateToProps = ({
    descTransferDaiLy
}) => {
    return {
        descTransferDaiLy
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        onError
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(DescView)


