import {
    ONERROR_LOCATION_ADD_DAILY
} from 'modules/home.admin.agent.list.add.location/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateLocationAddDaiLyID`).addClass('has-error')
        $(`#validateLocationMessAddDaiLyID`).removeClass('hide')
        $(`#validateLocationMessAddDaiLyID`).text(mess)
        dispatch({
            type: ONERROR_LOCATION_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onError


