const onSubmit = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: fromDate } = getState().filterAdminGiftcodeMarketingListFrom || {}
        const { data: toDate } = getState().filterAdminGiftcodeMarketingListTo || {}
        const { data: menhgia } = getState().menhgiaGiftCodeMarketingList || {}
        const { data: trangthai } = getState().trangthaiGiftCodeMarketingListFilter || {}
        window.open(encodeURI(`${Config.API_URL}admin/marketing/giftcodeExcel?fromDate=${fromDate}&toDate=${toDate}&menhgia=${menhgia}&trangthai=${trangthai}`));
    })
}
export default onSubmit


