import {
    ONCHANGE_FROMDATE_ADMIN_AGENT_GIFTCODE_LIST
} from 'modules/home.admin.giftcode.agent.list.from/types'
const onChange = (val) => {
    return (dispatch) => {
        dispatch({
            type: ONCHANGE_FROMDATE_ADMIN_AGENT_GIFTCODE_LIST,
            payload: {
                data: val
            }
        })
    }
}

module.exports = onChange


