import {
    ONFOCUS_CONFIRM_CHANGE_PASSWORD
} from 'modules/home.changePassword.confirmpass/types'
const onFocus = () => {
    return (dispatch) => {
        $(`#confirmPassID`).focus()
        dispatch({
            type: ONFOCUS_CONFIRM_CHANGE_PASSWORD,
            payload: null
        })
    }
}
module.exports = onFocus


