import {
    ONCHANGE_NICKNAME_BIGBABOL_HISTORY_ADMIN
} from 'modules/home.games.history.bigbabol.nickname/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_NICKNAME_BIGBABOL_HISTORY_ADMIN:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


