import {
    ON_RESET_PHONE_USERLIST
} from 'modules/home.userlist.submit/types'
import {
    show as showAlert
} from 'modules/notify/actions'
const onResetPhone = (id) => {
    return (dispatch) => new Promise((resolve, reject) => {
    	$(`#btnBlockAccountID`).addClass('isLoading')
        axios.get(`${Config.API_URL}admin/user/resetPhone`, {
                params: {
                    id: id
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info } = data || {}
                if (success) {
                    dispatch(showAlert('success', 'Reset phone thành công'))
                } else {
                    dispatch(showAlert('error', 'Hệ thống bảo trì. \nVui lòng thử lại sau.'))
                }
                $(`#btnBlockAccountID`).removeClass('isLoading')
                resolve(response)
            }, (err) => {
            	$(`#btnBlockAccountID`).removeClass('isLoading')
                reject(err)
            })
    })
}
module.exports = onResetPhone



