import {
    ONBLUR_USERNAME_LOGIN
} from 'modules/auth.username/types'
const onBlur = () => {
    return (dispatch) => {
        $(`#userID`).blur()
        dispatch({
            type: ONBLUR_USERNAME_LOGIN,
            payload: null
        })
    }
}
module.exports = onBlur


