import {
    GET_BALANCE
} from 'modules/home.balance/types'
const INITIAL_STATE = {
    data: 0
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case GET_BALANCE:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


