import {
    ON_GETDATA_ADMIN_GIFTCODE_MARKETING_LIST
} from 'modules/home.admin.giftcode.marketing.list/types'
import {
    onChange
} from 'modules/home.admin.giftcode.marketing.list.pagination/actions'
import {
    CHANGE_CURRENT_PAGE_ADMIN_GIFTCODE_MARKETING_LIST
} from 'modules/home.admin.giftcode.marketing.list.pagination/types'
const onSubmit = (isFirst) => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: fromDate } = getState().filterAdminGiftcodeMarketingListFrom || {}
        const { data: toDate } = getState().filterAdminGiftcodeMarketingListTo || {}
        const { data: menhgia } = getState().menhgiaGiftCodeMarketingList || {}
        const { data: trangthai } = getState().trangthaiGiftCodeMarketingListFilter || {}
        const { show, current } = getState().giftcodeMarketingPagination || {}
        $('.loading').addClass('active')
        axios.post(`${Config.API_URL}admin/marketing/giftcodeList`, {
                Data: {
                    fromDate: fromDate ? moment(fromDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 00:00:00 Z') : null,
                    toDate: toDate ? moment(toDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 23:59:59 Z') : null,
                    menhgia: menhgia,
                    trangthai: trangthai,
                    limit: show,
                    offset: (current - 1) * show
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info, totals } = data || {}
                if (success) {
                    dispatch({
                        type: ON_GETDATA_ADMIN_GIFTCODE_MARKETING_LIST,
                        payload: {
                            data: info
                        }
                    })
                    dispatch({
                        type: CHANGE_CURRENT_PAGE_ADMIN_GIFTCODE_MARKETING_LIST,
                        payload: {
                            show: show,
                            current: current,
                            totals: totals
                        }
                    })
                }
                $('.loading').removeClass('active')
                resolve()
            }, (err) => {
                $('.loading').removeClass('active')
                reject(err)
            })
    })
}
export default onSubmit


