import {
    HIDE_MODAL_FREEZE_TRANSACTION_AGENT
} from 'modules/home.admin.transfer.agent.history.freeze/types'
const hide = () => {
    return (dispatch) => {
        $(`#modalFreezeTransferAgentID`).removeClass('active')
        dispatch({
            type: HIDE_MODAL_FREEZE_TRANSACTION_AGENT,
            payload: null
        })
    }
}
module.exports = hide


