import HeaderView from 'modules/home.header/HeaderView'
import ChangePassView from 'modules/home.changePassword/ChangePassView'
import NotifyView from 'modules/notify/NotifyView'
import HeaderLeftView from 'modules/home.headerLeft/HeaderLeftView'
import MenuView from 'modules/home.menu/MenuView'
class HomeMainView extends React.Component {
	render(){
		return (
			<div className="layout">
				<div className="wrapper">
					<HeaderView />
					<main className="">
						<div className="container">
							<div className="page">
								<div className="page__container">
									<div className="page__aside">
										<aside className="sidebar">
											<HeaderLeftView />
											<MenuView />
										</aside>
									</div>
										{this.props.children}
									</div>
								</div>
							</div>
					</main>
				</div>
				<ChangePassView />
				<NotifyView />
			</div>
			)
	}
}
export default HomeMainView


