import MenhGiaView from 'modules/home.admin.giftcode.agent.add.menhgia/MenhGiaView'
import SoLuongView from 'modules/home.admin.giftcode.agent.add.soluong/SoLuongView'
import NgayThangView from 'modules/home.admin.giftcode.agent.add.ngaythang/NgayThangView'
import DaiLyView from 'modules/home.admin.giftcode.agent.add.daily/DaiLyView'
import {
	onSubmit,
	onValidate
} from 'modules/home.admin.giftcode.agent.add/actions'
class AddView extends React.Component {
	_onSubmit(){
		this.props.onValidate()
		.then(()=>{
			this.props.onSubmit()
		},(err)=>{

		})
	}
	render(){
		return (
			<div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Xuất Giftcode</h1>
					</div>
				</div>
				<div className="page__body">
					<form className="form form--transfer">
						<div className="form__body">
						   <MenhGiaView />
						   <SoLuongView />
									<NgayThangView />
						   <DaiLyView />
						</div>
						<div className="form__footer">
								<div className="btnWrapper" id="btnAddGiftCodeDaiLyID">
									<a
										className="btn btn--block btn--primary"
										onClick={event=>{
											this._onSubmit()
										}} >
										Xuất Giftcode
									</a>
								</div>
							</div>
					</form>
				</div>
			</div>
			)
	}
}
const mapDispatchToProps = (dispatch)=>{
	return Redux.bindActionCreators({
		onSubmit,
		onValidate
	}, dispatch)
}
export default ReactRedux.connect(null, mapDispatchToProps)(AddView)



