import {
    onChange
}
from 'modules/home.changePassword.newpass/actions'
import {
    onFocus as focusConfirmPass
} from 'modules/home.changePassword.confirmpass/actions'
class NewPassView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    _onKeyDown(event) {
        const { keyCode } = event || {}
        switch (keyCode) {
            case 13:
                this.props.focusConfirmPass()
                break
            default:
                break
        }
    }
    render() {
        const { data } = this.props.newPass || {}
        return (
            <div className="form-group-box" id="validateNewPassID">
				<label className="control-label">Mật khẩu mới</label>
				<div className="input">
					<input 
						onChange={event=>{
							this._onChange(event)
						}}
						onKeyDown={event=>{
							this._onKeyDown(event)
						}}
                        maxLength="12"
						value={data}
						type="password" 
						id="newPassID" 
						className="form-control" 
						placeholder="6 - 12 Ký tự" />
				</div>
                <p className="help-block hide" id="validateNewPassMessID"></p>
			</div>
        )
    }
}
const mapStateToProps = ({
    newPass
}) => {
    return {
        newPass
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        focusConfirmPass
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(NewPassView)


