import {
    ONERROR_VALUE_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent.value/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateValueTransferDaiLy`).addClass('has-error')
        $(`#validateMessValueTransferDaiLy`).removeClass('hide')
        $(`#validateMessValueTransferDaiLy`).text(mess)
        dispatch({
            type: ONERROR_VALUE_TRANSFER_AGENT,
            payload: null
        })
    }
}
module.exports = onError


