import {
	ONLOADED_BTN_LOGIN
} from 'modules/auth.btn/types'
const onLoaded = ()=>{
	return (dispatch)=>{
		$(`#btnLoginID`).removeClass("btn--loading")
		dispatch({
			type: ONLOADED_BTN_LOGIN,
			payload: null
		})
	}
}
module.exports = onLoaded


