import {
    ONCHANGE_NICKNAME_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent.nickname/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateNicknameTransferDaiLy`).removeClass('has-error')
        $(`#validateNicknameTransferDaiLy`).removeClass('has-success')
        $(`#validateMessNicknameTransferDaiLy`).addClass('hide')
        dispatch ({
            type: ONCHANGE_NICKNAME_TRANSFER_AGENT,
            payload: {
            	data: val
            }
        })
    }
}
module.exports = onChange


