import {
    ONERROR_SOLUONG_GIFTCODE_MARKETING
} from 'modules/home.admin.giftcode.marketing.add.soluong/types'
const onError = () => {
    return (dispatch) => {
        $(`#validateMessSoLuongGiftCodeMarketingAdd`).addClass('has-error')
        dispatch({
            type: ONERROR_SOLUONG_GIFTCODE_MARKETING,
            payload: null
        })
    }
}
module.exports = onError


