import {
    GET_HISTORY_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent.history/types'
import {
    onChange as changePagination
} from 'modules/home.admin.transfer.agent.history.pagination/actions'
const getData = (isFirst) => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { show, current } = getState().agentTransferHistoryPagination || {}
        $('.loading').addClass('active')
        axios.post(`${Config.API_URL}agent/transferHistory`, {
                Data: {
                    limit: show,
                    offset: (current - 1) * show
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info, totals } = data || {}
                if (success) {
                    dispatch({
                        type: GET_HISTORY_TRANSFER_AGENT,
                        payload: {
                            data: info
                        }
                    })
                    if (isFirst) {
                        dispatch(changePagination({
                            show: show,
                            current: current,
                            totals: totals
                        }))
                    }
                }
                $('.loading').removeClass('active')
            }, (err) => {
                $('.loading').removeClass('active')
                reject(err)
            })
    })
}
module.exports = getData


