import DaiLyView from 'modules/home.admin.giftcode.agent.list.daily/DaiLyView'
import FromView from 'modules/home.admin.giftcode.agent.list.from/FromView'
import ToView from 'modules/home.admin.giftcode.agent.list.to/ToView'
import SubmitView from 'modules/home.admin.giftcode.agent.list.submit/SubmitView'
import PaginationView from 'modules/home.admin.giftcode.agent.list.pagination/PaginationView'
import {
    onSubmit as getData
} from 'modules/home.admin.giftcode.agent.list.submit/actions'
import {
    onDelete as onDelete
} from 'modules/home.admin.giftcode.marketing.list/actions'
class ListView extends React.Component {
    componentDidMount() {
        this.props.getData()
    }
    _onDeleteCode(item){
     this.props.onDelete(item.code);
    }
    render() {
        const { data } = this.props.giftcodeListAdminAgent || {}
        const { show, current } = this.props.adminAgentGiftcodeListPagination || {}
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Danh sách Giftcode</h1>
					</div>
				</div>
				<div className="page__body">
					<div className="box margin-top-10">
						<div className="tableFilter">
							<div className="tableFilter__row">
								<div className="tableFilter__column">
								</div>
								<div className="tableFilter__column">
									<DaiLyView />
								</div>
								<div className="tableFilter__column">
									<FromView />
								</div>
								<div className="tableFilter__column">
									<ToView />
								</div>
								<div className="tableFilter__row">
								<div className="tableFilter__column" />
								<div className="tableFilter__column" />
								<div className="tableFilter__column">
									<SubmitView />
								</div>
							</div>
							</div>
						</div>
						<div className="table-container margin-top-10">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                                    <table className="table sticky">
                                        <thead>
                                            <tr>
                                                <th className="cell-no text-center">STT</th>
                                                <th>Đại lý</th>
                                                <th>Mã code</th>
                                                <th>Mệnh giá</th>
                                                <th>Ngày xuất</th>
                                                <th>Ngày hết hạn</th>
                                                <th>Tình trạng</th>
                                                <th>Thao tác</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                        	{data.map((item, index)=>{
                                        		return (
                                        			<tr key={`giftcode_agent_${item.code}`}>
        		                                        <td className="cell-no text-center">{(current-1)*show + index + 1}</td>
        		                                        <td className="text-nowrap text-center">{item.forAgent}</td>
        		                                        <td className="text-nowrap text-center">{item.code}</td>
        		                                        <td className="text-nowrap text-center">{Helper._formatMoney(item.red)}</td>
        		                                        <td className="text-center">{moment(item.date).format("DD/MM/YYYY HH:mm:ss")}</td>
        		                                        <td className="cell-datetime text-center">{moment(item.todate).format("DD/MM/YYYY HH:mm:ss")}</td>
        		                                        <td className="cell-datetime text-center">{item.uid ? 'Đã sử dụng' : 'Chưa sử dụng'}</td>
                                                  <td className="cell-actions text-center"><a className="btn btn--sm btn--primary margin-left-5" onClick={event=>{
                                                   this._onDeleteCode(item)
                                                  }}>
                                                   <i className="ti-reload" />
                                                   Xoá
                                                  </a></td>
                                           </tr>
                                        			)
                                        	})}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            <PaginationView />
                        </div>
					</div>
				</div>
			</div>
        )
    }
}
const mapStateToProps = ({
    giftcodeListAdminAgent,
    adminAgentGiftcodeListPagination
}) => {
    return {
        giftcodeListAdminAgent,
        adminAgentGiftcodeListPagination
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData,
        onDelete
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(ListView)



