import CodeView from 'modules/home.admin.giftcode.marketing.check.code/CodeView'
import SubmitView from 'modules/home.admin.giftcode.marketing.check.submit/SubmitView'
class CheckView extends React.Component {
    render() {
        const { data } = this.props.giftcodeCheck || {}
        return (
            <div className="page__content">
                <div className="page__header">
                    <div className="title">
                        <h1 className="pageTitle">Check Giftcode</h1>
                    </div>
                </div>
                <div className="page__body">
                    <div className="box margin-top-10">
                        <div className="tableFilter">
                            <div className="tableFilter__row">
                                <div className="tableFilter__column">
                                    <CodeView />
                                </div>
                                <div className="tableFilter__column">
                                    <SubmitView />
                                </div>
                                <div className="tableFilter__column">
                                </div>
                                <div className="tableFilter__column">
                                    
                                </div>
                                <div className="tableFilter__row">
                                <div className="tableFilter__column" />
                                <div className="tableFilter__column" />
                                <div className="tableFilter__column">
                                    
                                </div>
                            </div>
                            </div>
                        </div>
                        <div className="table-container margin-top-10">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                                    <table className="table sticky">
                                        <thead>
                                            <tr>
                                                <th>Mã code</th>
                                                <th>Mệnh giá</th>
                                                <th>Ngày xuất</th>
                                                <th>Ngày hết hạn</th>
                                                <th>Tình trạng</th>
                                                <th>Nickname sử dụng</th>
                                                <th>Time sử dụng</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {data ? (
                                                <tr>
                                                        <td className="text-nowrap text-center">{data.code}</td>
                                                        <td className="text-nowrap text-center">{Helper._formatMoney(data.red)}</td>
                                                        <td className="text-nowrap text-center">{moment(data.date).format("DD/MM/YYYY HH:mm:ss")}</td>
                                                        <td className="text-center">{moment(data.todate).format("DD/MM/YYYY HH:mm:ss")}</td>
                                                        <td className="cell-datetime text-center">{data.uid ? 'Đã sử dụng' : 'Chưa sử dụng'}</td>
                                                        <td className="cell-datetime text-center">{data.nicknameUse}</td>
                                                        <td className="cell-datetime text-center">{moment(data.timeUse).format("DD/MM/YYYY HH:mm:ss")}</td>
                                                    </tr>
                                                ) : (
                                                <tr>
                                                        <td className="text-nowrap text-center" colSpan="7">Không tìm thấy Giftcode</td>
                                                    </tr>
                                                )}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = ({
    giftcodeCheck

}) => {
    return {
        giftcodeCheck
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, null)(CheckView)


