import {
    ONCHANGE_DAILY_PROFIT_AGENTS
} from 'modules/home.admin.agents.profit.daily/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateMessAgentsProfitDaiLy`).removeClass('has-error')
        dispatch({
            type: ONCHANGE_DAILY_PROFIT_AGENTS,
            payload: {
                data: val
            }
        })
    }
}
module.exports = onChange


