import {
    ONERROR_SOLUONG_GIFTCODE_MARKETING_LIST
} from 'modules/home.admin.giftcode.marketing.list.soluong/types'
const onError = () => {
    return (dispatch) => {
        $(`#validateMessSoLuongGiftCodeMarketingList`).addClass('has-error')
        dispatch({
            type: ONERROR_SOLUONG_GIFTCODE_MARKETING_LIST,
            payload: null
        })
    }
}
module.exports = onError


