import {
    INIT_DAILY_GIFTCODE_DAILY_ADD
} from 'modules/home.admin.giftcode.agent.list.daily/types'
import {
    getDaily
} from 'modules/home.admin.agent.list/actions'
const init = () => {
    return (dispatch) => new Promise((resolve, reject) => {
        dispatch(getDaily())
            .then((response) => {
                resolve(resolve)
                dispatch({
                    type: INIT_DAILY_GIFTCODE_DAILY_ADD,
                    payload: null
                })
            })
    })
}
module.exports = init


