import {
    onChange
} from 'modules/home.admin.giftcode.marketing.list.pagination/actions'
import {
    onSubmit as getData
} from 'modules/home.admin.giftcode.marketing.list.submit/actions'
class PaginationView extends React.Component {
    _onChangeCurrent(event) {
        const { show, totals } = this.props.giftcodeMarketingPagination || {}
        this.props.onChange({
                show: show,
                totals: totals,
                current: event
            })
            .then(() => {
                this.props.getData()
            })
    }
    render() {
        const { show, totals, current } = this.props.giftcodeMarketingPagination || {}
        return (
            <Pagination 
            current={current} 
            total={totals} 
            show={show} 
            onChangeCurrent={event=>{
                this._onChangeCurrent(event)
            }}/>
        )
    }
}
const mapStateToProps = ({ giftcodeMarketingPagination }) => {
    return {
        giftcodeMarketingPagination
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        getData
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(PaginationView)


