import {
    ONERROR_DAILY_PROFIT_AGENTS
} from 'modules/home.admin.agents.profit.daily/types'
const onError = (dispatch) => {
    return (dispatch) => {
        $(`#validateMessAgentsProfitDaiLy`).addClass('has-error')
        dispatch({
            type: ONERROR_DAILY_PROFIT_AGENTS,
            payload: null
        })
    }
}
module.exports = onError


