import {
    ONCHANGE_VALUE_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent.value/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_VALUE_TRANSFER_AGENT:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


