import {
    onChange
} from 'modules/home.admin.agents.transfer.history.daily/actions'
import {
    getDaily
} from 'modules/home.admin.agent.list/actions'
class DaiLyView extends React.Component {
    componentDidMount() {
        this.props.getDaily()
    }
    _onChange(event){
    	this.props.onChange(event.target.value)
    }
    render() {
        const { data } = this.props.daily || {}
        const { data: active } = this.props.dailyTransferHistoryAgents || {}
        return (
            <div className="form-group-box" id="validateMessAgentsTransferHistoryDaiLy">
            <label className="control-label">Đại lý</label>
				<div className="select">
					<select 
						className="form-control" 
						value={active}
						selected={active}
						onChange={event=>{
							this._onChange(event)
						}}>
						<option value="">Chọn Đại lý</option>
						{
							data.map((item, index)=>{
								return (
									<option key={`daily_${index}`} value={item.nickname}>{item.nickname}</option>
									)
							})
						}
					</select>
				</div>
			</div>
        )
    }
}
const mapStateToProps = ({
    daily,
    dailyTransferHistoryAgents
}) => {
    return {
        daily,
        dailyTransferHistoryAgents
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        getDaily
    }, dispatch)
}

export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(DaiLyView)


