import {
    ONSUBMIT_ADMIN_ADD_GIFTCODE_AGENT
} from 'modules/home.admin.transfer.agent.history.report/types'
import { show as showAlert } from 'modules/notify/actions'
import {
    hide as hideModal
} from 'modules/home.admin.transfer.agent.history.report/actions'
import {
    getData
} from 'modules/home.admin.transfer.agent.history/actions'
const onSubmit = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: desc } = getState().descReportTransferDaiLy || {}
        const { id } = getState().modalReportTransferAgent || {}
        $(`#btnReportTransferAgentID`).addClass('isLoading')
        axios.post(`${Config.API_URL}agent/reportTransferHistory`, {
                Data: {
                    desc: desc,
                    id: id
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { data: info, success } = data || {}
                if (success) {
                    dispatch(showAlert('success', info.message))
                    dispatch(hideModal())
                    dispatch(getData())
                } else {
                    dispatch(showAlert('error', info.message))
                }
                $(`#btnReportTransferAgentID`).removeClass('isLoading')
            }, (err) => {
                reject(err)
                $(`#btnReportTransferAgentID`).removeClass('isLoading')
            })
    })
}
module.exports = onSubmit


