import {
    ONCHANGE_FROMDATE_AGENTS_TRANSFER_HISTORY
} from 'modules/home.admin.agents.transfer.history.from/types'
const onChange = (val) => {
    return (dispatch) => {
        dispatch({
            type: ONCHANGE_FROMDATE_AGENTS_TRANSFER_HISTORY,
            payload: {
                data: val
            }
        })
    }
}

module.exports = onChange


