import {
    ONERROR_TYPE_HISTORY_USER_FILTER
} from 'modules/home.user.history.type/types'
const onError = () => {
    return (dispatch) => {
        $(`#validateTypeHistoryUserFilter`)
            .addClass('has-error')
        dispatch({
            type: ONERROR_TYPE_HISTORY_USER_FILTER,
            payload: null
        })
    }
}
module.exports = onError


