import {
    GET_BALANCE
} from 'modules/home.balance/types'
const getData = () => {
    return (dispatch) => new Promise((resolve, reject) => {
        axios.get(`${Config.API_URL}getBalance`)
            .then((response) => {
                const { data } = response || {}
                const { data: balance, success } = data || {}
                if (success) {
                    dispatch({
                        type: GET_BALANCE,
                        payload: {
                            data: balance
                        }
                    })
                }
            }, (err) => {
                reject(err)
            })
    })
}
module.exports = getData


