import {
    ONERROR_DESC_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent.desc/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateDescTransferDaiLy`).addClass('has-error')
        $(`#validateMessDescTransferDaiLy`).removeClass('hide')
        $(`#validateMessDescTransferDaiLy`).text(mess)
        dispatch({
            type: ONERROR_DESC_TRANSFER_AGENT,
            payload: null
        })
    }
}
module.exports = onError


