import FromView from 'modules/home.admin.agent.profit.from/FromView'
import ToView from 'modules/home.admin.agent.profit.to/ToView'
import SubmitView from 'modules/home.admin.agent.profit.submit/SubmitView'
import {
    onSubmit
} from 'modules/home.admin.agent.profit.submit/actions'
class ProfitView extends React.Component {
    componentDidMount() {
        this.props.onSubmit()
    }
    render() {
        const { data } = this.props.profitAgent || {}
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Doanh thu của tôi</h1>
					</div>
				</div>
				<div className="page__body">
					<div className="box margin-top-10">
					<div className="box margin-top-10">
                        <div className="tableFilter">
                            <div className="tableFilter__row">
                                <div className="tableFilter__column">
                                    <FromView />
                                </div>
                                <div className="tableFilter__column">
                                    <ToView />
                                </div>
                                <div className="tableFilter__column">
                                    <SubmitView />
                                </div>
                            </div>
                        </div>
					</div>
						<div className="table-container margin-top-10">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                                    <table className="table sticky">
                                        <thead>
                                            <tr>
                                                <th>Doanh số Bán</th>
                                                <th>Doanh số Mua</th>
                                                <th>Doanh số Liên minh</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                        	<tr>
                                                <td className={`text-center ${data.totalFrom>=0? 'text-positive' : 'text-negative' }`}>
                                                    {Helper._formatMoney(data.totalFrom)}
                                                </td>
                                                <td className={`text-center ${data.totalTo>=0? 'text-positive' : 'text-negative' }`}>{Helper._formatMoney(data.totalTo)}</td>
                                                <td className={`text-center ${(data.totalFrom + data.totalTo)>=0? 'text-positive' : 'text-negative' }`}>{Helper._formatMoney(data.totalFrom + data.totalTo)}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
					</div>
				</div>
			</div>
        )
    }
}
const mapStateToProps = ({
    profitAgent
}) => {
    return {
        profitAgent
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onSubmit
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(ProfitView)


