import {
    onChange
} from 'modules/home.games.history.tamhung.pagination/actions'
import {
    getData
} from 'modules/home.games.history.tamhung/actions'
class PaginationView extends React.Component {
    _onChangeCurrent(event) {
        const { show, totals } = this.props.tamhungHistoryPagination || {}
        this.props.onChange({
                show: show,
                totals: totals,
                current: event
            })
            .then(() => {
                this.props.getData()
            })
    }
    render() {
        const { show, totals, current } = this.props.tamhungHistoryPagination || {}
        return (
            <Pagination
            current={current}
            total={totals}
            show={show}
            onChangeCurrent={event=>{
                this._onChangeCurrent(event)
            }}/>
        )
    }
}
const mapStateToProps = ({ tamhungHistoryPagination }) => {
    return {
        tamhungHistoryPagination
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        getData
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(PaginationView)



