import {
    ONCHANGE_DAILY_PROFIT_AGENTS
} from 'modules/home.admin.agents.profit.daily/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_DAILY_PROFIT_AGENTS:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


