import {
    onChange
}
from 'modules/home.admin.agent2.list.add.nickname/actions'
import {
    onValidate,
    onSubmit
} from 'modules/home.admin.agent2.list.add/actions'
class NickNameView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    _onKeyDown(event) {
        const { keyCode } = event || {}
        switch (keyCode) {
            case 13:
                this.props.onValidate()
                    .then(() => {
                        this.props.onSubmit()
                    })
                break
            default:
                break
        }
    }
    render() {
        const { data } = this.props.nicknameAddDaiLy2 || {}
        return (
            <div className="form-group-box" id="validateNicknameAddDaiLy2ID">
                <label className="control-label">Nickname</label>
                <div className="input">
                    <input 
                        onChange={event=>{
                            this._onChange(event)
                        }}
                        onKeyDown={event=>{
                            this._onKeyDown(event)
                        }}
                        value={data}
                        type="text" 
                        id="nicknameAddDaiLy2ID" 
                        className="form-control" 
                        placeholder="Nickname" />
                </div>
                <p className="help-block hide" id="validateNicknameMessAddDaiLy2ID"></p>
            </div>
        )
    }
}
const mapStateToProps = ({
    nicknameAddDaiLy2
}) => {
    return {
        nicknameAddDaiLy2
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        onValidate,
        onSubmit
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(NickNameView)


