import {
    HIDE_MODAL_ADD_DAILY2
} from 'modules/home.admin.agent2.list.add/types'
const hide = () => {
    return (dispatch) => {
        $(`#modalAddAgent2ID`).removeClass('active')
        dispatch({
            type: HIDE_MODAL_ADD_DAILY2,
            payload: null
        })
    }
}
module.exports = hide


