import {
    getData
} from 'modules/home.games.history.xocdia/actions'
import PaginationView from 'modules/home.games.history.xocdia.pagination/PaginationView'
import NicknameView from 'modules/home.games.history.xocdia.nickname/NicknameView'
import FromView from 'modules/home.games.history.xocdia.from/FromView'
import ToView from 'modules/home.games.history.xocdia.to/ToView'
import SubmitView from 'modules/home.games.history.xocdia.submit/SubmitView'
class HistoryView extends React.Component {
    componentDidMount() {
        this.props.getData()
    }
    render() {
        const { data } = this.props.xocdiaHistory || {}
        const { show, current } = this.props.xocdiaHistoryPagination || {}
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Lịch sử Xóc Đĩa</h1>
					</div>
				</div>
				<div className="page__body">
					<div className="box margin-top-10">
                        <div className="tableFilter">
                            <div className="tableFilter__row">
                                    <div className="tableFilter__column">
                                    </div>
                                    <div className="tableFilter__column">
                                        <NicknameView />
                                    </div>
                                    <div className="tableFilter__column">
                                        <FromView />
                                    </div>
                                    <div className="tableFilter__column">
                                        <ToView />
                                    </div>
                                    <div className="tableFilter__row">
                                    <div className="tableFilter__column" />
                                    <div className="tableFilter__column" />
                                    <div className="tableFilter__column">
                                        <SubmitView />
                                    </div>
                                </div>
                            </div>
                        </div>
						<div className="table-container margin-top-10">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                                <table className="table sticky">
                                    <thead>
                                        <tr>
                                            <th className="cell-no">Phiên</th>
                                            <th>Name</th>
                                            <th>Cược Chẵn</th>
                                            <th>Cược Lẻ</th>
                                            <th>Cược 3 Đỏ</th>
                                            <th>Cược 4 Đỏ</th>
                                            <th>Cược 3 Trắng</th>
                                            <th>Cược 4 Trắng</th>
                                            <th>ZON Nhận</th>
                                            <th>Thời Gian</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                    	{data.map((item, index)=>{
                                            let totalBet = item.chan + item.le + item.red3 + item.red4 + item.white3 + item.white4
                                    		return (
                                    			<tr key={`history_${item._id}`}>
    		                                        <td className="cell-no text-center">{item.phien}</td>
                                                    <td className="cell-nowrap text-center">{item.name}</td>
    		                                        <td className="text-nowrap text-center">
    		                                        	{Helper._formatMoney(item.chan)}
    		                                        </td>
    		                                        <td className="text-center">{Helper._formatMoney(item.le)}</td>
    		                                        <td className={`cell-datetime text-center`}>{Helper._formatMoney(item.red3)}</td>
                                                    <td className={`cell-datetime text-center`}>{Helper._formatMoney(item.red4)}</td>
                                                    <td className={`cell-datetime text-center`}>{Helper._formatMoney(item.white3)}</td>
                                                    <td className={`cell-datetime text-center`}>{Helper._formatMoney(item.white4)}</td>
    		                                        <td className={`text-center ${item.betwin >= totalBet  ? 'text-positive' : 'text-negative'}`}>{Helper._formatMoney(item.betwin)}</td>
    		                                        <td className="cell-datetime text-center">{moment(item.time).format(`DD/MM/YYYY HH:mm:ss`)}</td>
    		                                    </tr>
                                    			)
                                    	})}
                                    </tbody>
                                </table>
                            </div>
                            </div>
                            <div className="margin-top-10">
                                <PaginationView />
                        	</div>
                        </div>
					</div>
				</div>
			</div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData,
    }, dispatch)
}
const mapStateToProps = ({
    xocdiaHistory,
    xocdiaHistoryPagination
}) => {
    return {
        xocdiaHistory,
        xocdiaHistoryPagination
    }
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(HistoryView)



