import {
    ONSUBMIT_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent/types'
import { show as showAlert } from 'modules/notify/actions'
import {
    onError as onErrorNickname
} from 'modules/home.admin.transfer.agent.nickname/actions'
import {
    onError as onErrorOtp
} from 'modules/home.admin.transfer.agent.otp/actions'
import {getData as getBalance} from 'modules/home.balance/actions'
const onSubmit = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: nickname } = getState().nicknameTransferDaiLy || {}
        const { data: desc } = getState().descTransferDaiLy || {}
        const { data: valueRed } = getState().valueTransferDaiLy || {}
        const { data: otp } = getState().otpTransferDaiLy || {}
        $(`#btnTransferDaiLyID`).addClass('isLoading')
        dispatch({
            type: ONSUBMIT_TRANSFER_AGENT,
            payload: null
        })
        axios.post(`${Config.API_URL}agent/transferAgent`, {
                Data: {
                    nickname: nickname,
                    desc: desc,
                    valueRed: parseInt(valueRed.toString().split(',').join('')),
                    otp: otp
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { data: info, success } = data || {}
                if (success) {
                    document.getElementById('inputNicknameTransferDaily').value = '';
                    document.getElementById('inputOtpTransferDaiLy').value = '';
                    dispatch(showAlert('success', info.message))
                    dispatch(getBalance())
                } else {
                    if (info.nickname) {
                        dispatch(onErrorNickname(info.nickname))
                    } else if (info.otp) {
                        dispatch(onErrorOtp(info.otp))
                    } else {
                        dispatch(showAlert('error', info.message))
                    }
                }
                $(`#btnTransferDaiLyID`).removeClass('isLoading')
            }, (err) => {
                reject(err)
                $(`#btnTransferDaiLyID`).removeClass('isLoading')
            })
    })
}
module.exports = onSubmit



