import {
    ONERROR_NICKNAME_ADD_DAILY2
} from 'modules/home.admin.agent2.list.add.nickname/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateNicknameAddDaiLy2ID`).addClass('has-error')
        $(`#validateNicknameMessAddDaiLy2ID`).removeClass('hide')
        $(`#validateNicknameMessAddDaiLy2ID`).text(mess)
        dispatch({
            type: ONERROR_NICKNAME_ADD_DAILY2,
            payload: null
        })
    }
}
module.exports = onError


