import {
    ON_GETDATA_PROFIT_AGENT
} from 'modules/home.admin.agent.profit/types'
const onSubmit = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: fromDate } = getState().filterAgentProfitFrom || {}
        const { data: toDate } = getState().filterAgentProfitTo || {}
        $('.loading').addClass('active')
        axios.post(`${Config.API_URL}agent/profitAgent`, {
                Data: {
                    fromDate: moment(fromDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 00:00:00 Z'),
                    toDate: moment(toDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 23:59:59 Z'),
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info } = data || {}
                if (success) {
                    dispatch({
                        type: ON_GETDATA_PROFIT_AGENT,
                        payload: {
                            data: info
                        }
                    })
                }
                resolve()
                $('.loading').removeClass('active')
            }, (err) => {
                $('.loading').removeClass('active')
                reject(err)
            })
    })
}
export default onSubmit


