import {
    ONERROR_DAILY_TRANSFER_HISTORY_AGENTS
} from 'modules/home.admin.agents.transfer.history.daily/types'
const onError = (dispatch) => {
    return (dispatch) => {
        $(`#validateMessAgentsTransferHistoryDaiLy`).addClass('has-error')
        dispatch({
            type: ONERROR_DAILY_TRANSFER_HISTORY_AGENTS,
            payload: null
        })
    }
}
module.exports = onError


