import {
    ONERROR_SOLUONG_GIFTCODE_DAILY
} from 'modules/home.admin.giftcode.agent.add.soluong/types'
const onError = () => {
    return (dispatch) => {
        $(`#validateMessSoLuongGiftCodeDaiLyAdd`).addClass('has-error')
        dispatch({
            type: ONERROR_SOLUONG_GIFTCODE_DAILY,
            payload: null
        })
    }
}
module.exports = onError


