import {
    ONCHANGE_NICKNAME_USERLIST
} from 'modules/home.userlist.nickname/types'
const onChange = (val) => {
    return (dispatch) => {
        dispatch ({
            type: ONCHANGE_NICKNAME_USERLIST,
            payload: {
            	data: val
            }
        })
    }
}
module.exports = onChange


