import {
    ONERROR_MENHGIA_GIFTCODE_DAILY_FILTER
} from 'modules/home.agent.giftcode.list.menhgia/types'
const onError = () => {
    return (dispatch) => {
        $(`#validateMenhGiaGiftCodeDaiLyFilter`)
            .addClass('has-error')
        dispatch({
            type: ONERROR_MENHGIA_GIFTCODE_DAILY_FILTER,
            payload: null
        })
    }
}
module.exports = onError


