import {
    SHOW_CONFIRM_REMOVE_DAILY2
} from 'modules/home.admin.agent2.list.remove.confirm/types'
const show = () => {
    return (dispatch) => {
        $(`#confirmRemoveAgent2ID`).addClass('active')
        dispatch({
            type: SHOW_CONFIRM_REMOVE_DAILY2,
            payload: null
        })
    }
}
module.exports = show


