import {
    onChange
} from 'modules/home.admin.giftcode.marketing.list.menhgia/actions'
class MenhGiaView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    render() {
        const { data } = this.props.menhgiaGiftCodeMarketingList || {}
        return (
            <div className="form-group-box" id="validateMenhGiaGiftCodeMarketingList">
            		<label className="control-label">Mệnh giá</label>
					<div className="select">
						<select 
							className="form-control"  
							value={data}
							selected={data}
							onChange={event=>{
								this._onChange(event)
							}}>
							<option value="">Mệnh giá</option>
							<option value="10000">10K</option> 
							<option value="20000">20K</option> 
							<option value="50000">50K</option> 
							<option value="100000">100K</option> 
							<option value="200000">200K</option> 
							<option value="500000">500K</option>
						</select>
					</div>
				</div>
        )
    }
}
const mapStateToProps = ({ menhgiaGiftCodeMarketingList }) => {
    return {
        menhgiaGiftCodeMarketingList
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(MenhGiaView)


