import {
    ONERROR_TRANGTHAI_GIFTCODE_MARKETING_DAILY_FILTER
} from 'modules/home.admin.giftcode.marketing.list.trangthai/types'
const onError = () => {
    return (dispatch) => {
        $(`#validateTrangthaiGiftCodeDaiLyFilter`)
            .addClass('has-error')
        dispatch({
            type: ONERROR_TRANGTHAI_GIFTCODE_MARKETING_DAILY_FILTER,
            payload: null
        })
    }
}
module.exports = onError


