import {
    ONERROR_MENHGIA_GIFTCODE_MARKETING
} from 'modules/home.admin.giftcode.marketing.add.menhgia/types'
const onError = () => {
    return (dispatch) => {
        $(`#validateMenhGiaGiftCodeMarketingAdd`)
            .addClass('has-error')
        dispatch({
            type: ONERROR_MENHGIA_GIFTCODE_MARKETING,
            payload: null
        })
    }
}
module.exports = onError


