import {
    onChange
} from 'modules/home.games.history.bigbabol.to/actions'
class ToView extends React.Component {
    componentDidMount() {
        this.initDatePicker()
    }
    initDatePicker() {
        const { data } = this.props.filterBigbabolHistoryTo || {}
        const datePicker = $(`.datepicker_admin_bigbabol_history_to`).datepicker({
            dateFormat: 'dd/mm/yy',
            onSelect: function() {
                let date = $(`.datepicker_admin_bigbabol_history_to`).datepicker('getDate')
                date = moment(date).format('YYYY-MM-DD 00:00:00 Z')
                this.props.onChange(date)
            }.bind(this)
        })
        // $(datePicker).datepicker('setDate', moment(data, 'YYYY-MM-DD 00:00:00 Z').toDate())
    }
    render() {
        const { data } = this.props.filterBigbabolHistoryTo || {}
        return (
			<div className="form-group-box">
                <label className="control-label">Đến ngày</label>
                <div className="input input--icon">
                    <input type="text" className="form-control datepicker_admin_bigbabol_history_to" placeholder="dd/mm/yyyy" />
                    <span className="icon"><i className="ti-calendar"></i></span>
                </div>
            </div>
        )
    }
}
const mapStateToProps = ({
    filterBigbabolHistoryTo
}) => {
    return {
        filterBigbabolHistoryTo
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(ToView)


