import {
    ONCHANGE_TODATE_ADMIN_MINI3CAY_HISTORY
} from 'modules/home.games.history.mini3cay.to/types'
const onChange = (val) => {
    return (dispatch) => {
        dispatch({
            type: ONCHANGE_TODATE_ADMIN_MINI3CAY_HISTORY,
            payload: {
                data: val
            }
        })
    }
}

module.exports = onChange


