import {
    GET_ANGRYBIRDS_HISTORY_ADMIN
} from 'modules/home.games.history.angrybirds/types'
import {
    CHANGE_CURRENT_PAGE_ANGRYBIRDS_HISTORY_ADMIN
} from 'modules/home.games.history.angrybirds.pagination/types'
const getData = (isFirst) => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { show, current } = getState().angrybirdsHistoryPagination || {}
        const { data: nickname } = getState().nicknameAngrybirdsHistoryAdmin || {}
        const { data: fromDate } = getState().filterAngrybirdsHistoryFrom || {}
        const { data: toDate } = getState().filterAngrybirdsHistoryTo || {}
        $('.loading').addClass('active')
        axios.post(`${Config.API_URL}admin/angrybirdsHistory`, {
                Data: {
                    limit: show,
                    offset: (current - 1) * show,
                    fromDate: fromDate ? moment(fromDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 00:00:00 Z') : fromDate,
                    toDate: toDate ? moment(toDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 23:59:59 Z') : toDate,
                    nickname: nickname
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info, totals } = data || {}
                if (success) {
                    dispatch({
                        type: GET_ANGRYBIRDS_HISTORY_ADMIN,
                        payload: {
                            data: info
                        }
                    })
                    dispatch({
                        type: CHANGE_CURRENT_PAGE_ANGRYBIRDS_HISTORY_ADMIN,
                        payload: {
                            totals: totals,
                            show: show,
                            current: current
                        }
                    })
                }
                $('.loading').removeClass('active')
            }, (err) => {
                $('.loading').removeClass('active')
                reject(err)
            })
    })
}
module.exports = getData


