import {
    ONCHANGE_MENHGIA_GIFTCODE_MARKETING
} from 'modules/home.admin.giftcode.marketing.add.menhgia/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateMenhGiaGiftCodeMarketingAdd`).removeClass('has-error')
        dispatch({
            type: ONCHANGE_MENHGIA_GIFTCODE_MARKETING,
            payload: {
                data: val
            }
        })
    }
}
module.exports = onChange


