import {
    getDaily,
    onConfirmRemove
} from 'modules/home.admin.agent.list/actions'
import ConfirmRemoveView from 'modules/home.admin.agent.list.remove.confirm/ConfirmView'
import AddView from 'modules/home.admin.agent.list.add/AddView'
import {
    show
} from 'modules/home.admin.agent.list.add/actions'
class ListView extends React.Component {
    componentDidMount() {
        this.props.getDaily()
    }
    _onRemove(id) {
        this.props.onConfirmRemove(id)
    }
    _openModalCreate() {
        this.props.show()
    }
    render() {
        const { data } = this.props.daily || {}
        return (
            <div className="page__content">
					<div className="page__header">
						<div className="title">
							<h1 className="pageTitle">DANH SÁCH ĐẠI LÝ</h1>
						</div>
					</div>
					<div className="page__body">
						<div className="box margin-top-10">
							<div className="flex flex-middle margin-bottom-10">
								<div className="flex-1 flex">
								</div>
								<div>
									<a className="btn btn--primary" 
									onClick={event=>{
										this._openModalCreate()
									}}>Thêm Đại lý</a>
								</div>
								</div>
							<div className="table-container">
								<div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
		                            <table className="table sticky">
		                                <thead>
		                                    <tr>
		                                        <th className="cell-no">STT</th>
		                                        <th>Tên Đại lý</th>
		                                        <th>Nickname</th>
		                                        <th>Số điện thoại</th>
		                                        <th>Khu vực</th>
		                                        <th className="cell-actions">Hành động</th>
		                                    </tr>
		                                </thead>
		                                <tbody>
		                                    {data.map((item, index)=>{
		                                    	return (
		                                    		<tr key={`daily_${index}`}>
				                                        <td className="cell-no text-center">{index + 1}</td>
				                                        <td className="text-nowrap">{item.name ? item.name: ''}</td>
				                                        <td>{item.nickname ? item.nickname: ''}</td>
				                                        <td className="cell-datetime">{item.phone ? item.phone : ''}</td>
				                                        <td className="cell-datetime">{item.location ? item.location : ''}</td>
				                                        <td className="cell-actions text-center">
				                                            <a className="btn btn-icon-only delete" onClick={event=>{
				                                            	this._onRemove(item._id)
				                                            }}>
				                                            	<i className="ti-close"></i>
				                                            </a>
				                                        </td>
				                                    </tr>
		                                    		)
		                                    })}
		                                </tbody>
		                            </table>
		                        </div>
		                    </div>
	                        </div>
						</div>
					</div>
					<ConfirmRemoveView />
					<AddView />
				</div>
        )
    }
}
const mapStateToProps = ({
    daily
}) => {
    return {
        daily
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getDaily,
        onConfirmRemove,
        show
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(ListView)


