import {
    ONFOCUS_PHONE_ADD_DAILY
} from 'modules/home.admin.agent.list.add.phone/types'
const onFocus = () => {
    return (dispatch) => {
        $(`#phoneAddDaiLyID`).focus()
        dispatch({
            type: ONFOCUS_PHONE_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onFocus


