import {
    ONCHANGE_PHONE_ADD_DAILY
} from 'modules/home.admin.agent.list.add.phone/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_PHONE_ADD_DAILY:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


