import {
    ONFOCUS_USERNAME_LOGIN
} from 'modules/auth.username/types'
const onFocus = () => {
    return (dispatch) => {
        $(`#userID`).focus()
        dispatch({
            type: ONFOCUS_USERNAME_LOGIN,
            payload: null
        })
    }
}
module.exports = onFocus


