import {
    ONBLUR_OLD_CHANGE_PASSWORD
} from 'modules/home.changePassword.newpass/types'
const onBlur = () => {
    return (dispatch) => {
        $(`#newPassID`).focus()
        dispatch({
            type: ONBLUR_NEW_CHANGE_PASSWORD,
            payload: null
        })
    }
}
module.exports = onBlur


