import {
    ON_GETDATA_GIFTCODE_STATEMENT_AGENT
} from 'modules/home.agent.giftcode.statement/types'
import {
    onChange
} from 'modules/home.agent.giftcode.list.pagination/actions'
const onSubmit = (isFirst) => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: fromDate } = getState().filterAgentGiftcodeStatementFrom || {}
        const { data: toDate } = getState().filterAgentGiftcodeStatementTo || {}
        $('.loading').addClass('active')
        axios.post(`${Config.API_URL}agent/giftcodeStatement`, {
                Data: {
                    fromDate: fromDate ? moment(fromDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 00:00:00 Z') : null,
                    toDate: toDate ? moment(toDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 23:59:59 Z') : null,
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info } = data || {}
                if (success) {
                    dispatch({
                        type: ON_GETDATA_GIFTCODE_STATEMENT_AGENT,
                        payload: {
                            data: info
                        }
                    })
                }
                $('.loading').removeClass('active')
                resolve()
            }, (err) => {
                $('.loading').removeClass('active')
                reject(err)
            })
    })
}
export default onSubmit


