import {
    getData
} from 'modules/home.init/actions'
class SummaryView extends React.Component {
    componentDidMount() {
        this.props.getData()
    }
    render() {
        const { data } = this.props.summary || {}
        const { CCUNRU, card, bank, agent } = data || {}
        const { data: userInfo } = this.props.userInfo || {}
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Trang chủ</h1>
					</div>
				</div>
				{userInfo.rights == 9 ? (
					<div className="page__body">
						<div className="loading">
							<div className="loading__dimmer">
							</div>
							<div className="loading__content">
							<div className="cards grid grid-lg-4 grid grid-md-3 grid grid-sm-2 grid-gap-lg-10">
								<div className="card card--game">
									<div className="card__header">
										<div className="card__title">CCU - NRU</div>
									</div>
									<div className="card__body">
										<div className="card__content">
											<dl>
												<dt>User Online:</dt>
												<dd><span className="status status--success">{CCUNRU ? CCUNRU.userOnline : 0}</span></dd>
												<dt>User Mới:</dt>
												<dd><span className="status status--warning">{CCUNRU ? CCUNRU.userNew : 0}</span></dd>
												<dt>Tổng User:</dt>
												<dd><span className="status status--primary">{CCUNRU ? CCUNRU.total : 0}</span></dd>
											</dl>
										</div>
									</div>
								</div>
								<div className="card card--game">
									<div className="card__header">
										<div className="card__title">Thẻ Cào</div>
									</div>
									<div className="card__body">
										<div className="card__content">
											<dl>
												<dt>Tổng Nạp:</dt>
												<dd><span className="status status--success">{card ? Helper._formatMoney(card.deposit) : 0}</span></dd>
												<dt>Tổng Rút:</dt>
												<dd><span className="status status--warning">{card ? Helper._formatMoney(card.withdrawal) : 0}</span></dd>
												<dt>Lãi:</dt>
												<dd><span className={`status status--${card && card.profit < 0 ? 'danger' : 'success'}`}>{card ? Helper._formatMoney(card.profit) : 0}</span></dd>
											</dl>
										</div>
									</div>
								</div>
								<div className="card card--game">
									<div className="card__header">
										<div className="card__title">Ngân Hàng</div>
									</div>
									<div className="card__body">
										<div className="card__content">
											<dl>
												<dt>Tổng Nạp:</dt>
												<dd><span className="status status--success">{bank ? Helper._formatMoney(bank.deposit) : 0}</span></dd>
												<dt>Tổng Rút:</dt>
												<dd><span className="status status--warning">{bank ? Helper._formatMoney(bank.withdrawal) : 0}</span></dd>
												<dt>Lãi:</dt>
												<dd><span className={`status status--${bank && bank.profit < 0 ? 'danger' : 'success'}`}>{bank ? Helper._formatMoney(bank.profit) : 0}</span></dd>
											</dl>
										</div>
									</div>
								</div>
								<div className="card card--game">
									<div className="card__header">
										<div className="card__title">Đại Lý</div>
									</div>
									<div className="card__body">
										<div className="card__content">
											<dl>
												<dt>Tổng Mua:</dt>
												<dd><span className="status status--success">{agent ? Helper._formatMoney(agent.deposit) : 0}</span></dd>
												<dt>Tổng Bán:</dt>
												<dd><span className="status status--warning">{agent ? Helper._formatMoney(agent.withdrawal) : 0}</span></dd>
												<dt>Lãi:</dt>
												<dd><span className={`status status--${agent && agent.profit < 0 ? 'danger' : 'success'}`}>{agent ? Helper._formatMoney(agent.profit) : 0}</span></dd>
											</dl>
										</div>
									</div>
								</div>
							</div>
						</div>
						</div>

					</div>
					) : (<span />)}
				
			</div>
        )
    }
}
const mapStateToProps = ({
    summary,
    userInfo
}) => {
    return {
        summary,
        userInfo
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData
    }, dispatch)
}

export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(SummaryView)


