import {
    ONERROR_VALUE_TRANSFER_ADMIN
} from 'modules/home.admin.transfer.value/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateValueTransferAdmin`).addClass('has-error')
        $(`#validateMessValueTransferAdmin`).removeClass('hide')
        $(`#validateMessValueTransferAdmin`).text(mess)
        dispatch({
            type: ONERROR_VALUE_TRANSFER_ADMIN,
            payload: null
        })
    }
}
module.exports = onError


