import {
    DELETE_DAILY2
} from 'modules/home.admin.agent2.list/types'
import {
    show as showNotify
} from 'modules/notify/actions'
import {
    hide as hideConfirm
} from 'modules/home.admin.agent2.list.remove.confirm/actions'
import {
    getDaily
} from 'modules/home.admin.agent2.list/actions'
const deleteDaily = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { idRemove } = getState().daily2 || {}
        axios.get(`${Config.API_URL}agent/deleteDaily2`, {
                params: {
                    id: idRemove
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { data: infoRes, success } = data || {}
                if (success) {
                    dispatch({
                        type: DELETE_DAILY2,
                        payload: null
                    })
                    dispatch(hideConfirm())
                    dispatch(showNotify('success', 'Xoá thành công!'))
                    dispatch(getDaily())
                }
            }, (err) => {
                reject(err)
            })
    })
}
module.exports = deleteDaily


