import {
    ONCHANGE_NICKNAME_DEPOSIT_USER
} from 'modules/home.user.deposit.check.nickname/types'
const onChange = (val) => {
    return (dispatch) => {
        dispatch ({
            type: ONCHANGE_NICKNAME_DEPOSIT_USER,
            payload: {
            	data: val
            }
        })
    }
}
module.exports = onChange


