import {
    ONCHANGE_OLD_CHANGE_PASSWORD
} from 'modules/home.changePassword.oldpass/types'
const onChange = (val) => {
    return (dispatch, getState) => {
        const { data } = getState().oldPass || {}
        $(`#validateOldPassID`).removeClass('has-error')
        $(`#validateOldPassMessID`).addClass('hide')
        if (data != val) {
            dispatch({
                type: ONCHANGE_OLD_CHANGE_PASSWORD,
                payload: {
                    data: val
                }
            })
        }
    }
}
module.exports = onChange


