import {
    ONERROR_NGAYTHANG_GIFTCODE_MARKETING
} from 'modules/home.admin.giftcode.marketing.add.ngaythang/types'
const onError = () => {
    return (dispatch) => {
        $(`#validateMessNgayThangGiftCodeMarketingAdd`).addClass('has-error')
        dispatch({
            type: ONERROR_NGAYTHANG_GIFTCODE_MARKETING,
            payload: null
        })
    }
}
module.exports = onError



