import {
    SHOW_MODAL_REPORT_TRANSACTION_AGENT
} from 'modules/home.admin.transfer.agent.history.report/types'
const show = (id) => {
    return (dispatch) => {
        $(`#modalReportTransferAgentID`).addClass('active')
        dispatch({
            type: SHOW_MODAL_REPORT_TRANSACTION_AGENT,
            payload: {
                id: id
            }
        })
    }
}
module.exports = show


