import {
    ON_GETDATA_USERLIST
} from 'modules/home.userlist/types'
import {
    CHANGE_CURRENT_PAGE_USERLIST
} from 'modules/home.userlist.pagination/types'
const onSubmit = (isFirst) => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: nickname } = getState().nicknameUserlist || {}
        const { show, current } = getState().userlistPagination || {}
        $('.loading').addClass('active')
        axios.post(`${Config.API_URL}admin/userlist`, {
                Data: {
                    nickname: nickname,
                    limit: show,
                    offset: (current - 1) * show
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info, totals } = data || {}
                if (success) {
                    dispatch({
                        type: ON_GETDATA_USERLIST,
                        payload: {
                            data: info
                        }
                    })
                    dispatch({
                        type: CHANGE_CURRENT_PAGE_USERLIST,
                        payload: {
                            show: show,
                            current: current,
                            totals: totals
                        }
                    })
                }
                $('.loading').removeClass('active')
                resolve()
            }, (err) => {
                $('.loading').removeClass('active')
                reject(err)
            })
    })
}
export default onSubmit


