import {
    ONCHANGE_FROMDATE_STATEMENT_TRANSFER_USER_CARD
} from 'modules/home.user.card.statement.from/types'
const onChange = (val) => {
    return (dispatch) => {
        dispatch({
            type: ONCHANGE_FROMDATE_STATEMENT_TRANSFER_USER_CARD,
            payload: {
                data: val
            }
        })
    }
}

module.exports = onChange


