import {
    ONCHANGE_FROMDATE_AGENTS_PROFIT
} from 'modules/home.admin.agents.profit.from/types'
const INITIAL_STATE = {
    data: moment().format('YYYY-MM-DD 00:00:00 Z')
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_FROMDATE_AGENTS_PROFIT:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


