import { ONVALIDATE_ADMIN_ADD_GIFTCODE_MARKETING } from 'modules/home.admin.giftcode.marketing.add/types'
import {
    onError as onErrorMenhGia
} from 'modules/home.admin.giftcode.marketing.add.menhgia/actions'
import {
    onError as onErrorSoLuong
} from 'modules/home.admin.giftcode.marketing.add.soluong/actions'
import {
    onError as onErrorNgayThang
} from 'modules/home.admin.giftcode.marketing.add.ngaythang/actions'
const onValidate = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: menhgia } = getState().menhgiaGiftCodeMarketingAdd || {}
        const { data: soluong } = getState().soluongGiftCodeMarketingAdd || {}
        const { data: ngaythang } = getState().ngaythangGiftCodeMarketingAdd || {}
        if (!menhgia) {
            dispatch(onErrorMenhGia())
            reject()
        }
        if (!soluong) {
            dispatch(onErrorSoLuong())
            reject()
        }
        if(!ngaythang){
         dispatch(onErrorNgayThang())
         reject()
        }
        if (menhgia && soluong && ngaythang)
            resolve()
    })
}
module.exports = onValidate



