import {
    GET_DAILY2
} from 'modules/home.admin.agent2.list/types'
const getDaily = () => {
    return (dispatch) => new Promise((resolve, reject) => {
        $('.loading').addClass('active')
        axios.get(`${Config.API_URL}agent/getDaily2`)
            .then((response) => {
                const { data } = response || {}
                const { data: daily, success } = data || {}
                if (success) {
                    dispatch({
                        type: GET_DAILY2,
                        payload: {
                            data: daily
                        }
                    })
                }
                $('.loading').removeClass('active')
            }, (err) => {
                $('.loading').removeClass('active')
                reject(err)
            })
    })
}
module.exports = getDaily


