import {
    onChange
}
from 'modules/home.admin.agent.list.add.nickname/actions'
import {
	onFocus as focusPhone
} from 'modules/home.admin.agent.list.add.phone/actions'
class NickNameView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    _onKeyDown(event) {
        const { keyCode } = event || {}
        switch (keyCode) {
            case 13:
                this.props.focusPhone()
                break
            default:
                break
        }
    }
    render() {
        const { data } = this.props.nicknameAddDaiLy || {}
        return (
            <div className="form-group-box" id="validateNicknameAddDaiLyID">
				<label className="control-label">Nickname</label>
				<div className="input">
					<input 
						onChange={event=>{
							this._onChange(event)
						}}
						onKeyDown={event=>{
							this._onKeyDown(event)
						}}
						value={data}
						type="text" 
						id="nicknameAddDaiLyID" 
						className="form-control" 
						placeholder="Nickname" />
				</div>
                <p className="help-block hide" id="validateNicknameMessAddDaiLyID"></p>
			</div>
        )
    }
}
const mapStateToProps = ({
    nicknameAddDaiLy
}) => {
    return {
        nicknameAddDaiLy
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        focusPhone
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(NickNameView)


