import {
    onChange,
    onBlur
}
from 'modules/home.changePassword.confirmpass/actions'
import {
    onValidate,
    onSubmit
} from 'modules/home.changePassword/actions'
class ConfirmPassView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    _onKeyDown(event) {
        switch (event.keyCode) {
            case 13:
                this.props.onBlur()
                this.props.onValidate()
                    .then(() => {
                        this.props.onSubmit()
                    })
                break
            default:
                break
        }
    }
    render() {
        const { data } = this.props.confirmPass || {}
        return (
            <div className="form-group-box" id="validateConfirmPassID">
                <label className="control-label">Xác nhận lại</label>
                <div className="input">
                    <input 
                        onChange={event=>{
                            this._onChange(event)
                        }}
                        onKeyDown={event=>{
                            this._onKeyDown(event)
                        }}
                        maxLength="12"
                        value={data}
                        type="password" 
                        id="confirmPassID" 
                        className="form-control" 
                        placeholder="Xác nhận mật khẩu mới" />
                </div>
                <p className="help-block hide" id="validateConfirmPassMessID"></p>
            </div>
        )
    }
}
const mapStateToProps = ({
    confirmPass
}) => {
    return {
        confirmPass
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        onValidate,
        onBlur,
        onSubmit
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(ConfirmPassView)


