import {
    ONCHANGE_DAILY_GIFTCODE_DAILY_ADD
} from 'modules/home.admin.giftcode.agent.add.daily/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateMessDaiLyGiftCodeAddID`).removeClass('has-error')
        dispatch({
            type: ONCHANGE_DAILY_GIFTCODE_DAILY_ADD,
            payload: {
                data: val
            }
        })
    }
}
module.exports = onChange


