import {
    onChange
}
from 'modules/home.changePassword.oldpass/actions'
import {
	onFocus as focusNewPass
} from 'modules/home.changePassword.newpass/actions'
class OldPassView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    _onKeyDown(event) {
        const { keyCode } = event || {}
        switch (keyCode) {
            case 13:
                this.props.focusNewPass()
                break
            default:
                break
        }
    }
    render() {
        const { data } = this.props.oldPass || {}
        return (
            <div className="form-group-box" id="validateOldPassID">
				<label className="control-label">Mật khẩu cũ</label>
				<div className="input">
					<input 
						onChange={event=>{
							this._onChange(event)
						}}
						onKeyDown={event=>{
							this._onKeyDown(event)
						}}
						maxLength="12"
						value={data}
						type="password" 
						id="oldPassID" 
						className="form-control" 
						placeholder="Mật khẩu cũ" />
				</div>
                <p className="help-block hide" id="validateOldPassMessID"></p>
			</div>
        )
    }
}
const mapStateToProps = ({
    oldPass
}) => {
    return {
        oldPass
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        focusNewPass
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(OldPassView)


