import {
    ONERROR_DESC_REPORT_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent.history.report.desc/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateDescReportTransferDaiLy`).addClass('has-error')
        $(`#validateMessDescReportTransferDaiLy`).removeClass('hide')
        $(`#validateMessDescReportTransferDaiLy`).text(mess)
        dispatch({
            type: ONERROR_DESC_REPORT_TRANSFER_AGENT,
            payload: null
        })
    }
}
module.exports = onError


