import {
    ONVALIDATE_TRANSFER_ADMIN
} from 'modules/home.admin.transfer/types'
import {
    onError as onErrorNickname
} from 'modules/home.admin.transfer.nickname/actions'
import {
    onError as onErrorValue
} from 'modules/home.admin.transfer.value/actions'
import {
    onError as onErrorDesc
} from 'modules/home.admin.transfer.desc/actions'
const onValidate = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: nickname } = getState().nicknameTransferAdmin || {}
        const { data: desc } = getState().descTransferAdmin || {}
        const { data: valueRed } = getState().valueTransferAdmin || {}
        dispatch({
            type: ONVALIDATE_TRANSFER_ADMIN,
            payload: null
        })
        if (!desc || desc == '') {
            dispatch(onErrorDesc('Lí do bắt buộc'))
        }
        if (!valueRed || valueRed == '') {
            dispatch(onErrorValue('Giá trị bắt buộc'))
        }
        if (!nickname || nickname == '') {
            dispatch(onErrorNickname('Nickname bắt buộc'))
        }
        if (nickname &&
            desc &&
            valueRed) {
            resolve()
        } else {
            reject()
        }

    })
}
module.exports = onValidate


