import {
    onChange,
    onError
} from 'modules/home.admin.giftcode.marketing.check.code/actions'
class SoLuongView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    render() {
        const { data } = this.props.codeGiftCodeMarketingCheck || {}
        return (
            <div className="form-group-box" id="validateMessCodeGiftCodeMarketingCheck">
            <label className="control-label">Mã Code</label>
				<div className="input">
					<input 
						type="text"
                        maxLength="12"
						className="form-control" 
						placeholder="- - - - - - - - - - - - "  
						value={data}
						onChange={event=>{
							this._onChange(event)
						}}
						 />
				</div>
			</div>
        )
    }
}
const mapStateToProps = ({
    codeGiftCodeMarketingCheck
}) => {
    return {
        codeGiftCodeMarketingCheck
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        onError
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(SoLuongView)


