import {
    ONACTIVE_SUB_MENU
} from 'modules/home.menu/types'
const activeSubMenu = (subID) => {
    return (dispatch) => {
        dispatch({
            type: ONACTIVE_SUB_MENU,
            payload: {
                active: subID
            }
        })
    }
}
module.exports = activeSubMenu


