import {
    ONCHANGE_FROMDATE_STATEMENT_TRANSFER_USER
} from 'modules/home.user.transfer.statement.from/types'
const INITIAL_STATE = {
    data: null
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_FROMDATE_STATEMENT_TRANSFER_USER:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


