import {
    ONSUBMIT_ADD_DAILY
} from 'modules/home.admin.agent.list.add/types'
import {
    hide as hideModal
} from 'modules/home.admin.agent.list.add/actions'
import {
    show as showNotify
} from 'modules/notify/actions'
import {
    onError as onErrorNickname,
    onFocus as onFocusNickname
} from 'modules/home.admin.agent.list.add.nickname/actions'
import { getDaily } from 'modules/home.admin.agent.list/actions'
const onSubmit = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: nameAddDaiLy } = getState().nameAddDaiLy || {}
        const { data: nicknameAddDaiLy } = getState().nicknameAddDaiLy || {}
        const { data: phoneAddDaiLy } = getState().phoneAddDaiLy || {}
        const { data: locationAddDaiLy } = getState().locationAddDaiLy || {}
        const { data: fbAddDaiLy } = getState().fbAddDaiLy || {}
        $(`#btnCreateAgentID`).addClass('isLoading')
        axios.post(`${Config.API_URL}admin/createDaily`, {
                Data: {
                    name: nameAddDaiLy,
                    nickname: nicknameAddDaiLy,
                    phone: phoneAddDaiLy,
                    location: locationAddDaiLy,
                    fb: fbAddDaiLy
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { data: info, success } = data || {}
                if (success) {
                    dispatch(hideModal())
                    dispatch(showNotify('success', 'Thêm Đại lý thành công'))
                    dispatch(getDaily())
                } else {
                    if (info.nickname) {
                        dispatch(onErrorNickname(info.nickname))
                        dispatch(onFocusNickname())
                    } else {
                        dispatch(showNotify('error', info.message))
                    }
                }
                $(`#btnCreateAgentID`).removeClass('isLoading')
            }, (err) => {
                $(`#btnCreateAgentID`).removeClass('isLoading')
                reject(err)
            })
    })
}
module.exports = onSubmit


