import {
    ONCHANGE_NAME_ADD_DAILY
} from 'modules/home.admin.agent.list.add.name/types'
const onChange = (val) => {
    return (dispatch, getState) => {
        const { data } = getState().nameAddDaiLy || {}
        $(`#validateNameAddDaiLyID`).removeClass('has-error')
        $(`#validateNameMessAddDaiLyID`).addClass('hide')
        if (data != val) {
            dispatch({
                type: ONCHANGE_NAME_ADD_DAILY,
                payload: {
                    data: val
                }
            })
        }
    }
}
module.exports = onChange


