import {
    ONCHANGE_STATUS_LOGIN_USERLIST
} from 'modules/home.userlist.submit/types'
import {
    show as showAlert
} from 'modules/notify/actions'
import {
    onSubmit as getData
} from 'modules/home.userlist.submit/actions'
const onChangeStatus = (status, id) => {
    return (dispatch) => new Promise((resolve, reject) => {
    	$(`#btnBlockAccountID`).addClass('isLoading')
        axios.get(`${Config.API_URL}admin/user/changeStatus`, {
                params: {
                    status: status,
                    id: id
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info } = data || {}
                if (success) {
                    dispatch(showAlert('success', !status ? 'Mở khoá thành công' : 'Khoá thành công'))
                    dispatch(getData())
                } else {
                    dispatch(showAlert('error', 'Hệ thống bảo trì. \nVui lòng thử lại sau.'))
                }
                $(`#btnBlockAccountID`).removeClass('isLoading')
                resolve(response)
            }, (err) => {
            	$(`#btnBlockAccountID`).removeClass('isLoading')
                reject(err)
            })
    })
}
module.exports = onChangeStatus


