import {
    ONCHANGE_FROMDATE_ADMIM_BIGBABOL_HISTORY
} from 'modules/home.games.history.bigbabol.from/types'
const INITIAL_STATE = {
    data: null
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_FROMDATE_ADMIM_BIGBABOL_HISTORY:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


