import {
    ON_CHECK_DEPOSIT_USER
} from 'modules/home.user.deposit.check/types'
const onSubmit = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: nickname } = getState().nicknameUserDepositCheck || {}
        $('.loading').addClass('active')
        axios.post(`${Config.API_URL}admin/userDepositCheck`, {
                Data: {
                    nickname: nickname
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info, total } = data || {}
                if (success) {
                    dispatch({
                        type: ON_CHECK_DEPOSIT_USER,
                        payload: {
                            data: info,
                            total: total
                        }
                    })
                }
                $('.loading').removeClass('active')
                resolve()
            }, (err) => {
                $('.loading').removeClass('active')
                reject(err)
            })
    })
}
export default onSubmit


