import {
    onChange,
    onError
} from 'modules/home.admin.giftcode.marketing.add.ngaythang/actions'
class NgayThangView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    render() {
        const { data } = this.props.ngaythangGiftCodeMarketingAdd || {}
        return (
            <div className="form-group-box" id="validateMessNgayThangGiftCodeMarketingAdd">
            <label className="control-label">Ngày tháng</label>
				<div className="input">
					<input
						type="date"
						className="form-control"
						placeholder="Ngày tháng định dạng ngay-thang-nam XX/XX/XXXX"
						value={data}
						onChange={event=>{
							this._onChange(event)
						}}
						 />
				</div>
			</div>
        )
    }
}
const mapStateToProps = ({
    ngaythangGiftCodeMarketingAdd
}) => {
    return {
        ngaythangGiftCodeMarketingAdd
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        onError
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(NgayThangView)



