import { hide } from 'modules/home.user.history/actions'
import TypeView from 'modules/home.user.history.type/TypeView'
class HistoryView extends React.Component {
    _onClose() {
        this.props.hide()
    }
    render() {
        const { data } = this.props.userHistory || {}
        const { userInfo, logs } = data || {}
        const {
            joinedOn,
            email,
            cmt,
            red,
            ketSat,
            redWin,
            redLost,
            redPlay,
            thuong,
            vip,
            lastVip,
            hu,
            name,
            username,
            phone,
            uid,
            timeRegister
        } = userInfo || {}
        return (
            <div className="modal lg" id="modalUserHistoryID">
						<div className="modal__container">
							<div className="modal__dimmer"></div>
							<div className="modal__content">
								<div className="loading">
				                    <div className="loading__dimmer">
				                    </div>
				                    <div className="loading__content">
								<div className="modal__header">
									<div className="title">THÔNG TIN - LỊCH SỬ USER</div>
									<div className="btn close" onClick={event=>{
										this._onClose()
									}}><i className="ti-close"></i></div>
								</div>
								<div className="modal__body">
									<h3 className="text-info">THÔNG TIN CÁ NHÂN</h3>
									<div className="flex">
										<div className="flex-1">
											<table className="table-label-value">
												<tbody>
												<tr><td>Tên Đăng Nhập:</td><td>{username ?  username : ''}</td></tr>
												<tr><td>Số Điện Thoại:</td><td>{phone? phone : ''}</td></tr>
												<tr><td>Email:</td><td>{email ?  email : ''}</td></tr>
												<tr><td>CMT:</td><td>{cmt ? cmt : ''}</td></tr>
												<tr><td>Ngày Lập Nick:</td><td>{moment(timeRegister).format('DD/MM/YYYY HH:mm:ss')}</td></tr>
												<tr><td>Ngày Tham Gia:</td><td>{moment(joinedOn).format('DD/MM/YYYY HH:mm:ss')}</td></tr>
												<tr><td>VIP Hiện Tại:</td><td>{vip}</td></tr>
												<tr><td>VIP Tích Luỹ:</td><td>{lastVip}</td></tr>
												</tbody>
											</table>
										</div>
										<div className="flex-1">
											<table className="table-label-value">
												<tbody><tr><td>ZON:</td><td>{Helper._formatMoney(red)}</td></tr>
												<tr><td>Két Sắt:</td><td>{Helper._formatMoney(ketSat)}</td></tr>
												<tr><td>Thưởng:</td><td>{Helper._formatMoney(thuong)}</td></tr>
												<tr><td>ZON Đã Chơi:</td><td>{Helper._formatMoney(redPlay)}</td></tr>
												<tr><td>ZON Thắng:</td><td>{Helper._formatMoney(redWin)}</td></tr>
												<tr><td>ZON Thua:</td><td>{Helper._formatMoney(redLost)}</td></tr>
												<tr><td>ZON Lãi:</td><td className={redWin -redLost >=0 ? '' : 'text-negative'}>{Helper._formatMoney(redWin - redLost)}</td></tr>
												<tr><td>Hũ ZON Đã Trúng:</td><td>{Helper._formatMoney(hu)}</td></tr>
											</tbody></table>
										</div>

									</div>
									<div className="flex margin-top-20">
										<TypeView />
									</div>
									<table className="table sticky margin-top-20">
                                        <thead>
                                            <tr>
                                                <th>Name</th>
                                                <th>Game</th>
                                                <th>Loại</th>
                                                <th>ZON</th>
                                                <th>ZON Nhận</th>
                                                <th>Thời Gian</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                        	{logs.map((item, index)=>{
                                        		return (
                                        			<tr key={`history_user_${index}`}>
		                                                <td className="text-nowrap text-center">{item.name}</td>
		                                                <td className="text-nowrap text-center">{item.game}</td>
		                                                <td className="cell-nowrap text-center">{item.type}</td>
		                                                <td className="cell-nowrap text-center">{Helper._formatMoney(item.red)}</td>
		                                                <td className="cell-nowrap text-center">{Helper._formatMoney(item.red_c)}</td>
		                                                <td className="cell-datetime text-center">{moment(item.time).format('DD/MM/YYYY HH:mm:ss')}</td>
		                                            </tr>
                                        			)
                                        	})}
                                        </tbody>
                                    </table>
								</div>
								<div className="modal__footer"></div>
							</div>
						</div>
					</div>
				</div>
			</div>
        )
    }
}
const mapStateTopProps = ({ userHistory }) => {
    return {
        userHistory
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        hide,
        // getData
    }, dispatch)
}
export default ReactRedux.connect(mapStateTopProps, mapDispatchToProps)(HistoryView)



