import {
    ONBLUR_NICKNAME_ADD_DAILY2
} from 'modules/home.admin.agent2.list.add.nickname/types'
const onBlur = () => {
    return (dispatch) => {
        $(`#nicknameAddDaiLy2ID`).focus()
        dispatch({
            type: ONBLUR_NICKNAME_ADD_DAILY2,
            payload: null
        })
    }
}
module.exports = onBlur


