import {
    init,
    openMenu,
    activeSubMenu
} from 'modules/home.menu/actions'
class MenuView extends React.Component {
    componentDidMount() {
        this.props.init()
    }
    _onClickMenu(menu) {
        if (Object.keys(menu[1].subs).length != 0) {
            this.props.openMenu(menu[0])
        } else {
            this.props.push({
                pathname: menu[1].routes
            })
        }

    }
    _onClickSubMenu(sub) {
        this.props.activeSubMenu(sub[0])
        this.props.push({
            pathname: sub[1].routes
        })
    }
    render() {
        const { data, active, opened } = this.props.menu || {}
        const menuSorted = _.sortBy(Object.entries(data || {}), "[1].order")
        return (
            <div className="sidebar__nav">
				<ul className="menu-sidebar">
				{menuSorted.map((item, index)=>{
					const {subs} = item[1] || {}
					var subsMenuSorted =  _.sortBy(Object.entries(subs), "[1].order")
					return (
						<li className={`${item[0] == opened ? 'opened active' : ''}`} key={`menu_${item[0]}`} 
						>
								<a onClick={event=>{
							this._onClickMenu(item)
						}}>
								<span className="icon"><i className={item[1].icon}></i></span>
								<span className="name">{item[1].name}</span>
								<span className={subsMenuSorted.length!=0 ? 'arrow' : ''}></span>
								</a>
								<ul>
								 {subsMenuSorted.map((sub, indec)=>{
								 	return (
								 		<li key={`menu_${item[0]}_sub_${sub[0]}`} onClick={event=>{
								 			this._onClickSubMenu(sub)
								 		}} className={`${(sub[0] == active && item[0] == opened) ? 'active' : ''}`}>
								 			<a>
												<span className="icon"><i className="ti-minus"></i></span>
												<span className="name">{sub[1].name}</span>
											</a>
								 		</li>
								 		)
								 })}
								</ul>
						</li>
						)
				})}
				</ul>
			</div>
        )
    }
}
const mapStateToProps = ({ menu }) => {
    return {
        menu
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        init,
        openMenu,
        activeSubMenu,
        ...ReactRouterRedux.routerActions
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(MenuView)


