import {
    getData
} from 'modules/home.admin.transfer.agent.history/actions'
import PaginationView from 'modules/home.admin.transfer.agent.history.pagination/PaginationView'
import ReportView from 'modules/home.admin.transfer.agent.history.report/ReportView'
import {
    show as showModal
} from 'modules/home.admin.transfer.agent.history.report/actions'
import {
    show as showNotify
} from 'modules/notify/actions'
import FreezeView from 'modules/home.admin.transfer.agent.history.freeze/FreezeView'
import {
    show as showModalFreeze
} from 'modules/home.admin.transfer.agent.history.freeze/actions'
class HistoryView extends React.Component {
    componentDidMount() {
        this.props.getData(true)
    }
    _onReport(item) {
        // if (item.report) {
        // this.props.showNotify('error', 'Giao dịch đã được báo cáo Admin')
        // } else {
        this.props.showModal(item._id)
        // }
    }
    _onFreeze(item) {
        if (item.freeze) {
            this.props.showNotify('error', 'Giao dịch đã được đóng băng')
        } else {
            this.props.showModalFreeze(item._id)
        }
    }
    render() {
        const { data } = this.props.agentTransferHistory || {}
        const { show, current } = this.props.agentTransferHistoryPagination || {}
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Lịch sử giao dịch</h1>
					</div>
				</div>
				<div className="page__body">
					<div className="box margin-top-10">
						<div className="table-container">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                                    <table className="table sticky">
                                        <thead>
                                            <tr>
                                                <th className="cell-no">STT</th>
                                                <th>Người chuyển</th>
                                                <th>Người nhận</th>
                                                <th>ZON chuyển</th>
                                                <th>ZON nhận</th>
                                                <th>Nội dung</th>
                                                <th>Mã giao dịch</th>
                                                <th>Ngày giao dịch</th>
                                                <th className="cell-actions">Hành động</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                        	{data.map((item, index)=>{
                                        		return (
                                        			<tr key={`history_${item._id}`}>
        		                                        <td className="cell-no text-center">{ (current - 1) * show+index+1}</td>
        		                                        <td className="text-nowrap text-center">
        		                                        	{item.from}
        		                                        </td>
        		                                        <td className="text-center">{item.to}</td>
        		                                        <td className={`cell-datetime text-center ${item.red >=0 ? 'text-positive' : 'text-negative'}`}>{Helper._formatMoney(item.red)}</td>
        		                                        <td className={`cell-datetime text-center ${item.red_c >=0 ? 'text-positive' : 'text-negative'}`}>{Helper._formatMoney(item.red_c)}</td>
        		                                        <td className="text-center">{item.message}</td>
        		                                        <td className="text-center">{item._id}</td>
        		                                        <td className="cell-datetime text-center">{moment(item.time).format(`DD/MM/YYYY HH:mm:ss`)}</td>
        		                                        <td className="cell-actions text-center">
        		                                            <a className={`btn btn-icon-only ${item.report ? 'delete' : 'edit'}`} onClick={event=>{
        		                                            	this._onReport(item)
        		                                            }} title={item.report ? item.report : 'Chưa có báo cáo nào..'}><i className="ti-alert"></i></a>
        		                                            <a className={`btn btn-icon-only text-green ${item.freeze ? 'delete' : 'edit'}`} onClick={event=>{
        		                                            	this._onFreeze(item)
        		                                            }} title={item.freeze ? item.freeze : 'Chưa có đóng băng..'}><i className="ti-lock"></i></a>
        		                                        </td>
        		                                    </tr>
                                        			)
                                        	})}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            <div className="margin-top-10">
                                <PaginationView />
                        	</div>
                        </div>
					</div>
				</div>
				<ReportView />
				<FreezeView />
			</div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData,
        showModal,
        showNotify,
        showModalFreeze
    }, dispatch)
}
const mapStateToProps = ({
    agentTransferHistory,
    agentTransferHistoryPagination
}) => {
    return {
        agentTransferHistory,
        agentTransferHistoryPagination
    }
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(HistoryView)



