import {
    getData
} from 'modules/home.balance/actions'

class BalanceView extends React.Component {
    componentDidMount() {
        this.props.getData()
    }
    render() {
    	const {data} = this.props.balance || {}
        return (
            <div className="money">
                <div className="money__icon"><img src="/images/coin.png" /></div>
                <div className="money__value">
                    <div className="label">ZON</div>
                    <div className="value">{Helper._formatMoney(data || 0)}</div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = ({ balance }) => {
    return {
        balance
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(BalanceView)



