import {
    ONERROR_MENHGIA_GIFTCODE_MARKETING_LIST
} from 'modules/home.admin.giftcode.marketing.list.menhgia/types'
const onError = () => {
    return (dispatch) => {
        $(`#validateMenhGiaGiftCodeMarketingList`)
            .addClass('has-error')
        dispatch({
            type: ONERROR_MENHGIA_GIFTCODE_MARKETING_LIST,
            payload: null
        })
    }
}
module.exports = onError


