import {
    onChange,
} from 'modules/home.user.deposit.check.nickname/actions'
class NicknameView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    render() {
        const { data } = this.props.nicknameUserDepositCheck || {}
        return (
            <div className="form-group-box">
                <label className="control-label">Nickname</label>
				<div className="input">
					<input 
						type="text" 
						className="form-control" 
						placeholder="Nickname"  
						value={data}
						onChange={event=>{
							this._onChange(event)
						}}
						 />
				</div>
			</div>
        )
    }
}
const mapStateToProps = ({
    nicknameUserDepositCheck
}) => {
    return {
        nicknameUserDepositCheck
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(NicknameView)


