import {
    CHANGE_CURRENT_PAGE_GIFTCODE_LIST
} from 'modules/home.agent.giftcode.list.pagination/types'
const onChange = ({ totals, current, show }) => {
    return (dispatch) => new Promise((resolve, reject) => {
        dispatch({
            type: CHANGE_CURRENT_PAGE_GIFTCODE_LIST,
            payload: {
                totals: totals,
                current: current,
                show: show
            }
        })
        resolve()
    })
}
module.exports = onChange


