import {
    ONERROR_FB_ADD_DAILY
} from 'modules/home.admin.agent.list.add.fb/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateFBAddDaiLyID`).addClass('has-error')
        $(`#validateFBMessAddDaiLyID`).removeClass('hide')
        $(`#validateFBMessAddDaiLyID`).text(mess)
        dispatch({
            type: ONERROR_FB_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onError


