import {
    GET_XOCDIA_HISTORY_ADMIN
} from 'modules/home.games.history.xocdia/types'
const INITIAL_STATE = {
    data: []
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case GET_XOCDIA_HISTORY_ADMIN:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


