import {
    ONCHANGE_NICKNAME_ADD_DAILY
} from 'modules/home.admin.agent.list.add.nickname/types'
const onChange = (val) => {
    return (dispatch, getState) => {
        const { data } = getState().nameAddDaiLy || {}
        $(`#validateNicknameAddDaiLyID`).removeClass('has-error')
        $(`#validateNicknameMessAddDaiLyID`).addClass('hide')
        if (data != val) {
            dispatch({
                type: ONCHANGE_NICKNAME_ADD_DAILY,
                payload: {
                    data: val
                }
            })
        }
    }
}
module.exports = onChange


