import {
    ONCHANGE_PASSWORD_LOGIN
} from 'modules/auth.password/types'
const onChange = (val) => {
    return (dispatch, getState) => {
        const { data } = getState().authPassword || {}
        if (data != val) {
            dispatch({
                type: ONCHANGE_PASSWORD_LOGIN,
                payload: {
                    data: val
                }
            })
        }
    }
}
module.exports = onChange


