import {
    ONFOCUS_NICKNAME_ADD_DAILY
} from 'modules/home.admin.agent.list.add.nickname/types'
const onFocus = () => {
    return (dispatch) => {
        $(`#nicknameAddDaiLyID`).focus()
        dispatch({
            type: ONFOCUS_NICKNAME_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onFocus


