import {
    onChange,
    onError
} from 'modules/home.admin.transfer.value/actions'
class ValueView extends React.Component {
    _onChange(event) {
        let value = event.target.value
        value = value.split(',').join('')
        value = ((value).match(/\d+/) && (value).match(/\d+/)[0]>0) ? (value).match(/\d+/)[0] : ''
        this.props.onChange(Helper._formatNumber(value))
    }
    render() {
        const { data } = this.props.valueTransferAdmin || {}
        return (
            <div className="form-group-box" id="validateValueTransferAdmin">
            <label className="control-label">Giá trị</label>
				<div className="input">
					<input
						type="text"
						className="form-control"
						placeholder="Giá trị ZON"  
						value={data || ''}
						onChange={event=>{
							this._onChange(event)
						}}
						 />
				</div>
                <div className="help-block hide" id="validateMessValueTransferAdmin"></div>
			</div>
        )
    }
}
const mapStateToProps = ({
    valueTransferAdmin
}) => {
    return {
        valueTransferAdmin
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        onError
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(ValueView)



