import {
    ONCHANGE_MENHGIA_GIFTCODE_DAILY
} from 'modules/home.admin.giftcode.agent.add.menhgia/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateMenhGiaGiftCodeDaiLyAdd`).removeClass('has-error')
        dispatch({
            type: ONCHANGE_MENHGIA_GIFTCODE_DAILY,
            payload: {
                data: val
            }
        })
    }
}
module.exports = onChange


