import {
    ONCHANGE_TODATE_USER_TRANSFER_STATEMENT
} from 'modules/home.user.transfer.statement.to/types'
const onChange = (val) => {
    return (dispatch) => {
        dispatch({
            type: ONCHANGE_TODATE_USER_TRANSFER_STATEMENT,
            payload: {
                data: val
            }
        })
    }
}

module.exports = onChange


