import {
    ONCHANGE_FROMDATE_AGENT_GIFTCODE_STATEMENT
} from 'modules/home.agent.giftcode.statement.from/types'
const onChange = (val) => {
    return (dispatch) => {
        dispatch({
            type: ONCHANGE_FROMDATE_AGENT_GIFTCODE_STATEMENT,
            payload: {
                data: val
            }
        })
    }
}

module.exports = onChange


