import {
    GET_HISTORY_TRANSFER_AGENTS
} from 'modules/home.admin.agents.transfer.history/types'
import {
    CHANGE_CURRENT_PAGE_ADMIN_AGENTS_TRANSFER_HISTORY
} from 'modules/home.admin.agents.transfer.history.pagination/types'
const onSubmit = (isFirst) => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: fromDate } = getState().filterAgentsTransferHistoryFrom || {}
        const { data: toDate } = getState().filterAdminAgentsTranferHistoryTo || {}
        const { data: daily } = getState().dailyTransferHistoryAgents || {}
        const { show, current } = getState().giftcodeMarketingPagination || {}
        axios.post(`${Config.API_URL}admin/agents/transferHistory`, {
                Data: {
                    fromDate: fromDate ? moment(fromDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 00:00:00 Z') : null,
                    toDate: toDate ? moment(toDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 23:59:59 Z') : null,
                    daily: daily,
                    limit: show,
                    offset: (current - 1) * show
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info, totals } = data || {}
                if (success) {
                    dispatch({
                        type: GET_HISTORY_TRANSFER_AGENTS,
                        payload: {
                            data: info
                        }
                    })
                    dispatch({
                        type: CHANGE_CURRENT_PAGE_ADMIN_AGENTS_TRANSFER_HISTORY,
                        payload: {
                            show: show,
                            current: current,
                            totals: totals
                        }
                    })
                }
                resolve()
            }, (err) => {
                reject(err)
            })
    })
}
export default onSubmit


