import {
    ONCHANGE_NEW_CHANGE_PASSWORD
} from 'modules/home.changePassword.newpass/types'
const onChange = (val) => {
    return (dispatch, getState) => {
        const { data } = getState().newPass || {}
        $(`#validateNewPassID`).removeClass('has-error')
        $(`#validateNewPassMessID`).addClass('hide')
        if (data != val) {
            dispatch({
                type: ONCHANGE_NEW_CHANGE_PASSWORD,
                payload: {
                    data: val
                }
            })
        }
    }
}
module.exports = onChange


