import {
    ONERROR_DAILY_GIFTCODE_DAILY_ADD
} from 'modules/home.admin.giftcode.agent.add.daily/types'
const onError = (dispatch) => {
    return (dispatch) => {
        $(`#validateMessDaiLyGiftCodeAddID`).addClass('has-error')
        dispatch({
            type: ONERROR_DAILY_GIFTCODE_DAILY_ADD,
            payload: null
        })
    }
}
module.exports = onError


