import {
    ONCHANGE_FROMDATE_ADMIM_BIGBABOL_HISTORY
} from 'modules/home.games.history.bigbabol.from/types'
const onChange = (val) => {
    return (dispatch) => {
        dispatch({
            type: ONCHANGE_FROMDATE_ADMIM_BIGBABOL_HISTORY,
            payload: {
                data: val
            }
        })
    }
}

module.exports = onChange


