import {
    ON_GETDATA_STATEMENT_USER
} from 'modules/home.user.transfer.statement/types'
const onSubmit = () => {
    return (dispatch, getState) => new Promise((resolve, reject) => {
        const { data: fromDate } = getState().filterUserTransferStatementFrom || {}
        const { data: toDate } = getState().filterUserTransferStatementTo || {}
        $('.loading').addClass('active')
        axios.post(`${Config.API_URL}admin/userTransferStatement`, {
                Data: {
                    fromDate: fromDate ? moment(fromDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 00:00:00 Z') : null,
                    toDate: toDate ? moment(toDate, 'YYYY-MM-DD HH:mm:ss Z').format('YYYY-MM-DD 23:59:59 Z') : null,
                }
            })
            .then((response) => {
                const { data } = response || {}
                const { success, data: info } = data || {}
                if (success) {
                    dispatch({
                        type: ON_GETDATA_STATEMENT_USER,
                        payload: {
                            data: info
                        }
                    })
                }
                resolve()
                $('.loading').removeClass('active')
            }, (err) => {
                $('.loading').removeClass('active')
                reject(err)
            })
    })
}
export default onSubmit


