import {
    ONBLUR_NICKNAME_ADD_DAILY
} from 'modules/home.admin.agent.list.add.nickname/types'
const onBlur = () => {
    return (dispatch) => {
        $(`#nicknameAddDaiLyID`).focus()
        dispatch({
            type: ONBLUR_NICKNAME_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onBlur


