import {
    onChange,
    onBlur
} from 'modules/auth.username/actions'
import {
	onFocus as onFocusPassword
} from 'modules/auth.password/actions'
class UsernameView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    _onKeyDown(event) {
        let { keyCode } = event || {}
        switch (keyCode) {
            case 13:
                this.props.onBlur()
                this.props.onFocusPassword()
                break
            default:
                break
        }
    }
    render() {
        const { data } = this.props.authUsername || {}
        return (
            <div className="form-group" id="validateUsernameID">
				<div className="input input--icon">
					<div className="icon"><i className="ti-user"></i></div>
					<input 
						id="userID"
						type="text" 
						className="form-control" 
						placeholder="Nhập Tài khoản"
						onChange={event=>{
							this._onChange(event)
						}}
						value={data}
						onKeyDown={event=>{
							this._onKeyDown(event)
						}}
						 />
				</div>
				<p id="validateUsernameIDText" className="help-block hide">Tên đăng nhập bắt buột</p>
			</div>
        )
    }
}
const mapStateToProps = ({ authUsername }) => {
    return {
        authUsername
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        onFocusPassword,
        onBlur
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(UsernameView)


