import {
    ONCHANGE_VALUE_TRANSFER_ADMIN
} from 'modules/home.admin.transfer.value/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateValueTransferAdmin`).removeClass('has-error')
        $(`#validateMessValueTransferAdmin`).addClass('hide')
        dispatch ({
            type: ONCHANGE_VALUE_TRANSFER_ADMIN,
            payload: {
            	data: val
            }
        })
    }
}
module.exports = onChange


