import {
    getData
} from 'modules/home.games.history.taixiu/actions'
import PaginationView from 'modules/home.games.history.taixiu.pagination/PaginationView'
import NicknameView from 'modules/home.games.history.taixiu.nickname/NicknameView'
import FromView from 'modules/home.games.history.taixiu.from/FromView'
import ToView from 'modules/home.games.history.taixiu.to/ToView'
import SubmitView from 'modules/home.games.history.taixiu.submit/SubmitView'
class HistoryView extends React.Component {
    componentDidMount() {
        this.props.getData()
    }
    render() {
        const { data } = this.props.taixiuHistory || {}
        const { show, current } = this.props.taixiuHistoryPagination || {}
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Lịch sử Tài Xỉu</h1>
					</div>
				</div>
				<div className="page__body">
					<div className="box margin-top-10">
                        <div className="tableFilter">
                            <div className="tableFilter__row">
                                    <div className="tableFilter__column">
                                    </div>
                                    <div className="tableFilter__column">
                                        <NicknameView />
                                    </div>
                                    <div className="tableFilter__column">
                                        <FromView />
                                    </div>
                                    <div className="tableFilter__column">
                                        <ToView />
                                    </div>
                                    <div className="tableFilter__row">
                                    <div className="tableFilter__column" />
                                    <div className="tableFilter__column" />
                                    <div className="tableFilter__column">
                                        <SubmitView />
                                    </div>
                                </div>
                            </div>
                        </div>
						<div className="table-container margin-top-10">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                                    <table className="table sticky">
                                        <thead>
                                            <tr>
                                                <th className="cell-no">Phiên</th>
                                                <th>Name</th>
                                                <th>Cược</th>
                                                <th>Cửa Đặt</th>
                                                <th>Kết Quả</th>
                                                <th>ZON Hoàn</th>
                                                <th>ZON Nhận</th>
                                                <th>Thời Gian</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                        	{data.map((item, index)=>{
                                        		return (
                                        			<tr key={`history_${item._id}`}>
        		                                        <td className="cell-no text-center">{item.phien}</td>
        		                                        <td className="text-nowrap text-center">
        		                                        	{item.name}
        		                                        </td>
        		                                        <td className="text-center">{Helper._formatMoney(item.bet)}</td>
        		                                        <td className={`cell-datetime text-center`}>{item.select ? 'TÀI' : 'XỈU'}</td>
        		                                        <td className={`cell-datetime text-center ${item.win ? 'text-positive' : 'text-negative'}`}>{item.win ? 'THẮNG' : 'THUA'}</td>
        		                                        <td className="text-center">{Helper._formatMoney(item.tralai)}</td>
        		                                        <td className={`text-center ${item.betwin>=0 ? 'text-positive' : 'text-negative'}`}>{Helper._formatMoney(item.betwin)}</td>
        		                                        <td className="cell-datetime text-center">{moment(item.time).format(`DD/MM/YYYY HH:mm:ss`)}</td>
        		                                    </tr>
                                        			)
                                        	})}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            <div className="margin-top-10">
                                <PaginationView />
                        	</div>
                        </div>
					</div>
				</div>
			</div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData,
    }, dispatch)
}
const mapStateToProps = ({
    taixiuHistory,
    taixiuHistoryPagination
}) => {
    return {
        taixiuHistory,
        taixiuHistoryPagination
    }
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(HistoryView)



