import {
    ONERROR_DESC_TRANSFER_ADMIN
} from 'modules/home.admin.transfer.desc/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateDescTransferAdmin`).addClass('has-error')
        $(`#validateMessDescTransferAdmin`).removeClass('hide')
        $(`#validateMessDescTransferAdmin`).text(mess)
        dispatch({
            type: ONERROR_DESC_TRANSFER_ADMIN,
            payload: null
        })
    }
}
module.exports = onError


