import {
    SHOW_MODAL_FREEZE_TRANSACTION_AGENT,
    HIDE_MODAL_FREEZE_TRANSACTION_AGENT
} from 'modules/home.admin.transfer.agent.history.freeze/types'
const INITIAL_STATE = {
    id: null
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case SHOW_MODAL_FREEZE_TRANSACTION_AGENT:
            return {
                ...state,
                id: action.payload.id,
            }
        case HIDE_MODAL_FREEZE_TRANSACTION_AGENT:
            return {
                ...state,
                id: null
            }
        default:
            return state
    }
    return state
}
export default Reducer


