import FromView from 'modules/home.agent.giftcode.statement.from/FromView'
import ToView from 'modules/home.agent.giftcode.statement.to/ToView'
import SubmitView from 'modules/home.agent.giftcode.statement.submit/SubmitView'
import {
    onSubmit as getData
} from 'modules/home.agent.giftcode.statement.submit/actions'
class StatementView extends React.Component {
    componentDidMount() {
        this.props.getData()
    }
    render() {
        const { data } = this.props.giftcodeStatementAgent || {}
        return (
            <div className="page__content">
                <div className="page__header">
                    <div className="title">
                        <h1 className="pageTitle">Thống kê Giftcode</h1>
                    </div>
                </div>
                <div className="page__body">
                    <div className="box margin-top-10">
                        <div className="tableFilter">
                            <div className="tableFilter__row">
                                <div className="tableFilter__column">
                                </div>
                                <div className="tableFilter__column">
                                </div>
                                <div className="tableFilter__column">
                                    <FromView />
                                </div>
                                <div className="tableFilter__column">
                                    <ToView />
                                </div>
                                <div className="tableFilter__row">
                                <div className="tableFilter__column" />
                                <div className="tableFilter__column" />
                                <div className="tableFilter__column">
                                    <SubmitView />
                                </div>
                            </div>
                            </div>
                        </div>
                        <div className="table-container margin-top-10">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                                    <table className="table sticky">
                                        <thead>
                                            <tr>
                                                <th>Mệnh giá</th>
                                                <th>Số lượng</th>
                                                <th>Đã dùng</th>
                                                <th>Tổng tiền đã dùng</th>
                                                <th>Còn lại</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td className="text-nowrap text-center">10K</td>
                                                <td className="text-nowrap text-center">{(data && data[10000]) ? data[10000].total : 0 }</td>
                                                <td className="text-center">{(data && data[10000]) ? data[10000].totalUse : 0 }</td>
                                                <td className="cell-datetime text-center">{Helper._formatMoney((data && data[10000]) ? data[10000].totalMoneyUse : 0 )}</td>
                                                <td className="cell-datetime text-center">{(data && data[10000]) ? data[10000].totalAvailable : 0 }</td>
                                            </tr>
                                            <tr>
                                                <td className="text-nowrap text-center">20K</td>
                                                <td className="text-nowrap text-center">{(data && data[20000]) ? data[20000].total : 0 }</td>
                                                <td className="text-center">{(data && data[20000]) ? data[20000].totalUse : 0 }</td>
                                                <td className="cell-datetime text-center">{Helper._formatMoney((data && data[20000]) ? data[20000].totalMoneyUse : 0 )}</td>
                                                <td className="cell-datetime text-center">{(data && data[20000]) ? data[20000].totalAvailable : 0 }</td>
                                            </tr>
                                            <tr>
                                                <td className="text-nowrap text-center">50K</td>
                                                <td className="text-nowrap text-center">{(data && data[50000]) ? data[50000].total : 0 }</td>
                                                <td className="text-center">{(data && data[50000]) ? data[50000].totalUse : 0 }</td>
                                                <td className="cell-datetime text-center">{Helper._formatMoney((data && data[50000]) ? data[50000].totalMoneyUse : 0 )}</td>
                                                <td className="cell-datetime text-center">{(data && data[50000]) ? data[50000].totalAvailable : 0 }</td>
                                            </tr>
                                            <tr>
                                                <td className="text-nowrap text-center">100K</td>
                                                <td className="text-nowrap text-center">{(data && data[100000]) ? data[100000].total : 0 }</td>
                                                <td className="text-center">{(data && data[100000]) ? data[100000].totalUse : 0 }</td>
                                                <td className="cell-datetime text-center">{Helper._formatMoney((data && data[100000]) ? data[100000].totalMoneyUse : 0 )}</td>
                                                <td className="cell-datetime text-center">{(data && data[100000]) ? data[100000].totalAvailable : 0 }</td>
                                            </tr>
                                            <tr>
                                                <td className="text-nowrap text-center">200K</td>
                                                <td className="text-nowrap text-center">{(data && data[200000]) ? data[200000].total : 0 }</td>
                                                <td className="text-center">{(data && data[200000]) ? data[200000].totalUse : 0 }</td>
                                                <td className="cell-datetime text-center">{Helper._formatMoney((data && data[200000]) ? data[200000].totalMoneyUse : 0 )}</td>
                                                <td className="cell-datetime text-center">{(data && data[200000]) ? data[200000].totalAvailable : 0 }</td>
                                            </tr>
                                            <tr>
                                                <td className="text-nowrap text-center">500K</td>
                                                <td className="text-nowrap text-center">{(data && data[500000]) ? data[500000].total : 0 }</td>
                                                <td className="text-center">{(data && data[500000]) ? data[500000].totalUse : 0 }</td>
                                                <td className="cell-datetime text-center">{Helper._formatMoney((data && data[500000]) ? data[500000].totalMoneyUse : 0 )}</td>
                                                <td className="cell-datetime text-center">{(data && data[500000]) ? data[500000].totalAvailable : 0 }</td>
                                            </tr>
                                            <tr>
                                                <td className="text-nowrap text-center">Tổng</td>
                                                <td className="text-nowrap text-center">
                                                {data.total}</td>
                                                <td className="text-center">{data.totalUse}</td>
                                                <td className="cell-datetime text-center">{Helper._formatMoney(data.totalMoneyUse)}</td>
                                                <td className="cell-datetime text-center">{data.totalAvailable}</td>
                                                
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = ({
    giftcodeStatementAgent
}) => {
    return {
        giftcodeStatementAgent
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        getData
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(StatementView)


