import {
    ONERROR_OTP_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent.otp/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateOtpTransferDaiLy`).addClass('has-error')
        $(`#validateMessOtpTransferDaiLy`).removeClass('hide')
        $(`#validateMessOtpTransferDaiLy`).text(mess)
        dispatch({
            type: ONERROR_OTP_TRANSFER_AGENT,
            payload: null
        })
    }
}
module.exports = onError


