import FromView from 'modules/home.admin.agent.agent2.profit.from/FromView'
import ToView from 'modules/home.admin.agent.agent2.profit.to/ToView'
import SubmitView from 'modules/home.admin.agent.agent2.profit.submit/SubmitView'
import {
    onSubmit
} from 'modules/home.admin.agent.agent2.profit.submit/actions'
class ProfitView extends React.Component {
    componentDidMount() {
        this.props.onSubmit()
    }
    render() {
        const { data } = this.props.profitAgent2 || {}
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Doanh thu cấp 2</h1>
					</div>
				</div>
				<div className="page__body">
					<div className="box margin-top-10">
					<div className="box margin-top-10">
						<div className="tableFilter">
                            <div className="tableFilter__row">
                                <div className="tableFilter__column">
                                    <FromView />
                                </div>
                                <div className="tableFilter__column">
                                    <ToView />
                                </div>
                                <div className="tableFilter__column">
                                    <SubmitView />
                                </div>
                            </div>
                        </div>
					</div>
						<div className="table-container margin-top-10">
                            <div className="loading">
                                <div className="loading__dimmer">
                                </div>
                                <div className="loading__content">
                                    <table className="table sticky">
                                        <thead>
                                            <tr>
                                                <th className="cell-no">STT</th>
                                                <th>Nickname</th>
                                                <th>Doanh số Bán</th>
                                                <th>Doanh số Mua</th>
                                                <th>Doanh số Liên minh</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                        	{data.map((item, index)=>{
                                        		return (
                                        			<tr key={`profit_agent2_${index}`}>
        		                                        <td className="cell-no text-center">{index+1}</td>
        		                                        <td className="text-nowrap text-center">{item.nickname}</td>
        		                                        <td className="text-center">{Helper._formatMoney(item.totalFrom)}</td>
        		                                        <td className="cell-datetime text-center">{Helper._formatMoney(item.totalTo)}</td>
        		                                        <td className="cell-datetime text-center">{Helper._formatMoney(item.totalFrom + item.totalTo)}</td>
        		                                    </tr>
                                        			)
                                        	})}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
					</div>
				</div>
			</div>
        )
    }
}
const mapStateToProps = ({
    profitAgent2
}) => {
    return {
        profitAgent2
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onSubmit
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(ProfitView)


