class HeaderLeftView extends React.Component {
    render() {
        const { data } = this.props.userInfo || {}
        const { nickname, rights } = data || {}
        return (
            <div className="sidebar__header">
				<div className="sidebar__profile">
					<picture className="profile__image">
						<img src="/images/user.png"/>
					</picture>
					<div className="profile__info">
						<div className="name">{nickname ? nickname : ''}</div>
						<div className="career">{Helper._mapRightsToTitle(rights)}</div>
					</div>
				</div>
			</div>
        )
    }
}
const mapStateToProps = ({
    userInfo
}) => {
    return {
        userInfo
    }
}
export default ReactRedux.connect(mapStateToProps, null)(HeaderLeftView)



