import {
    ONCHANGE_SOLUONG_GIFTCODE_MARKETING
} from 'modules/home.admin.giftcode.marketing.add.soluong/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_SOLUONG_GIFTCODE_MARKETING:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


