const _formatMoney = (num) => {
  if (num && !isNaN(num)) {
    let p = (num.toString()).split(".")
    return (num < 0 ? '-' : '') + p[0].split("").reverse().reduce(function(acc, num, i, orig) {
      return num == "-" ? acc : num + (i && !(i % 3) ? "," : "") + acc
    }, "")
  }
  return '0'
}
module.exports = _formatMoney



