import {
    ONCHANGE_TRANGTHAI_GIFTCODE_DAILY_FILTER
} from 'modules/home.agent.giftcode.list.trangthai/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateTrangthaiGiftCodeDaiLyFilter`).removeClass('has-error')
        dispatch({
            type: ONCHANGE_TRANGTHAI_GIFTCODE_DAILY_FILTER,
            payload: {
                data: val
            }
        })
    }
}
module.exports = onChange


