import {
    ONCHANGE_TYPE_HISTORY_USER_FILTER
} from 'modules/home.user.history.type/types'
const onChange = (val) => {
    return (dispatch) => new Promise((resolve, reject) => {
        $(`#validateTypeHistoryUserFilter`).removeClass('has-error')
        dispatch({
            type: ONCHANGE_TYPE_HISTORY_USER_FILTER,
            payload: {
                data: val
            }
        })
        resolve(val)
    })
}
module.exports = onChange


