import {
    SHOW_MOAL_ADD_DAILY
} from 'modules/home.admin.agent.list.add/types'
import {
	onFocus as forcusName
} from 'modules/home.admin.agent.list.add.name/actions'
const show = () => {
    return (dispatch) => {
        $(`#modalAddAgentID`).addClass('active')
        dispatch({
            type: SHOW_MOAL_ADD_DAILY,
            payload: null
        })
        dispatch(forcusName())
    }
}
module.exports = show


