import {
    ONFOCUS_LOCATION_ADD_DAILY
} from 'modules/home.admin.agent.list.add.location/types'
const onFocus = () => {
    return (dispatch) => {
        $(`#locationAddDaiLyID`).focus()
        dispatch({
            type: ONFOCUS_LOCATION_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onFocus


