import NicknameView from 'modules/home.admin.transfer.nickname/NicknameView'
import ValueView from 'modules/home.admin.transfer.value/ValueView'
import DescView from 'modules/home.admin.transfer.desc/DescView'
import {
    onSubmit,
    onValidate
} from 'modules/home.admin.transfer/actions'
class TransferView extends React.Component {
    _onSubmit(isAdd) {
        this.props.onValidate()
            .then(() => {
                this.props.onSubmit(isAdd)
            }, (err) => {

            })
    }
    render() {
        return (
            <div className="page__content">
				<div className="page__header">
					<div className="title">
						<h1 className="pageTitle">Cộng/Trừ ZON</h1>
					</div>
				</div>
				<div className="page__body">
					<form className="form form--transfer">
						<div className="form__body">
						   <NicknameView />
						   <ValueView />
						   <DescView />
						</div>
						<div className="form__footer">
							<div className="btnWrapper" id="btnTransferAdminMinusID">
								<a
									className="btn btn--block btn--primary"
									onClick={event=>{
										this._onSubmit(false)
									}} >
									Trừ ZON
									</a>
							</div>
							<div className="btnWrapper margin-left-10" id="btnTransferAdminAddID">
								<a
									className="btn btn--block btn--primary"
									onClick={event=>{
										this._onSubmit(true)
									}} >
									Cộng ZON
									</a>
							</div>
						</div>
					</form>
				</div>
			</div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onSubmit,
        onValidate
    }, dispatch)
}
export default ReactRedux.connect(null, mapDispatchToProps)(TransferView)



