import {
    ONCHANGE_TOMDATE_AGENT2_PROFIT
} from 'modules/home.admin.agent.agent2.profit.to/types'
const INITIAL_STATE = {
    data: moment().format('YYYY-MM-DD 23:59:59 Z')
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_TOMDATE_AGENT2_PROFIT:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


