import {
    ON_GETDATA_PROFIT_AGENTS
} from 'modules/home.admin.agents.profit/types'
const INITIAL_STATE = {
    data: []
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ON_GETDATA_PROFIT_AGENTS:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


