import {
    ONBLUR_PHONE_ADD_DAILY
} from 'modules/home.admin.agent.list.add.phone/types'
const onBlur = () => {
    return (dispatch) => {
        $(`#phoneAddDaiLyID`).focus()
        dispatch({
            type: ONBLUR_PHONE_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onBlur


