import {
    ONCHANGE_DAILY_TRANSFER_HISTORY_AGENTS
} from 'modules/home.admin.agents.transfer.history.daily/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_DAILY_TRANSFER_HISTORY_AGENTS:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


