import {
    GET_DATA_USER_HISTORY,
    SHOW_MODAL_USER_HISTORY
} from 'modules/home.user.history/types'
const INITIAL_STATE = {
    data: {
    	userInfo: {},
    	logs: []
    },
    user: {},
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case GET_DATA_USER_HISTORY:
            return {
                ...state,
                data: action.payload.data
            }
        case SHOW_MODAL_USER_HISTORY:
            return {
                ...state,
                user: action.payload.user
            }
        default:
            return state
    }
    return state
}
export default Reducer


