import {
    onChange
} from 'modules/home.agent.giftcode.list.from/actions'
class FromView extends React.Component {
    componentDidMount() {
        this.initDatePicker()
    }
    initDatePicker() {
        const { data } = this.props.filterAgentGiftcodeListFrom || {}
        const datePicker = $(`.datepicker_agent_giftcode_list_from`).datepicker({
            dateFormat: 'dd/mm/yy',
            onSelect: function() {
                let date = $(`.datepicker_agent_giftcode_list_from`).datepicker('getDate')
                date = moment(date).format('YYYY-MM-DD 00:00:00 Z')
                this.props.onChange(date)
            }.bind(this)
        })
        // $(datePicker).datepicker('setDate', moment(data, 'YYYY-MM-DD 00:00:00 Z').toDate())
    }
    render() {
        const { data } = this.props.filterAgentGiftcodeListFrom || {}
        return (
			<div className="form-group-box">
                <label className="control-label">Từ ngày</label>
                <div className="input input--icon">
                    <input type="text" className="form-control datepicker_agent_giftcode_list_from" placeholder="dd/mm/yyyy" />
                    <span className="icon"><i className="ti-calendar"></i></span>
                </div>
            </div>
        )
    }
}
const mapStateToProps = ({
    filterAgentGiftcodeListFrom
}) => {
    return {
        filterAgentGiftcodeListFrom
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(FromView)


