import {
    ONCHANGE_TODATE_ADMIN_LONGLAN_HISTORY
} from 'modules/home.games.history.longlan.to/types'
const INITIAL_STATE = {
    data: null
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_TODATE_ADMIN_LONGLAN_HISTORY:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


