import {
    onChange,
    onError,
    onCheck
} from 'modules/home.admin.transfer.agent.nickname/actions'
class NicknameView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    _onBlur(){
        const { data } = this.props.nicknameTransferDaiLy || {}
        if(data){
            this.props.onCheck()
        }
    }
    render() {
        const { data } = this.props.nicknameTransferDaiLy || {}
        return (
            <div className="form-group-box" id="validateNicknameTransferDaiLy">
                <label className="control-label">Nickname</label>
				<div className="input">
					<input
						type="text"
						className="form-control"
						placeholder="Nickname nhận ZON"
      id = "inputNicknameTransferDaily"
						value={data}
						onChange={event=>{
							this._onChange(event)
						}}
                        onBlur={event=>{
                            this._onBlur()
                        }}
						 />
				</div>
                <div className="help-block hide" id="validateMessNicknameTransferDaiLy"></div>
			</div>
        )
    }
}
const mapStateToProps = ({
    nicknameTransferDaiLy
}) => {
    return {
        nicknameTransferDaiLy
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        onError,
        onCheck
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(NicknameView)



