import {
    GET_DAILY,
    ONCONFIRM_REMOVE
} from 'modules/home.admin.agent.list/types'
const INITIAL_STATE = {
    data: [],
    idRemove: null
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case GET_DAILY:
            return {
                ...state,
                data: action.payload.data
            }
        case ONCONFIRM_REMOVE:
            return {
                ...state,
                idRemove: action.payload.idRemove
            }
        default:
            return state
    }
    return state
}
export default Reducer


