import {
    onChange,
    onError
} from 'modules/home.admin.transfer.agent.history.report.desc/actions'
class DescView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    render() {
        const { data } = this.props.descReportTransferDaiLy || {}
        return (
            <div className="form-group margin-top-10" id="validateDescReportTransferDaiLy" >
				<div className="input">
					<textarea 
						type="text"
						className="form-control" 
						placeholder="Lý do báo cáo giao dịch"  
						value={data}
						onChange={event=>{
							this._onChange(event)
						}}
						 />
				</div>
                <div className="help-block hide" id="validateMessDescReportTransferDaiLy"></div>
			</div>
        )
    }
}
const mapStateToProps = ({
    descReportTransferDaiLy
}) => {
    return {
        descReportTransferDaiLy
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        onError
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(DescView)


