import {
    ONERROR_ADMIN_DAILY_GIFTCODE_DAILY
} from 'modules/home.admin.giftcode.agent.list.daily/types'
const onError = (dispatch) => {
    return (dispatch) => {
        $(`#validateMessAdminGiftCodeDailyID`).addClass('has-error')
        dispatch({
            type: ONERROR_ADMIN_DAILY_GIFTCODE_DAILY,
            payload: null
        })
    }
}
module.exports = onError


