import {
    ONCHANGE_OTP_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent.otp/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateOtpTransferDaiLy`).removeClass('has-error')
        $(`#validateMessOtpTransferDaiLy`).addClass('hide')
        dispatch ({
            type: ONCHANGE_OTP_TRANSFER_AGENT,
            payload: {
            	data: val
            }
        })
    }
}
module.exports = onChange


