import {
    ONERROR_MENHGIA_GIFTCODE_DAILY
} from 'modules/home.admin.giftcode.agent.add.menhgia/types'
const onError = () => {
    return (dispatch) => {
        $(`#validateMenhGiaGiftCodeDaiLyAdd`)
            .addClass('has-error')
        dispatch({
            type: ONERROR_MENHGIA_GIFTCODE_DAILY,
            payload: null
        })
    }
}
module.exports = onError


