import {
    INIT_HEADER
} from 'modules/home.header/types'
const INITIAL_STATE = {
    data: {}
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case INIT_HEADER:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


