import {
    hide
} from 'modules/home.admin.agent2.list.remove.confirm/actions'
import {
    deleteDaily
} from 'modules/home.admin.agent2.list/actions'
class ConfirmView extends React.Component {
    _hide() {
        this.props.hide()
    }
    _onSubmit() {
        this.props.deleteDaily()
    }
    render() {
        return (
            <div className="confirm" id="confirmRemoveAgent2ID">
				<div className="confirm__container">
					<div className="confirm__content">
						<a className="btn close" onClick={event=>{
							this._hide()
						}}>X</a>
						<h3>Xác nhận xoá</h3>
						<p>Bạn có chắc muốn xoá đại lý này khỏi danh sách ? </p>
					</div>
					<div className="confirm__actions">
						<a className="btn btn--cancel" onClick={event=>{
							this._hide()
						}}>Cancel</a>
						<input type="button" value="Ok" className="btn btn--submit" 
						onClick={event=>{
							this._onSubmit()
						}}/>
					</div>
				</div>
			</div>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        hide,
        deleteDaily
    }, dispatch)
}
export default ReactRedux.connect(null, mapDispatchToProps)(ConfirmView)


