import {
    onChange,
    onError
} from 'modules/home.admin.transfer.agent.otp/actions'
class OTPView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    render() {
        const { data } = this.props.otpTransferDaiLy || {}
        return (
            <div className="form-group-box" id="validateOtpTransferDaiLy">
                <label className="control-label">OTP</label>
				<div className="input">
					<input
						type="text"
						className="form-control"
      id="inputOtpTransferDaiLy"
						placeholder="Nhập OTP"
						value={data}
						onChange={event=>{
							this._onChange(event)
						}}
						 />
				</div>
                <div className="help-block hide" id="validateMessOtpTransferDaiLy"></div>
			</div>
        )
    }
}
const mapStateToProps = ({
    otpTransferDaiLy
}) => {
    return {
        otpTransferDaiLy
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        onError
    }, dispatch)
}
export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(OTPView)



