import {
    onChange,
    onBlur
} from 'modules/auth.password/actions'
import {
    onValidate,
    onLogin
} from 'modules/auth/actions'
class PasswordView extends React.Component {
    _onChange(event) {
        this.props.onChange(event.target.value)
    }
    _onKeyDown(event) {
        const { keyCode } = event || {}
        switch (keyCode) {
            case 13:
                this.props.onBlur()
                this.props.onValidate()
                    .then(() => {
                        //call login
                        this.props.onLogin()
                    }, (err) => {
                        //validate failed
                    })
                break
            default:
                break
        }
    }
    render() {
        const { data } = this.props.authPassword || {}
        return (
            <div className="form-group" id="validatePassswordID">
				<div className="input input--icon">
					<div className="icon"><i className="ti-lock"></i></div>
					<input 
						type="password" 
						className="form-control" 
						placeholder="Nhập Mật khẩu"
						id="passID"
						value={data}
						onChange={event=>{
							this._onChange(event)
						}}
						onKeyDown={event=>{
							this._onKeyDown(event)
						}}
						 />
				</div>
                <p id="validateComonIDText" className="text-center hide text-danger"></p>
			</div>
        )
    }
}
const mapStateToProps = ({
    authPassword
}) => {
    return {
        authPassword
    }
}
const mapDispatchToProps = (dispatch) => {
    return Redux.bindActionCreators({
        onChange,
        onValidate,
        onLogin,
        onBlur
    }, dispatch)
}

export default ReactRedux.connect(mapStateToProps, mapDispatchToProps)(PasswordView)


