import {
    ONERROR_NICKNAME_ADD_DAILY
} from 'modules/home.admin.agent.list.add.nickname/types'
const onError = (mess) => {
    return (dispatch) => {
        $(`#validateNicknameAddDaiLyID`).addClass('has-error')
        $(`#validateNicknameMessAddDaiLyID`).removeClass('hide')
        $(`#validateNicknameMessAddDaiLyID`).text(mess)
        dispatch({
            type: ONERROR_NICKNAME_ADD_DAILY,
            payload: null
        })
    }
}
module.exports = onError


