import {
    ONCHANGE_DAILY_GIFTCODE_DAILY_ADD
} from 'modules/home.admin.giftcode.agent.add.daily/types'
const INITIAL_STATE = {
    data: ''
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ONCHANGE_DAILY_GIFTCODE_DAILY_ADD:
            return {
                ...state,
                data: action.payload.data
            }
        default:
            return state
    }
    return state
}
export default Reducer


