import {
	onValidate,
	onLogin
} from 'modules/auth/actions'
class BtnView extends React.Component {
	_onClick(){
		this.props.onValidate()
		.then(()=>{
			this.props.onLogin()
		},(err)=>{

		})
	}
	render(){
		return (
			<div className="login__footer" onClick={event=>{
				this._onClick()
			}}>
				<a id="btnLoginID" className="btn submit active">Đăng nhập</a>
			</div>
			)
	}
}
const mapDispatchToProps = (dispatch)=>{
	return Redux.bindActionCreators({
		onValidate,
		onLogin
	}, dispatch)
}
export default ReactRedux.connect(null, mapDispatchToProps)(BtnView)


