import {
    GET_SUMMARY_DATA
} from 'modules/home.init/types'
const getData = () => {
    return (dispatch) => new Promise((resolve, reject) => {
        setTimeout(() => {
            $('.loading').addClass('active')
        }, 100)
        axios.get(`${Config.API_URL}getSummary`)
            .then((response) => {
                const { data } = response || {}
                const { success, data: summ } = data || {}
                if (success) {
                    dispatch({
                        type: GET_SUMMARY_DATA,
                        payload: {
                            data: summ
                        }
                    })
                }
                setTimeout(() => {
                    $('.loading').removeClass('active')
                }, 100)
            }, (err) => {
                setTimeout(() => {
                    $('.loading').removeClass('active')
                }, 100)
                reject(err)
            })
    })
}
module.exports = getData


