import {
    ONCHANGE_ADMIN_DAILY_GIFTCODE_DAILY
} from 'modules/home.admin.giftcode.agent.list.daily/types'
const onChange = (val) => {
    return (dispatch) => {
    	$(`#validateMessAdminGiftCodeDailyID`).removeClass('has-error')
        dispatch({
            type: ONCHANGE_ADMIN_DAILY_GIFTCODE_DAILY,
            payload: {
                data: val
            }
        })
    }
}
module.exports = onChange


