import {
    ON_CHECK_DEPOSIT_USER
} from 'modules/home.user.deposit.check/types'
const INITIAL_STATE = {
    data: []
}
const Reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ON_CHECK_DEPOSIT_USER:
            return {
                ...state,
                data: action.payload.data,
                total: action.payload.total
            }
        default:
            return state
    }
    return state
}
export default Reducer


