import {
    ONCHANGE_DESC_TRANSFER_AGENT
} from 'modules/home.admin.transfer.agent.desc/types'
const onChange = (val) => {
    return (dispatch) => {
        $(`#validateDescTransferDaiLy`).removeClass('has-error')
        $(`#validateMessDescTransferDaiLy`).addClass('hide')
        dispatch({
            type: ONCHANGE_DESC_TRANSFER_AGENT,
            payload: {
                data: val
            }
        })
    }
}
module.exports = onChange


